<template>
    <div class="has-topbar">
    <div class="index-notice bar_top">
        <div class="nav_bar">
            <div class="icon-left fast-click" @click="goBack(-1)"></div>
            <p>{{title[cate_id-1]}}</p>
        </div>
        <div class="wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <mt-loadmore  :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
        <ul class="list">
            <li class="item" v-for="(list,index) in lists" :key="index">
                <h4 class="item_tit">{{list.title}}</h4>
                <p>{{list.desc}}</p>
                <div class="details">
                    <router-link :to="{name:'NoticeDetails',params: { id: $route.params.id, details: list.id}}">查看详情</router-link>
                </div>
            </li>
        </ul>
        </mt-loadmore>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: 'IndexNotice',
        data () {
            return {
                lists:[],
                listSign:'',
                cate_id: 1,
                page: 1,
                lastPage: '',
                title:['公告','常见问题'],
                token:'',

                allLoaded: false,
                wrapperHeight: 0,
                autoFill:false,
                bottomPullText:'加载更多',
            }
        },
        created(){
            this.cate_id = this.$route.params.id;
            this.token = this.getToken();
            this.noticeList()
        },
        methods:{
            noticeList(){
                let listSign = this.getEncrypt('cate_id='+this.cate_id+'&page='+this.page+'&type=list');
                this.$http.post('/app.Placard/get.html',{
                    cate_id:this.cate_id,
                    page:this.page,
                    type:'list',
                    sign:listSign,
                    // token: this.token
                }).then(response => {
                    if(response.data.code == 1){
                        if(this.page <= 1){
                            this.lists = response.data.data.data;
                            this.lastPage = response.data.data.last_page;
                            if(this.lastPage == 1) this.allLoaded = true;
                            return
                        }else{
                            this.lists = this.lists.concat(response.data.data);
                            if(this.page >= this.lastPage) this.allLoaded = true;
                            return
                        }
                    }
                }).catch(error=>{
                    console.log(error)
                })
            },
            loadBottom(){
                this.page += 1;

                this.$refs.loadmore.onBottomLoaded();
            },
        },
        mounted(){
            setTimeout(()=>{
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar){
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - 34;
                    return
                }else{
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
                    return
                }
            },50)
        },
    }
</script>
<style lang="scss" scoped>
.wrapper{
    background: $c8-color;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    position: relative;
}
.list{
    padding:20px;
    .item{
        padding:20px;
        box-shadow: $list-shadow;
        margin-bottom:20px;
        .item_tit{
            font-size:30px;
            padding-bottom:20px;
            font-weight:bold;
        }
        p{
            font-size:24px;
            line-height: 30px;
            padding-bottom:20px;
            color: $c4-color;
        }
        .details{
            font-size:26px;
            display: flex;
            justify-content: flex-end;
            a{
                color: $c4-color;
                &:active{
                    color:$main-color;
                 }
            }
        }
    }
}
</style>



