exports.install = function (Vue) {
    Vue.prototype.changeData = function (){
        alert('执行成功');
    };
    //md5解密方法
    Vue.prototype.getEncrypt = function (value ='') {
        let secretKey = "&key=A8sUd9bqis3sN5GK6aF9JDFl5I9skPkd";
        let stringSignTemp = value + secretKey;
        let sign =  this.$md5(stringSignTemp).toUpperCase();
        return sign
    };
    Vue.prototype.goBack = function (value){
        if(isNaN(value)){
            this.$router.push(value);
        }else{
            // this.$router.push(value)
            window.history.go(value);
        }

        // console.log(typeof(value))
    }
    Vue.prototype.btn = function (){
        alert('OK');
    };

    Vue.prototype.getToken = function (){
        var arr,reg=new RegExp("(^| )"+'mysteryCellToken'+"=([^;]*)(;|$)");
        if(arr=document.cookie.match(reg)){
            return unescape(arr[2]);
        }
        else{
            return null;
        }
    };
    Vue.prototype.openVideoPlay = (url,isLogin = true)=>{
        if(!isLogin){
            api.toast({
                msg: '您还没有登录，请先登录!',
                duration: 1000,
                location: 'middle'
            });
            return;
        }else {
            let videoPlayer = api.require('videoPlayer');
            videoPlayer.play({
                styles: {
                    head: {
                        bg: 'rgba(0.5,0.5,0.5,0.7)',
                        height: 44,
                        titleSize: 20,
                        titleColor: '#fff',
                        backSize: 20,
                        setSize: 20,
                    },

                },
                path: url,
                autoPlay:true
            },function(ret, err) {
                if (ret) {

                } else {

                }
            });
        }
    };
    Vue.prototype.openImg = function(value,index,isLogin = true){
        if(!isLogin){
            api.toast({
                msg: '您还没有登录，请先登录!',
                duration: 1000,
                location: 'middle'
            });
            return;
        }else {
            let imageBrowser = api.require('imageBrowser');
            imageBrowser.openImages({
                imageUrls: value,   //图片数组
                activeIndex: index,     //图片索引
                showList: false,
                bg: '#000',
                tapClose: false     //是否开启导航，false为开启
            });
        }
    }
};


