<template>
    <div class="has-topbar">
    <div class="my-set bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack('/Set')"></span>
            <p>屏蔽设置</p>
        </div>
        <div class="photo_tab">
            <div :class="{'photo-active':index == photoTabNum }" v-for="(photoTab,index) in photoTabs" :key="index" @click="photoTabAcitve(index,photoTab.src)">{{photoTab.title}}</div>
        </div>
        <keep-alive>
            <router-view/>
        </keep-alive>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        data () {
            return {
                photoTabs:[
                    {
                        title:'用户',
                        src:'/Blacklist',
                        id:5
                    },
                    {
                        title:'细胞圈',
                        src:'/Blacklist/BlacklistCircle',
                        id:4
                    },
                ],
                photoTabNum: 0,
            }
        },
        methods:{
            photoTabAcitve(index,src){
                this.photoTabNum = index;
                this.$router.push(src);
            }
        }
    }
</script>
<style lang="scss" scoped>
    .photo_tab{
        display: flex;
        justify-content: space-around;
        align-items: stretch;
        height:100px;
        background: #fff;
        border-bottom:1px solid $c6-color;/*no*/
        font-size:36px;
        line-height: 100px;
        width:100%;
        z-index: 88;
        color: $c4-color;
        div{
            padding: 0 20px;
            &.photo-active{
                color: $main-color;
                border-bottom:3px solid $main-color;/*no*/
            }
        }
    }
</style>



