<template>
    <div class="has-topbar">
    <div class="information-report bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>举报</p>
        </div>
    </div>
    <div class="information-report-con">
        <div class="report-top">
            <p class="report-top-title">投诉<span>用户{{UserData.nickname}}的细胞圈</span></p>
            <p class="report-top-con"><span>用户{{UserData.nickname}}:</span>{{UserData.content}}</p>
        </div>
        <div class="report-list">
            <ul>
                <li v-for="(list,index) in lists" :key="index" @click="choose(index)">
                    <div :class="['choose',{check_on: check_on_index==index}]"></div>
                    <p class="report-list-con">{{list}}</p>
                </li>
            </ul>
            <div v-if="check_on_index==4" class="report-list-other">
                <p>请输入需补充内容(不超过140字) <span>当前字数：{{nowother}}</span></p>
                <textarea v-model="other"></textarea>
            </div>
        </div>
        <div class="btn-group">
            <button @click="submit" class="btn">提交</button>
        </div>
    </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return{
            lists:[
                '涉黄信息',
                '政治敏感',
                '违法',
                '病毒木马',
                '其它'
            ],
            check_on_index: 0,
            other:'',
            nowother:0,
            CircleId:'',
            UserData:[],
        }
    },
    created(){
        this.CircleId = this.$route.params.CircleId;
        this.getCircleList();
    },
    methods:{
        choose(index){
            this.check_on_index = index
        },
        getCircleList(){
            let sign = this.getEncrypt("id="+this.CircleId+"&token="+this.getToken())
            this.$http.post("/app.bbs/getinfo.html",{
                sign: sign,
                token: this.getToken(),
                id: this.CircleId,
            }).then(res => {
                if(res.data.code == 1){
                    this.UserData = res.data.data
                }
            })
        },
        submit(){
            console.log(this.check_on_index)
            let sign = this.getEncrypt(
                "bid=" +this.UserData.id+
                "&content="+this.lists[this.check_on_index]+
                "&respondent="+ this.UserData.mid +
                "&token=" + this.getToken()
            );
            this.$http.post("/app.Safety/report.html",{
                sign: sign,
                token: this.getToken(),
                bid: this.UserData.id,
                content: this.lists[this.check_on_index],
                respondent: this.UserData.mid
            }).then(res => {
                if(res.data.code == 1){
                    this.goBack(-1)
                }
                Toast({
                    message: res.data.msg,
                    duration: 1500
                });
            })

        }
    },
    watch:{
        other: function (newQuestion, oldQuestion) {
            this.nowother = newQuestion.length
            if(newQuestion.length>140){
                alert('已经超过字数')
                this.other = oldQuestion
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.information-report-con{
    padding-top: 20px;
    .report-top span{
        color: $main-color
    }
    .report-top-title{
        // width: 100%;
        height: 20px;
        line-height: 30px;
        padding: 20px;
        font-size: 26px;
        background-color: #fff;
    }
    .report-top-con{
        // width: 600px;
        // height: 40px;
        padding: 20px;
        line-height: 40px;
        margin: 20px;
        background-color: $c6-color;
        font-size: 26px;
    }
    .report-list{
        padding: 20px;
        ul{
            overflow: hidden;
        }
        li{
            width: 250px;
            height: 80px;
            line-height: 80px;
            float: left;
            margin-left: 80px;
        }
        .choose{
            float: left;
            line-height: 40px;
        }
        .report-list-con{
            float: left;
            line-height: 40px;
            font-size: 28px;
            margin-left: 24px;
        }
        .report-list-other{
            padding: 20px;
            p{
                font-size: 26px;
                margin-bottom: 20px;
            }
            textarea{
                width: 100%;
                height: 160px;
            }
        }
    }
    .btn-group{
        background: #fff;
        padding: 80px 40px 0;
    .btn{
        line-height:80px;
        text-align: center;
        display: block;
        color: #fff;
        border: 1px solid $main-color;/*no*/
        background: $main-color;
        border-radius: 5px;
        font-size: 30px;
        width: 100%;
    }
}
}
</style>



