<template>
    <div class="has-topbar">
    <div class="information-release bar_top">
        <div class="nav_bar">
            <span @click.stop="getBack"> 取消</span>
            <div :class="{'active':state}" @click.stop="submit">发表</div>
        </div>
        <div class="con">
            <div class="list">
                <textarea rows="3" ref="textArea" v-model="info" placeholder="这一刻的想法..."></textarea>
                <div class="item">
                    <div v-if="videoImg.length >0" class="file-video">
                        <img :src="videoImg[0]" alt="视频" />
                        <span class="icon-play" @click="openVideoPlay(filevideo)"></span>
                        <span class="icon-cross" @click="deleteImg(0)"></span>
                    </div>
                    <div v-else class="img-group" v-for="(list,index) in imgList" :key="index">
                        <img class="thumbnail" v-gallery v-lazy="list" :key="list"/>
                        <span class="icon-cross" @click="deleteImg(index)"></span>
                    </div>
                    <div v-if="upload" class="upload" @click="sheetShow = true">
                        <i>+</i>
                    </div>
                </div>
            </div>
        </div>
        <!--回复选择弹窗-->
        <div v-if="sheetShow" @click="closeComment" class="sheet-box">
            <div class="sheet has-fixed-bottom" ref="commentBox">
                <div class="list" @click="photograph">拍照</div>
                <div class="list" @click="readFile">照片图库</div>
                <!--<div v-if="imgList.length < 1" class="list" @click="shootVideo">拍视频</div>-->
                <!--<div v-if="imgList.length < 1" class="list" @click="readVideo">视频库</div>-->
                <div class="list-bottom" @click="sheetShow=false">取消</div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Indicator } from 'mint-ui';
    import { Toast } from 'mint-ui';
    export default {
        data () {
            return {
                imgList:[],
                videoImg:[],
                info:'',
                token:'',
                sign:'',
                filetype:1,
                filevideo: "",
                photoNum:9,

                upload: true,
                sheetShow: false,
                submitIng: false,
            }
        },
        methods:{
            //返回上一页
            getBack(){
                Indicator.close();
                this.goBack(-1);
            },
            //点击空白关闭弹框
            closeComment(){
                var sp = this.$refs.commentBox;
                if(sp){
                    if(!sp.contains(event.target)){ //这句是说如果我们点击到了id为myPanel以外的区域
                        this.sheetShow = false;
                    }
                }
            },
//            //视频库
//            readVideo(){
//                let _this = this;
//                this.sheetShow = false;
//                var UIMediaScanner = api.require('UIMediaScanner');
//                UIMediaScanner.open({
//                    type: 'video',
//                    column: 4,
//                    max: 1,
//                    texts:{
//                        selectedMaxText:'最多选择1个视频'
//                    },
//                    exchange: true,
//                    rotation: false
//                }, function(ret) {
//                    if (ret) {
//                        if(ret.list) {
//                        }
//                    }
//                });
//            },
            //选择照片
            readFile(){
                let _this = this;
                this.sheetShow = false;
                //base64转码函数
                this.photoNum -= this.imgList.length;
                function convertImgToBase64(url, callback, outputFormat){
                    var canvas = document.createElement('canvas'),
                        ctx = canvas.getContext('2d'),
                        img = new Image;
                    img.crossOrigin = 'Anonymous';//它开启了本地的跨域允许。当然服务器存储那边也要开放相应的权限才行，如果是设置了防盗链的图片在服务端就没有相应的权限的话你本地端开启了权限也是没有用的
                    img.onload = function(){
                        canvas.width = 800;
                        canvas.height = img.height/(img.width/800);
                        ctx.drawImage(img,0,0,800,img.height/(img.width/800));
                        var dataURL = canvas.toDataURL('image/jpg',0.1);//没权限的跨域图片在使用canvas.toDataURL()数据导出时会报错
                        callback.call(this, dataURL);
                        canvas = null;
                    };
                    img.src = url;
                };
                var UIMediaScanner = api.require('UIMediaScanner');
                UIMediaScanner.open({
                    type: 'picture',
                    column: 4,
                    max: this.photoNum,
                    texts:{
                        selectedMaxText:'最多选择9张照片！'
                    },
                    exchange: true,
                    rotation: false
                }, function(ret) {
                    if (ret) {
                        if(ret.list) {
                            if (ret.list.length > 0) {
                                for (var i = 0; i < ret.list.length; i++) {
                                    convertImgToBase64(ret.list[i].path, function (base64Img) {
                                        _this.imgList.push(base64Img)
                                        if(_this.imgList.length >= 9){
                                            _this.upload = false;
                                        }
                                    })
                                }
                            }
                        }
                    }
                });
            },
            //拍照
            photograph(){
                let _this = this;
                this.sheetShow = false;
                api.getPicture({
                    sourceType: 'camera',
                    encodingType: 'jpg',
                    mediaValue: 'pic',
                    allowEdit: false,
                    destinationType: 'base64',
                    quality: 50,
                    targetWidth: 800,
                    targetHeight: 800,
                    saveToPhotoAlbum: false
                }, function(ret, err) {
                    if (ret) {
                        _this.imgList.push(ret.base64Data)
                        if(_this.imgList.length >= 9){
                            _this.upload = false;
                        }
                    } else {

                    }
                });
            },
            //拍视频
            shootVideo(){
                let _this = this;
                this.sheetShow = false;
                //图片转码
                function convertImgToBase64(url, callback, outputFormat){
                    var canvas = document.createElement('canvas'),
                        ctx = canvas.getContext('2d'),
                        img = new Image;
                    img.crossOrigin = 'Anonymous';//它开启了本地的跨域允许。当然服务器存储那边也要开放相应的权限才行，如果是设置了防盗链的图片在服务端就没有相应的权限的话你本地端开启了权限也是没有用的
                    img.onload = function(){
                        canvas.width = 800;
                        canvas.height = img.height/(img.width/800);
                        ctx.drawImage(img,0,0,800,img.height/(img.width/800));
                        var dataURL = canvas.toDataURL('image/jpg',0.1);//没权限的跨域图片在使用canvas.toDataURL()数据导出时会报错
                        callback.call(this, dataURL);
                        canvas = null;
                    };
                    img.src = url;
                };
                //视频模块
                var zySmallVideo = api.require('zySmallVideo');
                zySmallVideo.open({
                    MaxRecordTime: 10,
                    videoColor: api.systemType=="ios"?0xcd0000:"#cd0000",
                    AVAssetExportPreset:"AVAssetExportPreset1280x720",
                    mVideoSizeW:720,
                    mVideoSizeH:1280
                },function(ret, err){
                    if(ret){
                        _this.filevideo = ret.fileUrl;
                        _this.upload = false;
                        convertImgToBase64(ret.filePic, function (base64Img) {
                            _this.videoImg.push(base64Img)
                        })
                    }
                });
            },
            deleteImg(index){
                this.imgList.splice(index,1);
                this.videoImg.splice(index,1);
                this.filevideo = '';
                this.upload = true;
            },
            submit(){
//                //视频提交
                if(this.info !=''){
                    Indicator.open({
                        text: '发表中...',
                        spinnerType: 'fading-circle',
                    });
                    if(!this.submitIng){
                        this.submitIng = true;
                        if(this.filevideo.length > 0){
                            this.filetype = 2;
                            var imagesList = this.videoImg.join("|||");
                        }else{
                            this.filetype = 1;
                            var imagesList = this.imgList.join("|||");
                        }
                        let _this = this;
                        _this.token = this.getToken();

                        _this.sign = this.getEncrypt(
                            "content="+this.info+
                            "&file="+imagesList+
                            "&filetype="+this.filetype+
                            "&token="+this.token);
                        if(this.filetype == 1){
                            api.ajax({
                                url: _this.httpUrl() + '/app.Bbs/add.html',
                                method: 'post',
                                data: {
                                    values:{
                                        sign: _this.sign,
                                        token:_this.token,
                                        filetype: _this.filetype,
                                        content:_this.info,
                                        file: imagesList
                                    }
                                }
                            },(response,err) => {
                                if(response.msg == "发布成功！") {
                                    this.$router.push("/Information/2");
                                    this.submitIng = false;
                                }
                                Toast({
                                    message: response.msg,
                                    duration: 1000
                                });
                                Indicator.close();
                            })
                        }else{
                            api.ajax({
                                url: _this.httpUrl() + '/app.Bbs/add.html',
                                method: 'post',
                                data: {
                                    values:{
                                        sign: _this.sign,
                                        token:_this.token,
                                        filetype: _this.filetype,
                                        content:_this.info,
                                        file: imagesList
                                    },
                                    files:{
                                        filevideo:_this.filevideo,
                                    },
                                }
                            },(response,err) => {
                                if(response.msg == "发布成功！") {
                                    this.$router.push("/Information/2");
                                    this.submitIng = false;
                                }
                                Toast({
                                    message: response.msg,
                                    duration: 1000
                                });
                                Indicator.close();
                            })
                        }
                    }
                }
            }
        },
        computed:{
            state(){
                if(this.info !='' || this.imgList.length>0){
                    return true;
                }else{
                    return false;
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
.nav_bar{
    padding: 0 40px;
    justify-content: space-between;
    box-sizing: border-box;
    div{
        color: $c4-color;
        &.active{
            color: $main-color;
        }
    }
}
.con{
    background: $c8-color;
    padding-top: 20px;
    .list{
        background: #fff;
        textarea{
            border: none;
            padding: 40px 40px 0;
            font-size: 26px;
            width: 100%;
            box-sizing: border-box;
            resize: none;
        }
        .item{
            margin-top: 40px;
        }
    }

}
.item{
    padding-left:60px;
    .img-group{
        float: left;
        position: relative;
        span{
            position: absolute;
            right:10px;
            top:0px;
            color: #fff;
            background: rgba(0,0,0,0.3);
            font-size:30px;
            padding:5px;
        }
    }
    .file-video{
        position: relative;
        display: inline-block;
        width: 35%;
        img{
            width: 100%;
        }
        .icon-play{
            position: absolute;
            top: 50%;
            left: 50%;
            margin: -50px 0 0 -50px;
            font-size:100px;
            color: #fff;
        }
        .icon-cross{
            position: absolute;
            right:10px;
            top:0px;
            color: #fff;
            background: rgba(0,0,0,0.3);
            font-size:30px;
            padding:5px;
        }
    }
}

.upload {
    height: 198px;
    width: 198px;
    line-height: 185px;
    position: relative;
    margin:0 10px 10px 0;
    cursor: pointer;
    color: #888;
    background: #e6e6e6;
    overflow: hidden;
    float: left;
    text-align: center;
    i{
        font-size:100px;
    }
    input {
        position: absolute;
        /*font-size: 100px;*/
        display: inline-block;
        width:198px;
        height:198px;
        right: 0;
        top: 0;
        opacity: 0;
        filter: alpha(opacity=0);
        cursor: pointer
    }
}
.thumbnail{
    display: inline-block;
    height: 198px;
    width: 198px;
    margin:0 10px 10px 0;
    object-fit: cover;
}
.sheet-box{
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 999;
    width: 100%;
    height:100%;
    background: rgba(0,0,0,0.5);
    .sheet{
        position:absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        background: #e0e0e0;
        text-align: center;
        font-size: 26px;
        &>div{
              background: #fff;
              height: 100px;
              line-height: 100px;
        &.list{
             border-bottom: 1px solid $c6-color;/*no*/
         }
        &.delete{
             color: #df5000;
         }
        &.list-bottom{
             margin-top: 20px;
         }
        }
    }
}
</style>



