<template>
    <div class="has-topbar">
    <div class="new-password bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>输入新密码</p>
        </div>
        <div class="list">
            <div class="item">
                <input placeholder="请输入新密码" type="password" v-model="password"/>
            </div>
            <div class="item">
                <input placeholder="请重新输入新密码" type="password" v-model="anewPassword"/>
            </div>
            <div class="btn-ground">
                <button class="btn-next" @click="confirm()">确定</button>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui'
    export default {
        name: 'NewPassword',
        data () {
            return {
                password:'',
                anewPassword:''
            }
        },
        methods:{
            confirm(){
                if( this.password != this.anewPassword){
                    Toast({
                        message: '两次密码不一样，请重新输入',
                        duration: 2000
                    })

                }else{
                    let sign = this.getEncrypt('password='+this.anewPassword+'&phone='+this.$route.params.phone+'&token='+this.getToken()+'&verify='+this.$route.params.verify)
                    this.$http.post('/app.Member/resetPassword.html',{
                        sign: sign,
                        token: this.getToken(),
                        phone:this.$route.params.phone,
                        verify:this.$route.params.verify,
                        password: this.anewPassword
                    }).then( response => {
                        if(response.data.code == 1){
                            Toast({
                                message: response.data.msg,
                                duration: 2000
                            })
                            this.$router.push('/Set')
                        }else{
                            Toast({
                                message: response.data.msg,
                                duration: 2000
                            })
                        }
                    }).catch( error => {
                        console.log(error)
                    })
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
.list{
    background: $c8-color;
    padding-top: 20px;
    .item{
        height: 120px;
        background: #fff;
        display: flex;
        justify-content:flex-start;
        align-items: stretch;
        border-bottom: 1px solid $c6-color; /*no*/
        position:relative;
        &:first-child{
             border-top: 1px solid $c6-color; /*no*/
         }
        input{
            border:none;
            background: none;
            flex:1;
            font-size:24px;
            padding: 0 40px;
            &::-webkit-input-placeholder{
                 color: $c4-color;
             }
            &::-moz-placeholder{
                 color: $c4-color;
             }
            &::-ms-input-placeholder{
                 color: $c4-color;
             }
        }
    }
    .btn-ground{
        padding:80px 40px 0;
        background: #fff;
        .btn-next{
            width: 100%;
            height: 80px;
            background: $main-color;
            border: none;
            border-radius: 5px;
            font-size: 30px;
            color: #fff;
            &:disabled{
                 background: $c6-color;
             }
        }
    }
}
</style>



