<template>
    <div class="has-topbar">
    <div class="submit-order bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click.stop="goBack(-1)"></span>
            <p>确认订单</p>
        </div>
        <div class="site">
            <div class="info" @click.stop="goSite">
                <div v-if="site != ''">
                    <div class="top">
                        <p>收货人：<span>{{site.username}}</span></p>
                        <div class="phone">电话：<span>{{site.phone}}</span></div>
                    </div>
                    <div>详细地址：<span>{{site.province}}{{site.city}}{{site.area}}{{site.address}}</span></div>
                </div>
                <div v-else>请选择收货地址</div>
                <i class="icon-right"></i>
            </div>
        </div>
        <div class="list">
            <div class="item" v-for="(list,index) in orderList" :key="index">
                <div class="top">
                    <div class="goods-img">
                        <img class="img" v-lazy="list.img" :key="list.img" />
                    </div>
                    <div class="goods-info">
                        <h4>{{list.title}}</h4>
                        <h5>{{list.alias}}</h5>
                        <p><span class="price">￥{{list.price}}</span> <span>数量：{{list.num}}</span></p>
                    </div>
                </div>
                <!--<div class="bottom">-->
                    <!--<div>购买数量</div>-->
                    <!--<div class="btn-group">-->
                        <!--<span class="btn" @click.stop="less(index)">-</span>-->
                        <!--<input class="num" onkeyup="this.value=this.value.replace(/\D/g,'')" @input="handleInput(index)" type="tel" v-model="list.num"/>-->
                        <!--<span class="btn" @click.stop="add(index)">+</span>-->
                    <!--</div>-->
                <!--</div>-->
            </div>
            <div class="distribution">
                <p>配送方式</p>
                <p>{{logistics}}</p>
            </div>
            <div class="guestbook">
                <p>买家留言：</p>
                <textarea placeholder="留言最多40个字哦~（选填）" rows="3" v-model="orderDesc"></textarea>
            </div>
        </div>
        <!--支付方式-->
        <div class="order-pay">
            <div class="pay-list">
                <h4>支付方式</h4>
                <!-- <div @click="selectFn(1)" :class="['pay-item',{check_on: isSelect == 1}]"><p><i class="icon-integral"></i><span>积分支付</span></p><div :class="['choose',{check_on: isSelect == 1}]"></div></div> -->
                <div v-if="orderList[0].is_need != 1" @click="selectFn(2)" :class="['pay-item',{check_on: isSelect == 2}]"><p><i class="icon-wechat"></i><span>微信支付</span></p><div :class="['choose',{check_on: isSelect == 2}]"></div></div>
                <!--<div v-if="orderList[0].is_need != 1" @click="selectFn(3)" :class="['pay-item',{check_on: isSelect == 3}]"><p><i class="icon-wechat"></i><span>支付宝支付</span></p><div :class="['choose',{check_on: isSelect == 3}]"></div></div>-->
            </div>
        </div>
        <div class="main-footer">
            <p>共<span>{{orderList.length}}</span>件商品，合计：<i>￥{{totalPrice}}</i></p>
            <div @click.stop="subOrder" class="btn-primary" >确定付款</div>
        </div>
        <mt-popup  v-model="popupVisible" position="bottom" :closeOnClickModal="false">
            <div class="po-box has-padding-bottom">
                <div class="po-header"><i class="icon-cross" @click.stop="goBack('/order/1/PayOrder')"></i><p>{{ popupTitleValue | popupTitle }}</p></div>
                <div v-if="!keyboardShow">
                    <h4>支付积分：{{totalPrice}}</h4>
                    <ul>
                        <li><span>支付方式</span><span>积分</span></li>
                        <li><span>可用积分</span><span>{{usableIntegral}}</span></li>
                    </ul>
                    <button @click.stop="payment" class="btn bottom-menu-fixed">立即支付</button>
                </div>
                <div class="key-list" v-else>
                    <div class="key-input">
                        <div class="input-group">
                            <div><span v-if="passwordList.length>=1"></span></div>
                            <div><span v-if="passwordList.length>=2"></span></div>
                            <div><span v-if="passwordList.length>=3"></span></div>
                            <div><span v-if="passwordList.length>=4"></span></div>
                            <div><span v-if="passwordList.length>=5"></span></div>
                            <div><span v-if="passwordList.length>=6"></span></div>
                        </div>
                    </div>
                    <div class="input-pan bottom-menu-fixed">
                        <div class="pan-group" v-for="(list,index) in panNumber" :key="index">
                            <div class="pan-num fast-click" v-for="(item,ind) in list" @click="inputPsd(item)" :key="ind">{{item}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </mt-popup>
        <div class="message-box" v-if="messageBoxShow">
            <div class="confirm-box">
                <div>
                    <div class="header">
                        <h4>您还没有设置支付密码！</h4>
                        <p>是否要进入设置支付密码？</p>
                    </div>
                    <div class="btn-group">
                        <button class="btn" @click.stop="messageBoxShow = false">否</button>
                        <button @click.stop="setPassword" class="btn btn-main">是</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        data () {
            return {
                num:1,
                orderList:[],
                siteList:[],
                site:[],
                goodsInfo:'',
                orderId:'',
                sign:'',
                token:'',
                siteIndex:'',
                orderDesc:'',
                orderNo:'',
                //购物车
                cartLists:[],
                placeOrderIndex:[],
                //弹框
                popupVisible:false,
                popupTitleValue: '',
                usableIntegral:'',
                isSetPassword:'',
                //输入金额显示
                messageBoxShow: false,
                keyboardShow: false,
                //键盘
                panNumber:[
                    ['1','2','3'],
                    ['4','5','6'],
                    ['7','8','9'],
                    ['','0','删除'],
                ],
                passwordList:[],
                isSelect: '2',
                logistics:'',
            }
        },
        created(){
            this.cartLists = this.$cache.get("cartList");
            this.placeOrderIndex = this.$cache.get("placeOrderIndex");
            this.orderList = this.$cache.get("placeOrder");
            this.token = this.getToken();
            this.sign = this.getEncrypt("token="+this.token);
            this.siteIndex = this.$cache.get('SiteIndex');
            this.LogisticsFn();
            this.$http.post('/app.Address/get.html',{
                sign: this.sign,
                token: this.token
            }).then(response => {
                this.siteList = response.data.data;
                if(this.siteList.length > 0){
                    this.siteList.forEach( (item,index) => {
                        if(this.siteIndex == index){
                            return this.site = item;
                            return;
                        }
                        if(this.siteIndex === '' && item.is_default){
                            this.site = item;
                            return;
                        }
                    })
                }
            })
        },
        computed:{
            //总价
            totalPrice(){
                let _price = 0;
                if(this.orderList.length > 0){
                    this.orderList.forEach((item) => {
                        _price += Number(item.num) * Number(item.price)
                    })
                }
                return _price.toFixed(2);
            }
        },
        methods:{
//            add(index){
//                this.orderList[index].num = parseInt(this.orderList[index].num) + 1;
//            },
//            less(index){
//                if( this.orderList[index].num <= 1 ) return;
//                this.orderList[index].num = parseInt(this.orderList[index].num) - 1;
//            },
//            handleInput(index){
//                if(this.orderList[index].num == '' || this.orderList[index].num == 0){
//                    this.orderList[index].num  = 0;
//                    return
//                }else{
//                    this.orderList[index].num = this.orderList[index].num.replace(/\b(0+)/gi,"");
//                }
//            },
            LogisticsFn(){
                let sign = this.getEncrypt('');
                this.$http.post("/app.Data/postage.html",{
                    sign:sign
                }).then(res => {
                    if(res.data.code == 1){
                        this.logistics = res.data.data
                    }
                })
            },
            //支付方式选择方式
            selectFn(type){
                this.isSelect = type;
            },
            //修改地址
            goSite(){
                this.$router.push('/Site/2')
            },
            setPassword(){
                this.$router.push({name:'ResetPassword',params:{value:'paySubNew',orderId: this.orderId}})
            },
            //确定付款
            subOrder(){
                this.goodsInfo = '';
                if(this.site == ''){
                    Toast({
                        message: '请选择收货地址',
                        duration: 1000
                    });
                }else{
                    if(this.orderList.length > 0){
                        this.orderList.forEach((item) =>{
                            this.goodsInfo += item.id+"@"+item.alias+"@"+item.num+";"
                        })
                    }
                    let address_id = this.site.address_id;
                    var _this = this;
                    let sign = this.getEncrypt("address_id="+address_id+"&goods_info="+this.goodsInfo+"&order_desc="+this.orderDesc+"&token="+this.token);
                    this.$http.post('/app.Order/create.html',{
                        sign: sign,
                        token: this.token,
                        goods_info: this.goodsInfo,
                        address_id: address_id,
                        order_desc: this.orderDesc,
                    }).then(response => {
                        if(response.data.code == 1){
                            this.orderNo = response.data.data.order_no;
                            this.usableIntegral = response.data.data.integral;
                            this.isSetPassword = response.data.data.is_set_password;
                            this.orderId = response.data.data.order_id;
                            //删除购物车
                            if(this.placeOrderIndex.length > 0){
                                for (var i = this.placeOrderIndex.length - 1;i >= 0;i--) {
                                    var index = this.placeOrderIndex[i];
                                    this.cartLists.splice(index,1);
                                }
                            }
                            this.$cache.set('cartList',this.cartLists)
                            if(this.isSelect == 2){
                                //检查有没有安装微信
                                var wx = api.require('wx');
                                wx.isInstalled(function(ret, err) {
                                    if (!ret.installed) {
                                        Toast({
                                            message: '当前设备未安装微信客户端',
                                            duration: 1500
                                        });
                                        _this.$router.push("/order/1/PayOrder");
                                    } else {
                                        //微信支付（app）接口
                                        let sign = _this.getEncrypt("order_no="+_this.orderNo+"&token="+_this.token)
                                        _this.$http.post("/app.Order/wxpay.html",{
                                            order_no: _this.orderNo,
                                            token: _this.token,
                                            sign: sign
                                        }).then( res => {
                                            var wxPay = api.require('wxPay');
                                            wxPay.payOrder({
                                                apiKey: res.data.data.appid,
                                                orderId: res.data.data.prepayid,
                                                mchId: res.data.data.partnerid,
                                                nonceStr: res.data.data.noncestr,
                                                timeStamp: res.data.data.timestamp,
                                                package: res.data.data.package,
                                                sign: res.data.data.sign,
                                            }, function(ret, err) {
                                                if (ret.status) {
                                                    Toast({
                                                        message: '支付成功',
                                                        duration: 2000
                                                    });
                                                    //检查是否支付（微信支付）
                                                    let checkPaySign = _this.getEncrypt("order_no="+_this.orderNo+"&token="+_this.token)
                                                    _this.$http.post("/app.Order/checkPay.html",{
                                                        order_no: _this.orderNo,
                                                        token: _this.token,
                                                        sign: checkPaySign
                                                    }).then(res => {
                                                        if( res.data.code == 1){
                                                            _this.$router.push("/order/2/DeliverOrder")
                                                        }else {
                                                            _this.$router.push("/order/1/PayOrder")
                                                        }
                                                    })
                                                } else {
                                                    if (err.code == 1) {
                                                        Toast({
                                                            message: 'apiKey值非法',
                                                            duration: 2000
                                                        });
                                                    } else if (err.code == -1) {
                                                        Toast({
                                                            message: '未知错误',
                                                            duration: 2000
                                                        });
                                                    } else if (err.code == -2) {
                                                        Toast({
                                                            message: '用户取消',
                                                            duration: 2000
                                                        });
                                                    }
                                                    _this.$router.push("/order/1/PayOrder")
                                                }
                                            });
                                        })
                                        return
                                    }
                                });
                            }else if( this.isSelect == 1) {
                                //弹窗显示
                                this.popupVisible = true;
                                this.popupTitleValue = 1;
                                this.keyboardShow = false;
                                this.passwordList = [];
                            }
                        }else{
                            Toast({
                                message: response.data.msg,
                                duration: 1000
                            });
                        }
                    })
                }
                //end
            },
            payment(){
                if(this.totalPrice > this.usableIntegral){
                    Toast({
                        message: "可用积分不足，请先充值积分",
                        duration: 2000
                    });
                    return
                }else if(this.isSetPassword == 0){
                    this.popupVisible = false;
                    this.messageBoxShow = true;
                    return
                }else {
                    this.popupTitleValue = 2;
                    this.keyboardShow = true;
                }

            },
            //输入密码
            inputPsd(value){
                if(value == '删除'){
                    this.passwordList.pop()
                    return
                }else if(value == ''){
                    return
                }else{
                    if(this.passwordList.length < 6) {
                        this.passwordList.push(value);
                        if (this.passwordList.length == 6) {
                            let sign = this.getEncrypt("order_no="+this.orderNo+"&password="+this.passwordList.join("")+"&token="+this.token)
                            this.$http.post("/app.Order/integralPay.html",{
                                sign: sign,
                                token: this.token,
                                order_no: this.orderNo,
                                password: this.passwordList.join("")
                            }).then(res => {
                                if(res.data.code == 1){
                                    this.$router.push("/order/2/DeliverOrder")
                                }else{
                                    this.passwordList = [];
                                }
                                Toast({
                                    message: res.data.msg,
                                    duration: 1000
                                });
                            })
                        }
                    }
                }
            }
        },
        filters:{
            popupTitle(value){
                if(value == 1){
                    return "确认支付"
                }else if(value == 2){
                    return "输入密码"
                }
            }
        }

    }
</script>
<style lang="scss" scoped>
.submit-order{
    padding-bottom: 120px;
}
.order-pay{
    padding-top: 20px;
    background: $c8-color;
    .pay-list{
        background: #fff;
        padding:20px 40px;
        border-top: 1px solid $c6-color;/*no*/
        .pay-item{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin: 20px 0;
            font-size: 20px;
            border: 1px solid $c6-color;/*no*/
            padding: 12px;
            border-radius: 5px;
            &.check_on{
                color: $main-color;
                border-color: $main-color;
            }
            p{
                display: flex;
                justify-content: flex-start;
                align-items: center;
                i{
                    font-size: 40px;
                    margin-right: 10px;
                }
            }

        }
    }
    h4{
        font-size: 26px;
    }
}
.site{
    padding: 20px 0;
    background: $c8-color;
    .info{
        max-height:120px;
        background: #fff;
        padding: 40px 30px 40px 40px;
        font-size:26px;
        border-top: 1px solid $c6-color;/*no*/
        border-bottom: 1px solid $c6-color;/*no*/
        display:flex;
        justify-content:space-between;
        align-items:center;
        &>div{
            padding-right: 10px;
            flex: 1;
        }
        .icon-right{
            font-size: 40px;
        }
        .top{
            display: flex;
            justify-content: space-between;
            margin-bottom:40px;
            .phone span{
                min-width:170px;
                min-height:26px;
                display: inline-block;
            }
        }
    }
}
.po-box{
    position: relative;
    min-height: 700px;
    .po-header{
        height: 100px;
        border-bottom: 1px solid $c6-color;/*no*/
        padding: 0 40px;
        box-sizing: border-box;
        p{
            text-align: center;
            line-height: 100px;
            font-size: 30px;
        }
        i{
            padding: 0 20px;
            position: absolute;
            font-size: 28px;
            line-height: 100px;
            left: 20px;
            top: 0px;
        }
    }
    h4{
        font-size: 40px;
        text-align: center;
        padding: 40px 0;
    }
    ul{
        padding-bottom: 100px;
        li{
            display: flex;
            height: 80px;
            line-height: 80px;
            padding: 0 40px;
            font-size: 26px;
            justify-content: space-between;
            border-bottom: 1px solid $c6-color;/*no*/
        }
    }
    .btn{
        width: 100%;
        height: 120px;
        background: $main-color;
        border:none;
        color: #fff;
        font-size: 34px;
        position: absolute;
        bottom: 0;
        left: 0;
        width:100%;
    }
}
.list{
    border-top: 1px solid $c6-color;/*no*/
    .item{
        .top{
            padding:20px 40px;
            display: flex;
            justify-content: flex-start;
            font-size:26px;
            border-bottom: 1px solid $c6-color;/*no*/
            .goods-info{
                flex:1;
                h4{
                    @include ellipsis;
                }
                h5{
                    font-size:24px;
                    color: $c4-color;
                    margin-top:10px;
