                }
            }
        }
    }
    .distribution{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height:120px;
        padding:0 40px;
        border-bottom: 1px solid $c6-color;/*no*/
        span{
            margin-right:15px;
        }
    }
}

.main-footer{
    position: fixed;
    left:0;
    bottom:0;
    width:100%;
    height:120px;
    background: #fff;
    box-shadow:$top-shadow;
    display: flex;
