    p{
        span{
            color: $main-color;
            margin: 0 5px;
        }
        i{
            font-size: 36px;
            color: $main-color;
            margin-right: 25px;
        }
    }
    .btn-primary{
        width:250px;
        height:100%;
        line-height:120px;
        text-align: center;
        display: inline-block;
        background: $main-color;
