        border: 0;
        font-size: 30px;
    }

}
.guestbook{
    display: flex;
    justify-content: flex-start;
    padding: 20px 40px;
    border-bottom: 1px solid $c6-color;/*no*/
    p{
        font-size: 26px;
    }
    textarea{
        flex:1;
        border: none;
        resize: none;
        padding: 0 !important;
        border-radius:0;
        margin:0;
        &::-webkit-input-placeholder{
             color: $c4-color;
         }
        &::-moz-placeholder{
             color: $c4-color;
         }
        &::-ms-input-placeholder{
             color: $c4-color;
         }
    }
}
.message-box{
    background: rgba(0,0,0,0.5);
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 2000;
    .confirm-box{
        position: absolute;
        min-height: 200px;
        margin-top: -120px;
        top:50%;
        width: 100%;
        box-sizing: border-box;
        padding:0 60px;
        &>div{
            background: #fff;
            min-height: 200px;
            border-radius: 10px;
            overflow: hidden;
            .header{
                min-height:120px;
                padding: 40px;
                box-sizing: border-box;
                h4{
                    font-weight: bold;
                    font-size: 30px;
                }
                p{
                    font-size: 30px;
                    color: $c4-color;
                    margin-top: 20px;
                }
            }
            .btn-group{
                height:80px;
                box-sizing: border-box;
                border-top: 1px solid $c6-color;/*no*/
                display: flex;
                align-items: stretch;
                .btn{
                    flex: 1;
                    background: #fff;
                    border: none;
                    padding: 0;
                    font-size: 26px;
                }
                .btn-main{
                    color: $main-color;
                    border-left: 1px solid $c6-color;
                }
            }
        }
    }
}
.key-list{
    /*display: flex;*/
    /*justify-content: center;*/
    /*align-items: center;*/
    .input-pan{
        position: absolute;
        background: #fff;
        bottom: 0;
        left: 0;
        width: 100%;
        .pan-group{
            display: flex;
            justify-content: center;
            align-items: stretch;
            height: 110px;
            border-top: 1px solid $c6-color;/*no*/
            box-sizing: border-box;
            .pan-num{
                flex: 1;
                text-align: center;
                line-height: 110px;
                font-size: 36px;
                box-sizing: border-box;
                border-right: 1px solid $c6-color;/*no*/
                &:last-child{
                     border:none;
                }
                &:active{
                    background: $c6-color;
                }
            }
        }
    }
    .key-input{
        padding: 40px 40px 480px;
        display: flex;
        justify-content: center;
        box-sizing: border-box;
        .input-group{
            height: 80px;
            border-left: 1px solid $c6-color;/*no*/
            border-top: 1px solid $c6-color;/*no*/
            width: 480px;
            display: flex;
            &>div{
                width: 80px;
                flex: 1;
                box-sizing: border-box;
                border-right: 1px solid $c6-color;/*no*/
                border-bottom: 1px solid $c6-color;/*no*/
                position: relative;
                span{
                    width: 20px;
                    height: 20px;
                    position: absolute;
                    top: 30px;
                    left: 30px;
                    background: #000;
                    border-radius: 50%;
                }
            }
        }
    }
}
</style>



