<template>
    <div class="has-topbar">
        <div class="sign-in">
            <div class="nav"><i class="icon-left fast-click" @click="getBack"></i> <span>细胞之迷商家注册</span></div>
            <div class="phone">
                <label for="user">手机号:</label>
                <input id="user" type="text" v-model.trim="phone"/>
            </div>
            <div class="code">
                <label for="code">手机验证码:</label>
                <input id="code" type="text" v-model.trim="verify"/>
                <div class="btn" @click="getCode">{{getCodeName}}</div>
            </div>
            <div class="code">
                <label for="spreat">邀请码:</label>
                <input id="spreat" type="text" v-model.trim="spreat"/>
            </div>
            <div class="btn-group">
                <button class="btn btn-main" @click="nextFn">下一步</button>
            </div>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        name: "Merchant",
        data() {
            return {
                phone: "",
                getCodeName: "获取验证码",
                spreat: "",
                time: 60,
                verify: '',
            };
        },
        created(){
            this.spreat = sessionStorage.getItem('xbzm-appParam').replace(/[^a-zA-Z]/g,'');
            // localStorage.removeItem('xbzm-appParam');
        },
        mounted(){

        },
        methods: {
            getBack(){
                sessionStorage.removeItem('xbzm-appParam')
                this.goBack("/Index")
            },
            getCode(){
                let reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
                let sign = this.getEncrypt("phone="+this.phone);
                if(this.phone == ''){
                    Toast({
                        message: '请输入手机号码',
                        duration: 1500
                    });
                    return;
                }else if(!reg.test(this.phone)){
                    Toast({
                        message: '请输入正确的手机号码',
                        duration: 1500
                    });
                    return;
                }else{
                    let sign = this.getEncrypt("phone="+this.phone + "&type=3");
                    this.$http.post("/app.member/registerVerfiy.html",{
                        sign: sign,
                        phone: this.phone,
                        type: 3
                    }).then(response => {
                        if(response.data.code == 1){
                            this.time = 60;
                            this.timer();
                        }else{
                            Toast({
                                message: response.data.msg,
                                duration: 1500
                            });
                        }
                    }).catch(error => {
                        console.log(error)
                    })
                }
            },
            timer(){
                if (this.time > 1) {
                    this.time--;
                    this.getCodeName = this.time+'s后重新获取';
                    setTimeout(this.timer, 1000);
                }else if(this.time == 1){
                    this.getCodeName = '获取验证码';
                }
            },
            nextFn(){
                let sign = this.getEncrypt("phone=" + this.phone + "&spreat=" + this.spreat + "&verify=" + this.verify);
                let reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
                if(this.phone == ''){
                    Toast({
                        message: '请输入手机号码',
                        duration: 1500
                    });
                }else if(!reg.test(this.phone)){
                    Toast({
                        message: '请输入正确的手机号码',
                        duration: 1500
                    });
                }else if(this.verify == ''){
                    Toast({
                        message: '请输入验证码',
                        duration: 1500
                    });
                }else {
                    this.$http.post("/app.member/registerCheckVerfiy.html", {
                        sign: sign,
                        phone: this.phone,
                        verify: this.verify,
                        spreat: this.spreat,
                    })
                    .then(res => {
                        if (res.data.code == 1) {
                            localStorage.setItem("mysterycellspreat", this.spreat);
                            localStorage.setItem("mysterycellphone", this.phone);
                            sessionStorage.removeItem('xbzm-appParam')
                            this.$router.push(
                                {
                                    name: 'Registered',
                                    params: {id: this.checkbox + 3}
                                },
                            )
                        } else {
                            alert(res.data.msg)
                        }
                    });
                }
            }
        }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
    .sign-in{
        padding:40px;
        .nav{
            font-size: 52px;
            color: $main-color;
            padding-bottom: 30px;
            i{
                font-size: 46px;
            }
        }
        .phone,.code,.invite{
            position: relative;
            margin-top: 110px;
            font-size: 30px;
            color: #ccc;
            height: 60px;
            #code{
                padding: 0 180px;
            }
            label{
                position: absolute;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                line-height: 60px;
                top: 0px;
                left: 5px;
                i{
                    font-size: 36px;
                }
            }
            input{
                height: 100%;
                box-sizing: border-box;
                width: 100%;
                font-size: 30px;
                border:0;
                padding-left: 120px;
                border-bottom: 1px solid $c6-color;/*no*/
            }
            .btn{
                height: 50px;
                line-height: 50px;
                min-width: 130px;
                background: $main-color;
                padding: 0 15px;
                color: #fff;
                font-size: 26px;
                border-radius: 5px;
                text-align: center;
                position: absolute;
                right:0;
                top:0;
            }
        }
        .btn-group{
            margin-top: 90px;
            .btn{
                width: 100%;
                height: 80px;
                font-size: 30px;
                background: #fff;
                border: 1px solid $main-color;/*no*/
                color: $main-color;
                border-radius: 5px;
            }
            .btn-main{
                background: $main-color;
                color: #fff;
                margin-bottom: 20px;
            }
        }
    }
</style>



