<template>
    <div class="has-topbar">
    <div class="sign-in">
        <div class="nav"><i class="icon-left fast-click" @click="goBack(-1)"></i> 忘记密码</div>
        <div class="phone">
            <label for="user">手机号:</label>
            <input id="user" type="text" v-model="userPhone">
        </div>
        <div class="code">
            <label for="code">手机验证码:</label>
            <input id="code" type="text" v-model="userVerify">
            <div class="btn" @click="getVerification">{{getCodeName}}</div>
        </div>
        <div class="btn-group">
            <button class="btn btn-main" @click="nextFn">下一步</button>
        </div>
    </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return{
            userPhone: '',
            userVerify: '',
            spreat: '',
            getCodeName: '获取验证码',
            time: 60,
        }
    },
    methods:{
        //获取验证码
        getVerification(){
            let reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
            if(this.userPhone == ''){
                Toast({
                    message: '请输入手机号码',
                    duration: 1500
                });
                return;
            }else if(!reg.test(this.userPhone)){
                Toast({
                    message: '请输入正确的手机号码',
                    duration: 1500
                });
                return;
            }else{
                let sign = this.getEncrypt('phone='+this.userPhone)
                this.$http.post('/app.Member/resetVerify.html',{
                    sign: sign,
                    phone:this.userPhone
                }).then( response => {
                    if(response.data.code == 1){
                        this.time = 60;
                        this.timer();
//                        alert(response.data.data.verify)
                    }else{
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        })
                    }
                }).catch( error => {
                    console.log(error)
                })

            }
        },
        //下一步
        nextFn(){
            let sign = this.getEncrypt('phone='+this.userPhone+'&verify='+this.userVerify)
            this.$http.post('/app.Member/resetCheckVerify.html',{
                sign: sign,
                phone:this.userPhone,
                verify:this.userVerify
            }).then( response => {
                if(response.data.code == 1){
                    this.$router.push({name:'ConfirmPassword',params:{phone:this.userPhone,verify:this.userVerify}})
                }else{
                    Toast({
                        message: response.data.msg,
                        duration: 2000
                    })
                }
            }).catch( error => {
                console.log(error)
            })
        },
        timer(){
            if (this.time > 1) {
                this.time--;
                this.getCodeName = this.time+'s后重新获取';
                setTimeout(this.timer, 1000);
            }else if(this.time == 1){
                this.getCodeName = '获取验证码';
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.sign-in{
    padding:40px;
    .nav{
        font-size: 52px;
        color: $main-color;
        padding-bottom: 30px;
        i{
            font-size: 46px;
        }
    }
    .phone,.code,.invite{
        position: relative;
        margin-top: 110px;
        font-size: 30px;
        color: #ccc;
        height: 60px;
        #code{
            padding: 0 180px;
        }
        label{
            position: absolute;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            line-height: 60px;
            top: 0px;
            left: 5px;
            i{
                font-size: 36px;
            }
        }
        input{
            height: 100%;
            box-sizing: border-box;
            width: 100%;
            font-size: 30px;
            border:0;
            padding-left: 120px;
            border-bottom: 1px solid $c6-color;/*no*/
        }
        .btn{
            height: 50px;
            line-height: 50px;
            min-width: 130px;
            background: $main-color;
            padding: 0 15px;
            color: #fff;
            font-size: 26px;
            border-radius: 5px;
            text-align: center;
            position: absolute;
            right:0;
            top:0;
        }
    }
    .btn-group{
        margin-top: 90px;
        .btn{
            width: 100%;
            height: 80px;
            font-size: 30px;
            background: #fff;
            border: 1px solid $main-color;/*no*/
            color: $main-color;
            border-radius: 5px;
        }
        .btn-main{
            background: $main-color;
            color: #fff;
            margin-bottom: 20px;
        }
    }
}
</style>



