<template>
    <div class="has-topbar">
    <div class="my-set bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack('/My')"></span>
            <p>设置</p>
        </div>
        <div class="list">
            <router-link :to="list.url" class="item" v-for="(list,index) in lists" :key="index"><i :class="list.icon"></i><span>{{list.title}}</span></router-link>
        </div>
        <div class="btn-group">
            <div class="btn" @click="loginUp()">退出登录</div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        data () {
            return {
                lists:[
                    {
                        icon:'icon-user',
                        title:'个人资料',
                        url:'/Personage'
                    },
                    {
                        icon:'icon-address',
                        title:'收货地址',
                        url:'/Site/1'
                    },
                    {
                        icon:'icon-password',
                        title:'修改密码',
                        url:'/ResetPassword/login'
                    },
                    {
                        icon:'icon-about',
                        title:'关于我们',
                        url:'/AboutMe'
                    },
                    {
                        icon:'icon-blacklist',
                        title:'屏蔽设置',
                        url:'/Blacklist'
                    },
                    {
                        icon:'icon-phone',
                        title:'更改手机号',
                        url:'/ResetPassword/phone'
                    },
                    {
                        icon:'icon-wechat',
                        title:'绑定微信',
                        url:'/WechatBinding'
                    },
                ],
            }
        },
        // created(){
        //     this.isRechargeFn();
        // },
        methods:{
            //退出订单
            loginUp(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.member/logout.html',
                {
                    sign: sign,
                    token:this.getToken()
                })
                .then(response => {
                    Toast({
                        message: response.data.msg,
                        duration: 2000
                    });
                    this.$router.push('/index')
                })
                .catch(error => {
                    // console.log(error)
                })
                let keys = document.cookie.match(/[^ =;]+(?=\=)/g);
				if(keys) {
					for(var i = keys.length; i--;)
						document.cookie = keys[i] + '=0;expires=' + new Date(0).toUTCString()
				}
            }
        }
    }
</script>
<style lang="scss" scoped>
.list{
    background: $c8-color;
    padding-top:20px;
    .item{
        height:100px;
        display: block;
        line-height:100px;
        background: #fff;
        border-bottom: 1px solid $c6-color;
        font-size: 26px;
        position: relative;
        padding:0 40px 0 90px;
        &:first-child{
            border-top: 1px solid $c6-color;
        }
        i{
            font-size:40px;
            position: absolute;
            left:40px;
            top:32px;
        }
    }

}
.btn-group{
    background: #fff;
    padding: 80px 40px 0;
    .btn{
        line-height:80px;
        text-align: center;
        display: block;
        color: #fff;
        background: $main-color;
        border-radius: 5px;
        font-size: 30px;
    }
}
</style>



