<template>
    <div class="has-topbar">
    <div class="set-site bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>收货地址</p>
        </div>
        <div class="list">
            <router-link to="/SiteDetails/0/-1" class="add-site"><i class="icon-add" replace></i>新增收货地址</router-link>
            <div class="item" v-for="(userAddress,index) in userAddresss" :key="index">
                <div class="top" @click="goSubmitOrder(index)">
                    <p>姓名：{{userAddress.username}}</p>
                    <p>地址：{{userAddress.province + userAddress.city + userAddress.area + userAddress.address}}</p>
                    <p>电话：{{userAddress.phone}}</p>
                </div>
                <div class="bottom">
                    <div class="">
                        <span>默认地址</span>
                        <div class="switch-group">
                            <div :class="['switch_btn',{ switch_on:mydefault==index}]" @click="setDefault(index, userAddress.address_id)"></div>
                        </div>
                    </div>
                    <div>
                        <router-link class="btn" :to="{ path: `/SiteDetails/1/${index}`}">修改</router-link>
                        <div class="btn btn-primary" @click="delAddress(userAddress.address_id,index)">删除</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import {Toast} from "mint-ui"
    export default {
        name: 'setSite',
        data () {
            return {
                id:'',
                userAddresss:{},
                mydefault:'',

            }
        },
        created(){
            this.getUserAddress();
            this.id = this.$route.params.id;
        },
        methods:{
            setDefault(index,address_id){
                let sign = this.getEncrypt('address_id='+address_id+'&token='+this.getToken())
                this.$http.post('/app.Address/up.html',
                {
                    sign: sign,
                    token:this.getToken(),
                    address_id: address_id
                })
                .then(response => {
                    this.mydefault = index
                })
                .catch(error => {
                    console.log(error)
                })
            },

            getUserAddress(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.Address/get.html',
                {
                    sign: sign,
                    token:this.getToken(),
                })
                .then(response => {
                    for(let i = 0; i<response.data.data.length;i++){
                        if(response.data.data[i].is_default==1){
                            this.mydefault = i
                        }
                    }
                    this.userAddresss = response.data.data;
                })
                .catch(error => {
                    console.log(error)
                })
            },
            delAddress(key,index){
                let sign = this.getEncrypt('address_id='+key+'&token='+this.getToken())
                this.$http.post('/app.Address/del.html',
                {
                    sign: sign,
                    token:this.getToken(),
                    address_id:key
                })
                .then(response => {
                    if(response.data.code == 1){
                        this.getUserAddress();
                        Toast({
                            message: '删除成功！',
                            duration: 1500
                        });
                    }
                })
                .catch(error => {
                    console.log(error)
                })
            },

            goSubmitOrder(index){
                if( this.id == '2'){
                    this.$cache.set('SiteIndex', index)
                    this.goBack(-1);
                }
            }
        },
    }
</script>
<style lang="scss" scoped>

.load{
    line-height: 60px;
    text-align: center;
    font-size:26px;
    background: $c8-color;
}
.list{
    background: $c8-color;
    .add-site{
        line-height: 100px;
        display: block;
        font-size:26px;
        padding:0 40px 0 100px;
        background: #fff;
        margin-top:10px;
        position: relative;
        border-top:1px solid $c6-color;/*no*/
        border-bottom:1px solid $c6-color;/*no*/
        i{
            font-size: 60px;
            position: absolute;
            top: 20px;
            color: $c4-color;
            left: 40px;
        }
    }
    .item{
        background: #fff;
        margin-top: 20px;
        border-top:1px solid $c6-color;/*no*/
        .top{
            padding: 20px 40px;
            font-size: 24px;
            line-height: 44px;
            border-bottom:1px solid $c6-color;/*no*/
        }
        .bottom{
            padding: 0 40px;
            border-bottom:1px solid $c6-color;/*no*/
            font-size:26px;
            height: 100px;
            display: flex;
            justify-content: space-between;
            position:relative;
            align-items:center;
            .switch-group{
                position: absolute;
                left:180px;
                top:20px;
            }
            .btn{
                width:90px;
                height: 51px;
                line-height: 51px;
                display: inline-block;
                border:1px solid #000;/*no*/
                text-align: center;
                border-radius: 5px;
                &.btn-primary{
                   border-color: $main-color;
                   color: $main-color;
                }
            }
        }
    }
}
</style>



