<template>
    <div class="has-topbar">
        <div class="avater bar_top">
            <div class="nav_bar">
                <router-link to="./Personage" class="icon-left fast-click"></router-link>
                <p>个人头像</p>
                <div @click="sheetShow = true" class="btn">修改</div>
            </div>
        </div>
        <div class="con" ref="wrapper">
            <div class="img-group">
                <img class="img" v-lazy="headerImage" :key="headerImage" alt=""/>
            </div>
        </div>
        <!--选择弹窗-->
        <div v-if="sheetShow" @click="closeComment" class="sheet-box">
            <div class="sheet has-fixed-bottom" ref="commentBox">
                <div class="list" @click="photograph">拍照</div>
                <div @click="readFile">照片图库</div>
                <div class="list-bottom" @click="sheetShow=false">取消</div>
            </div>
        </div>
        <!--裁图height-->
        <div v-if="ImageClip" class="ImageClip bar_top">
            <div class="nav_bar">
                <div @click="cancel" class="cancel">取消</div>
                <p></p>
                <div @click="accomplish" class="btn">完成</div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                headerImage: "",
                sheetShow: false,
                headerH:'',
                ImageClip: false,
                ImageUrl:'',
                imgList:'',
            };
        },
        created() {
            this.getUserInfo();
        },
        mounted(){
            if(this.$refs.wrapper){
                this.headerH = this.$refs.wrapper.getBoundingClientRect().top;
            }
        },
        methods: {
            getUserInfo() {
                let sign = this.getEncrypt("token=" + this.getToken());
                this.$http
                    .post("/app.Member/index.html", {
                        sign: sign,
                        token: this.getToken()
                    })
                    .then(response => {
                        if (response.data.code == 1) {
                            this.headerImage = response.data.data.member.headimg;
                        } else {
                        }
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },
            //点击空白关闭弹框
            closeComment(){
                var sp = this.$refs.commentBox;
                if(sp){
                    if(!sp.contains(event.target)){ //这句是说如果我们点击到了id为myPanel以外的区域
                        this.sheetShow = false;
                    }
                }
            },
            accomplish(){
                var _this = this;
                var token = this.getToken();
                function convertImgToBase64(url, callback, outputFormat){
                    var canvas = document.createElement('canvas'),
                        ctx = canvas.getContext('2d'),
                        img = new Image;
                    img.crossOrigin = 'Anonymous';//它开启了本地的跨域允许。当然服务器存储那边也要开放相应的权限才行，如果是设置了防盗链的图片在服务端就没有相应的权限的话你本地端开启了权限也是没有用的
                    img.onload = function(){
                        canvas.width = 800;
                        canvas.height = img.height/(img.width/800);
                        ctx.drawImage(img,0,0,800,img.height/(img.width/800));
                        var dataURL = canvas.toDataURL('image/jpg',0.6);//没权限的跨域图片在使用canvas.toDataURL()数据导出时会报错
                        callback.call(this, dataURL);
                        canvas = null;
                    };
                    img.src = url;
                };
                function submit(imgUrl){
                    let sign = _this.getEncrypt('image='+imgUrl+'&token='+token);
                    // alert(sign)
                    _this.$http.post("/app.Member/headimg.html",{
                        sign: sign,
                        token: token,
                        image: imgUrl,
                    }).then(res => {
//                        alert(res.data.msg)
                        _this.cancel()
                        document.location.reload();
                    })
                }
                var FNImageClip = api.require('FNImageClip');
                FNImageClip.save({
                    destPath: _this.ImageUrl,
                    copyToAlbum: false,
                    quality: 0.6
                }, function(ret, err) {
                    if (ret) {
//                        alert(JSON.stringify(ret.destPath));
                        convertImgToBase64(ret.destPath,function(base64Img){
//                            _this.imgList.push(base64Img)
                            submit(base64Img)
                        })
                    } else {
//                        alert(JSON.stringify(err));
                    }
                });
            },
            cancel(){
                this.ImageClip = false;
                var FNImageClip = api.require('FNImageClip');
                FNImageClip.close();
                // this.$router.push('/Personage')
            },
            photograph(){
                let _this = this;
                this.sheetShow = false;

                api.getPicture({
                    sourceType: 'camera',
                    encodingType: 'jpg',
                    mediaValue: 'pic',
                    allowEdit: false,
                    destinationType: 'url',
                    quality: 80,
                    targetWidth: 800,
                    targetHeight: 800,
                    saveToPhotoAlbum: false
                }, function(ret, err) {
                    if (ret) {
                        _this.ImageUrl = ret.data;
                        _this.ImageClip = true;
                        var FNImageClip = api.require('FNImageClip');
                        FNImageClip.open({
                            rect: {
                                x: 0,
                                y: _this.headerH,
                            },
                            mode: 'image',
                            srcPath: ret.data,
                            style: {
                                mask: 'rgba(0,0,0,0.6)',
                                clip: {
                                    w: api.winWidth - 60,
                                    h: api.winWidth - 60,
                                    x: 30,
                                    y: (api.winHeight - api.winWidth - 20) / 2,
                                    borderColor: '#fefefe',
                                    borderWidth: 1
                                }
                            },
                        }, function(ret2, err2) {
                            if (ret2) {
//                                alert(JSON.stringify(ret2));
                            } else {
//                                alert(JSON.stringify(err2));
                            }
                        });
                    } else {

                    }
                });
            },
            readFile(){
                const _this = this
                api.getPicture({
                    sourceType: 'album',
                    direction: 'front',
                    quality: 90,
                    saveToPhotoAlbum: false
                }, function(ret, err) {
                    if (ret) {
                        // alert(JSON.stringify(ret.data))
                        _this.ImageUrl = ret.data;
                        _this.ImageClip = true;
                        var FNImageClip = api.require('FNImageClip');
                        FNImageClip.open({
                            rect: {
                                x: 0,
                                y: _this.headerH,
                            },
                            mode: 'image',
                            srcPath: ret.data,
                            style: {
                                mask: 'rgba(0,0,0,0.6)',
                                clip: {
                                    w: api.winWidth - 60,
                                    h: api.winWidth - 60,
                                    x: 30,
                                    y: (api.winHeight - api.winWidth - 20) / 2,
                                    borderColor: '#fefefe',
                                    borderWidth: 1
                                }
                            },
                        }, function(ret2, err2) {
                            if (ret2) {
                            //    alert(JSON.stringify(ret2));
                            } else {
                            //    alert(JSON.stringify(err2));
                            }
                        });
                        // if (ret.data.length > 0) {
                        //     // openImgClip(ret.data);
                        //     self.$root.imgClip = ret.data

                        // 	self.$root.goTo({path: 'img-clip'})


                        // }
                    }
                });
            },
            // readFile(){
            //     let _this = this;
            //     this.sheetShow = false;
            //     function convertImgToBase64(url, callback, outputFormat){
            //         var canvas = document.createElement('canvas'),
            //             ctx = canvas.getContext('2d'),
            //             img = new Image;
            //         img.crossOrigin = 'Anonymous';//它开启了本地的跨域允许。当然服务器存储那边也要开放相应的权限才行，如果是设置了防盗链的图片在服务端就没有相应的权限的话你本地端开启了权限也是没有用的
            //         img.onload = function(){
            //             canvas.width = 800;
            //             canvas.height = img.height/(img.width/800);
            //             ctx.drawImage(img,0,0,800,img.height/(img.width/800));
            //             var dataURL = canvas.toDataURL('image/jpg',0.1);//没权限的跨域图片在使用canvas.toDataURL()数据导出时会报错
            //             callback.call(this, dataURL);
            //             canvas = null;
            //         };
            //         img.src = url;
            //     };
            //     var UIMediaScanner = api.require('UIMediaScanner');
            //     UIMediaScanner.open({
            //         type: 'picture',
            //         column: 4,
            //         max: 1,
            //         texts:{
            //             selectedMaxText:'最多选择1张照片！'
            //         },
            //         exchange: true,
            //         rotation: false
            //     }, function(ret) {
            //         if (ret) {
            //             convertImgToBase64(ret.list[0].path,function(base64Img){
            //                 _this.ImageUrl = base64Img;
            //                 _this.ImageClip = true;
            //                 var FNImageClip = api.require('FNImageClip');
            //                 FNImageClip.open({
            //                     rect: {
            //                         x: 0,
            //                         y: _this.headerH,
            //                     },
            //                     mode: 'image',
            //                     srcPath: base64Img,
            //                     style: {
            //                         mask: 'rgba(0,0,0,0.6)',
            //                         clip: {
            //                             w: api.winWidth - 60,
            //                             h: api.winWidth - 60,
            //                             x: 30,
            //                             y: (api.winHeight - api.winWidth - 20) / 2,
            //                             borderColor: '#fefefe',
            //                             borderWidth: 1
            //                         }
            //                     },
            //                 }, function(ret2, err2) {
            //                     if (ret2) {
            //                     //    alert(JSON.stringify(ret2));
            //                     } else {
            //                     //    alert(JSON.stringify(err2));
            //                     }
            //                 });
            //             })
                        
            //         }
            //     });
            // },
        }
    };
</script>

<style lang="scss" scoped>
    .con{
        /*padding-top: 100px;*/
        position: relative;
        .img-group{
            position: absolute;
            z-index: 99;
            left: 0;
            width:100%;
            top: 150px;
            padding: 60px;
            box-sizing: border-box;
        }
    }
    .nav_bar .btn{
        padding: 10px 40px;
        color: $main-color;
    }
    .cancel{
        padding: 10px 40px;
    }
    .ImageClip{
        z-index: 2003;
    }
    .sheet-box{
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        height:100%;
        z-index: 2003;
        background: rgba(0,0,0,0.5);
        .sheet{
            position:absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            background: #e0e0e0;
            text-align: center;
            font-size: 26px;
            &>div{
                  background: #fff;
                  height: 80px;
                  line-height: 80px;
                &.list{
                     border-bottom: 1px solid $c6-color;/*no*/
                 }
                &.delete{
                     color: #df5000;
                 }
                &.list-bottom{
                     margin-top: 20px;
                 }
            }
        }
    }

</style>


