<template>
    <div class="has-topbar">
        <div class="my-invite bar_top">
            <div class="nav_bar">
                <div @click="goBack(-1)" class="icon-left fast-click"></div>
                <p>邀请好友</p>
                <div v-if="QRcodeShow" @click="WXshare" class="btn">分享</div>
            </div>
            <div class="con" v-if="QRcodeShow">
                <div class="img-group">
                    <img @click="openImg(imgList,0,true)" class="img" :src="QRcode.qrcode" alt="">
                    <p>点击查看大图，保存到手机</p>
                </div>
                <!--<h4 class="code">邀请码：{{QRcode.spreat_code}}</h4>-->
                <!--<button @click="lookFn" class="btn">复制邀请码</button>-->
            </div>
            <div class="hint" v-else>{{msggage}}</div>
        </div>
    </div>
</template>

<script>
    import {Toast} from 'mint-ui'
    export default {
        data () {
            return {
                QRcode:[],
                imgList:[],
                QRcodeShow: false,
                msggage:'',
                shareThumbPath:'',
            }
        },
        created(){
            this.getInvite();
        },
        methods:{
            getInvite(){
                let sign = this.getEncrypt("token="+this.getToken())
                this.$http.post("/app.member/myqrcode.html",{
                    sign: sign,
                    token: this.getToken()
                }).then(res => {
                    if(res.data.code == 1){
                        this.QRcode = res.data.data
                        this.imgList.push(this.QRcode.qrcode);
                        this.QRcodeShow = true;
                        this.download();
                    }
                    this.msggage = res.data.msg
                })
            },
            lookFn(){
                const input = document.createElement('input');
                input.setAttribute('readonly', 'readonly');
                input.setAttribute('value', this.QRcode.spreat_code);
                document.body.appendChild(input);
                input.setSelectionRange(0, 9999);
                if (document.execCommand('copy')) {
                    document.execCommand('copy');
                    Toast({
                        message: "复制成功",
                        duration: 2000,
                    });
                }
                document.body.removeChild(input);
            },
            WXshare(){
                var self = this;
                var wx = api.require('wx');
                //检查微信
                wx.isInstalled(function(ret, err) {
                    if (ret.installed) {
                        //微信分享
                        wx.shareImage({
                            scene: 'session',
                            thumb: self.shareThumbPath,
                            contentUrl: self.shareThumbPath,
                        }, function(ret, err) {
                            if (ret.status) {
                                Toast({
                                    message: "分享成功",
                                    duration: 2000,
                                });
                            } else {
                                if (err.code == 2){
                                    Toast({
                                        message: '用户取消',
                                        duration: 2000,
                                    });
                                    return;
                                }else if(err.code == 3){
                                    Toast({
                                        message: '发送失败',
                                        duration: 2000,
                                    });
                                }else if(err.code == 4){
                                    Toast({
                                        message: '授权拒绝',
                                        duration: 2000,
                                    });
                                }
                            }
                        });
                    }else {
                        Toast({
                            message: "当前设备未安装微信客户端",
                            duration: 2000
                        });
                        return
                    }
                })
            },
            download(){
                var self = this;
                api.download({
                    url: self.QRcode.qrcode+'?imageView2/0/h/160/q/95',
                    cache: false,
                    allowResume: true
                }, function(ret) {
                    if (ret.state == 1) {
                        self.shareThumbPath = ret.savePath;
                    }
                });
            },
        }
    }
</script>
<style lang="scss" scoped>
    .con{
        padding: 100px 40px 0;
        .img-group{
            width: 300px;
            margin: 0 auto;
            p{
                text-align: center;
                color: $c4-color;
                font-size: 24px;
                margin-top: 10px;
            }
        }
        .code{
            text-align: center;
            margin-top: 60px;
            font-size: 36px;
        }
        .btn{
            height: 80px;
            background: $main-color;
            border: 0;
            border-radius: 5px;
            color: #fff;
            width: 100%;
            margin-top: 60px;
        }
    }
    .hint{
        padding-top: 60px;
        font-size: 30px;
        color: $c4-color;
        text-align: center;
    }
    .nav_bar .btn{
        padding: 10px 40px;
        color: $main-color;
    }
</style>



