router.beforeEach((to, from, next) => {
    if (to.meta.requireAuth) {
        let arr,reg=new RegExp("(^| )"+'mysteryCellToken'+"=([^;]*)(;|$)");
        if(arr=document.cookie.match(reg)){
            if (unescape(arr[2])) {   //判断是否已经登录
                next();
            } else {
                next({
                    path: '/login'
                })
            }
        }else{
            next({
                path: '/login'
            })
        }
    } else {
        next();
    }
});

export default router



