<template>
<div class="tourism-index">
    <div class="has-topbar">
    <div class="message-lists bar_top">
        <div class="nav_bar">
            <span class="icon-left" @click="goBack('/My')"></span>
            <p>我的福利</p>
        </div>

        <div class="con">
            <div class="menu">
                <router-link class="list" :to="{name:'MyTourismList', params: {id:tourism.id}}" v-for="(tourism,index) in tourismData" :key="index">{{tourism.title}}</router-link>
                <router-link class="list" to="/MyWelfare">车辆奖励</router-link>
                <!-- <router-link class="list" to="/MyTourismList/2">国外游</router-link> -->
            </div>
        </div>
    </div>
    </div>
</div>
</template>

<script>
    // import { Toast } from 'mint-ui';
    export default {
        data(){
            return{
                tourismData:{}
            }
        },
        created(){
            this.getTourismData()
        },
        methods:{
            getTourismData(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.Wallet/tourism.html',{
                    sign: sign,
                    token: this.getToken()
                }).then( response => {
                    if(response.data.code == 1){
                        this.tourismData = response.data.data
                    }else{
                        
                    }
                }).catch( error => {
                    console.log(error)
                })
            }
        }
    }
</script>
<style lang="scss" scoped>
.menu{
    border-top: 1px solid $c6-color;/*no*/
    .list{
        height:100px;
        display: flex;
        padding:0 40px;
        line-height: 100px;
        border-bottom: 1px solid $c6-color;/*no*/
        font-size: 26px;
        align-items: center;
        p{
            flex: 1;
            padding-left: 20px;
        }
        i{
            font-size:40px;
        }
    }
}
</style>


