<template>
    <div class="has-topbar">
        <div class="bill-details bar_top">
            <div class="nav_bar">
                <span class="icon-left fast-click" @click="goBack(-1)"></span>
                <p>创始人</p>
            </div>
            <div class="con">
                <div class="header">
                    <h4>￥{{lists.teamAmount}}
                        <p>团队销售总额</p>
                    </h4>
                </div>
                <div class="box">
                    <div class="left">
                        <h4>{{lists.teamnum}}人</h4>
                        <p>团队总人数</p>
                    </div>
                    <div class="right">
                        <h4>{{lists.teamOrderNum}}单</h4>
                        <p>团队总单数</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                lists:[],
            }
        },
        created(){
            this.getDate();
        },
        methods:{
            getDate(){
                let sign = this.getEncrypt("token="+this.getToken())
                this.$http.post("/app.teams/team_cofounder.html",{
                    sign: sign,
                    token: this.getToken()
                }).then(res => {
                    if(res.data.code == 1){
                        this.lists = res.data.data
                    }
                })
            }
        },
    }
</script>
<style lang="scss" scoped>
.con{
    .header{
        height: 200px;
        border-bottom: 1px solid $c6-color;/*no*/
        display: flex;
        justify-content: center;
        align-items: center;
        p{
            margin-top: 18px;
        }
    }
    .box{
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 1px solid $c6-color;/*no*/
        &>div{
            height:140px;
            flex:1;
            padding-top:32px;
            box-sizing: border-box;
        }
        .left{
            border-right: 1px solid $c6-color;/*no*/
        }
    }
    h4{
        font-size: 36px;
        color: #000;
        text-align: center;
        margin-bottom: 18px;
    }
    p{
        font-size: 24px;
        color: #999;
        text-align: center;
    }
}
</style>



