<template>
    <div class="has-topbar">
        <div class="bar_top">
            <div class="nav_bar">
                <router-link class="icon-left fast-click" to="/Index"></router-link>
                <!-- <div @click="goBack(-1)"></div> -->
                <p>返回首页</p>
            </div>
        </div>
        
    <div class="login">
        <div class="nav">请登录</div>
        <div class="agreement">
            <!--<div :class="['choose',{check_on: isSelect}]"  @click="single"></div>-->
            <div>登录注册表示同意</div>
            <router-link to="/Agreement/1">用户协议</router-link> 和
            <router-link to="/Agreement/2">隐私条例</router-link>
        </div>

        <div class="phone"><label for="user"><i class="icon-user"></i> 账号:</label><input id="user" type="text" v-model.trim="phone"></div>
        <div class="password"><label for="password"><i class="icon-password"></i> 密码:</label><input id="password" type="password" v-model="password"></div>
        <div class="forget-psd">
            <router-link to="/ForgetPassword" >忘记密码？</router-link>
        </div>
        <div class="btn-group">
            <button class="btn btn-main" @click="login()">立即登录</button>
            <button class="btn" @click="signIn()">立即注册</button>
        </div>
        <div class="others" v-if="wxShow">
            <div class="others_title">
                <i class="line"></i>
                <span class="other_login">使用第三方账号登录</span>
                <i class="line"></i>
            </div>
            <div class="wechat_icon">
                <div @click="wechatLogin()"><img src="@/common/images/wechat_login.png" alt=""></div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return{
            phone: '',
            password:'',
            wxShow: false,
        }
    },
    created(){
        this.anExamination();
        api.closeFrame({
            name: 'cs'
        });
    },
    methods:{
        login(){
            let sign = this.getEncrypt('password='+this.password+'&phone='+this.phone)
            this.$http.post('/app.Member/login.html',{
                sign: sign,
                phone: this.phone,
                password: this.password
            }).then( response => {
                if(response.data.code == 1){
                    this.setCookie(response.data.data.token,response.data.data.tokentime)
                    this.$router.push({ path: '/Index' })
                }else{
                    Toast({
                        message: response.data.msg,
                        duration: 1500
                    });
                }
            }).catch( error => {
                console.log(error)
            })
        },
        setCookie(token,tokentime){
            let newdate = new Date();
            newdate.setTime(tokentime * 1000);
            newdate = newdate.toUTCString()
            document.cookie = `mysteryCellToken=${token};expires=${newdate}`
        },
        signIn(){
            this.$cache.set("PhoneData",'');
            this.$router.push({name:'SignIn'});
        },
        anExamination(){
            var self = this;
            var wx = api.require('wx');
            wx.isInstalled(function(ret, err) {
                if (!ret.installed) {
                    self.wxShow = false;
                    return
                }else {
                    self.wxShow = true;
                }
            });
        },
        wechatLogin(){
            var wx = api.require('wx');
            var _this = this;
            //检查有没有安装微信
            wx.isInstalled(function(ret, err) {
                if (!ret.installed) {
                    Toast({
                        message: '当前设备未安装微信客户端',
                        duration: 1500
                    });
                } else {
                    wx.auth(function(ret, err) {
                        if (ret.status) {
                            //获取微信的token
                            wx.getToken({
                                code: ret.code
                            }, function(ret, getToken) {
                                if (ret.status) {
                                    //获取微信用户的信息
//                                    requestBindingWX(ret.openId)
//                                    wx.getUserInfo()
                                    wx.getUserInfo({
                                        accessToken: ret.accessToken,
                                        openId: ret.openId
                                    }, function(retUser, errUser) {
                                        if (retUser.status) {
//                                            alert(JSON.stringify(retUser));
                                            requestBindingWX(retUser.headimgurl,retUser.nickname,retUser.openid,retUser.sex,retUser.unionid)
                                        } else {
                                            alert(errUser.code);
                                        }
                                    });
                                } else {
                                    alert(getToken)
                                }
                            });
                        } else {
                            var codeArr = ['用户同意登录', '用户取消', '用户拒绝授权', '当前设备未安装微信客户端'];
                            if (err.code != -1) {
                                Toast({
                                    message: codeArr[err.code],
                                    duration: 1500
                                });
                            } else {
                                // return msg.toast('未知错误');
                                Toast({
                                    message: "未知错误",
                                    duration: 1500
                                });
                            }
                        }
                    });
                }
            });
            function requestBindingWX(headImgUrl,nickName,openId,Sex,unionId){
                //发送绑定请求
                let appId = "wx5ebbf0c30f8fd02f";
                let sign = _this.getEncrypt("appid="+appId+
                        "&headimgurl="+headImgUrl+
                        "&nickname="+nickName+
                        "&openid="+openId+
                        "&sex="+Sex+
                        "&unionid="+unionId
                )
                _this.$http.post("/app.Member/wxlogin.html",{
                    sign: sign,
                    appid: appId,
                    openid: openId,
                    nickname: nickName,
                    sex: Sex,
                    headimgurl: headImgUrl,
                    unionid: unionId,
                }).then(res => {
                    if(res.data.code == 1){
                        let newdate = new Date();
                        newdate.setTime(res.data.data.tokentime * 1000);
                        newdate = newdate.toUTCString()
                        document.cookie = `mysteryCellToken=${res.data.data.token};expires=${newdate}`;
                        if(res.data.data.phone == '' || res.data.data.phone == undefined){
                            _this.$router.push("/WxPhone");
                        }else{
                            _this.$router.push("/Index");
                            Toast({
                                message: res.data.msg,
                                duration: 1500
                            });
                        }
                    }

                }).catch(err => {
                    alert(err)
                })
            };
        }
    }
}
</script>

<style lang="scss" scoped>
.agreement{
    display: flex;
    align-items: center;
    margin-top: 10px;
    font-size: 30px;
    div{
        /*margin-right: 10px;*/
        color: $c4-color;
    }
    a{
        color: $main-color;
        margin: 0 10px;
    }
}
.login{
    padding:40px;
    .nav{
        font-size: 52px;
        color: $main-color;
        padding-bottom: 30px;
    }
    .phone,.password{
        position: relative;
        margin-top: 110px;
        font-size: 30px;
        color: #ccc;
        height: 60px;
        label{
            position: absolute;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            line-height: 60px;
            top: 0px;
            left: 5px;
            i{
                font-size: 36px;
            }
        }
        input{
            height: 100%;
            font-size: 30px;
            box-sizing: border-box;
            width: 100%;
            border:0;
            padding-left: 120px;
            border-bottom: 1px solid $c6-color;/*no*/
        }
    }
    .forget-psd{
        text-align: right;
        display: block;
        font-size: 30px;
        margin-top: 30px;
        a{
            color: $main-color;
        }
    }
    .btn-group{
        margin-top: 90px;
        .btn{
            width: 100%;
            height: 80px;
            font-size: 30px;
            background: #fff;
            border: 1px solid $main-color;/*no*/
            color: $main-color;
            border-radius: 5px;
        }
        .btn-main{
            background: $main-color;
            color: #fff;
            margin-bottom: 20px;
        }
    }
}
.others{
    margin-top: 150px;
    .others_title{
        display: flex;
        justify-items: center;
        align-items: center;
        .line{
            flex: 1;
            height: 1px;/*no*/
            background: $c4-color;
        }
        .other_login{
            color: $c4-color;
            padding: 0 20px;
            font-size: 30px;
        }
    }
    .wechat_icon{
        display: flex;
        align-items: center;
        justify-content: center;
        div{
            margin-top:40px;
            img{
                width: 60px;
            }
        }
    }
}
</style>



