<template>
    <div class="has-topbar">
    <div class="about-me bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>关于我们</p>
        </div>
        <div class="con">
            <div class="text-box" v-html="msg"></div>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: 'AboutMe',
        data () {
            return {
                msg:'',
                sign:''
            }
        },
        created(){
            let sign = this.getEncrypt(`token=${this.getToken()}`);
            this.$http.post('/app.Data/about.html',{
                sign: sign,
                token:this.getToken(),
            }).then( response => {
                this.msg = response.data.data
            }).catch( error => {
                console.log(error)
            })
        }
    }
</script>
<style lang="scss" scoped>
.con{
    background: $c8-color;
    padding-top: 20px;
    .text-box{
        padding: 40px;
        background: #fff;
        border-top: 1px solid $c6-color;/*no*/
        font-size: 26px;
        line-height: 26px;/*no*/
    }
}
</style>



