<template>
    <div class="has-topbar">
    <div class="order-index bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack('/My')"></span>
            <p>我的订单</p>
        </div>
        <div class="order">
            <router-link :to="tab.url" v-for="(tab,index) in tabs" :key="index"><span>{{tab.name}}</span></router-link>
        </div>

        <keep-alive>
            <router-view/>
        </keep-alive>

    </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            orderId:'',
            tabs:[
                {name:'全部',url:'/order/0/AllOrder'},
                {name:'待付款',url:'/order/1/PayOrder'},
                {name:'待发货',url:'/order/2/DeliverOrder'},
                {name:'待收货',url:'/order/3/TakeOrder'},
                {name:'已完成',url:'/order/4/FinishOrder'},
            ]
        }
    },
    created(){
        this.orderId = this.$route.params.id;
    },
    methods:{

    }
}
</script>

<style lang="scss" scoped>
.order{
    height:100px;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    font-size: 24px;
    padding: 0 20px;
    border-bottom: 1px solid $c6-color;/*no*/
    a{
        vertical-align: middle;
        color: $c4-color;
        padding: 0 20px;
        span{
            height:100%;
            line-height: 100px;
            display: inline-block;
            padding: 0 5px;
            box-sizing: border-box;
        }
        &.active{
            span{
                border-bottom: 3px solid $main-color;/*no*/
                color: $main-color;
            }
         }
    }

}
</style>



