<template>
    <div class="index-index" >
        <div v-if="lists != ''" class="wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
            <mt-swipe :auto="3000">
                <mt-swipe-item v-for="(list,index) in lists.slider" :key="index"><router-link :to="{name:'Goods',params:{id:list.url}}"><img :src="list.image" alt=""/></router-link></mt-swipe-item>
            </mt-swipe>
            <div class="notice" >
                <router-link v-if="notice != ''" class="notice-details" :to="{ name: 'NoticeDetails', params: {id: 1,details:notice.id} }"><i class="icon icon-notice"></i><p>【公告】{{notice.title}}</p></router-link>
                <router-link v-else class="notice-details" to=""><i class="icon icon-notice"></i><p>【公告】</p></router-link>
                <router-link class="more" :to="{ name: 'Notice', params: {id: 1} }"><span>更多+</span></router-link>
            </div>
            <ul class="list" >
               <li class="item" v-for="(list,index) in lists.goodslist" :key="index">
                   <router-link class="fast-click" :to="{name:'Goods',params:{id:list.id}}"><img class="img" v-lazy="list.goods_logo" :key="list.goods_logo" /></router-link>
               </li>
            </ul>
            <div @click="openVideoPlay(videoUrl)" class="video" :style="{backgroundImage: 'url(' + videoPic + ')'}">
                <span class="icon-play"></span>
            </div>
            </mt-loadmore>
        </div>
        <Nav></Nav>
    </div>
</template>

<script>
    import Nav from '@/base/nav/nav'
    export default {
        data(){
            return {
                lists: [],
                sign:'',
                playShow:true,
                notice:'',
                videoPic:'',
                videoUrl:'',
                token:'',
//                member:[],

                wrapperHeight: 0,
                autoFill:false,
                bottomPullText:'加载更多',
                topPullText:'刷新',
                allLoaded: false,
            }
        },
        components: {
            Nav
        },
        beforeCreate(){

        },
        created(){
           alert(localStorage.getItem('xbzm-appParam')+'index.vue')
            let appParam = sessionStorage.getItem('xbzm-appParam');
            if(appParam == "" || appParam == null || appParam == undefined){

            }else{
                this.$router.push('/merchant');
            }

            if(this.$cache.get("IndexData")){
                this.lists = this.$cache.get("IndexData");
                this.notice = this.lists.notice;
                this.videoPic = this.lists.video.pic;
                this.videoUrl = this.lists.video.url;
                this.getIndexData(0);
                return
            }else{
                this.getIndexData(0);
            }

        },
        activated(){

        },
        methods:{
            //下拉刷新
            loadTop() {
                this.getIndexData(1);
                this.$refs.loadmore.onTopLoaded();
            },
            getIndexData(value){
//                this.token = this.getToken();
                let sign =  this.getEncrypt('');
                this.$http.post('/app.Index/getinfo.html',
                    {
                        sign: sign,
                    })
                    .then(response => {
                        if(response.data.code == 1){
                            if(value == 1){
                                this.$refs.loadmore.onTopLoaded();
                                this.lists = response.data.data;
                                this.notice = this.lists.notice;
                                this.videoPic = this.lists.video.pic;
                                this.videoUrl = this.lists.video.url;
                                this.$cache.set("IndexData",response.data.data);
                                return
                            }else{
                                if(!this.$cache.get("IndexData")){
                                    this.lists = response.data.data;
                                    this.notice = this.lists.notice;
                                    this.videoPic = this.lists.video.pic;
                                    this.videoUrl = this.lists.video.url;
                                }
                                this.$cache.set("IndexData",response.data.data);
                            }
                        }
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },
            //通过扫码修改上家
//            spreatCode(){
//                console.log(this.member.is_relationship == 0)
//                if ( this.member.spreat_code != this.$cache.get("appParam") && this.member.is_relationship == 0 && this.$cache.get("appParam") != false){
//                    let appParamSign = this.getEncrypt("spead="+this.$cache.get("appParam")+"&token="+this.token);
//                    this.$http.post("/app.member/saveRelation.html",{
//                        sign: appParamSign,
//                        token: this.token,
//                        spead: this.$cache.get("appParam")
//                    }).then(res => {
//                        console.log(res.data.msg)
//                        this.$cache.set("appParam","");
//                    })
//                }
//            }
        },
        mounted(){
            setTimeout(()=>{
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar){
                    this.wrapperHeight = document.documentElement.clientHeight - (1.7*window.rem) - 34;
                    return
                }else{
                    this.wrapperHeight = document.documentElement.clientHeight - (1.7*window.rem);
                    return
                }
            },50)
        }
    }
</script>

<style lang='scss' scoped>
    .wrapper{
        overflow:scroll;
        -webkit-overflow-scrolling: touch;
        position: relative;
    }
    .index-index{
        padding-bottom:125px;
        background: #fff;
    }
    .mint-swipe{
        height: 500px;
        .mint-swipe-item img{
            width:100%;
        }
    }
    .notice{
        height:100px;
        display: flex;
        font-size:24px;
        color:$c4-color;
        justify-content:space-between;
        /*align-items:stretch;*/
        .notice-details{
            display: flex;
            height:100px;
            flex: 1;
            justify-content: flex-start;
            align-items:center;
            padding-left: 20px;
            font-size: 24px;
            color: $c4-color;
            p{
                line-height: 30px;
            }
            i{
                color: $main-color;
                font-size: 30px;
            }
        }
        .more{
            padding: 0 20px;
            line-height: 100px;
            color: $c4-color;
        }
    }
    .list{
        width:100%;
        .item{
            height:300px;
            margin-bottom:20px;
            overflow:hidden;
            img{
                width:100%;
                height:auto;
            }
        }
    }
    .video{
        height: 350px;
        position:relative;
        background-size:contain;
        background-repeat: no-repeat;
        background-position: center center;
        span{
            position: absolute;
            top: 50%;
            left: 50%;
            margin: -50px 0 0 -50px;
            font-size:100px;
            color: #fff;
        }
    }

</style>



