<template>
    <div class="has-topbar">
    <div class="my-team bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack('/My')"></span>
            <p>我的团队</p>
        </div>
        <div class="wrapper"  ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
                <div>
                    <div class="user">
                        <div class="headimg">
                            <img v-lazy="members.headimg" :key="members.headimg" alt="">
                        </div>
                        <div class="info">
                            <h4>{{members.nickname}}</h4>
                            <!--<h5>当前等级：<span>{{members.leveltitle}}</span></h5>-->
                            <p>团队总人数：<span>{{HierarchyTeamNums.TotalTeamNum}}人</span></p>
                        </div>
                    </div>
                    <ul class="tabs">
                        <li>
                            <h4>{{HierarchyTeamNums.one}}人</h4>
                            <p>直属</p>
                        </li>
                        <li>
                            <h4>{{HierarchyTeamNums.two}}人</h4>
                            <p>隔代</p>
                        </li>
                    </ul>
                    <div v-if="OneLevelLists.length > 0" class="list">
                        <div @click="getBack(members.is_auth,OneLevelList.id)" class="item" v-for="(OneLevelList,index) in OneLevelLists" :key="index">
                            <div class="left">
                                <div class="image">
                                    <img class="img" v-lazy="OneLevelList.headimg" :key="OneLevelList.headimg" alt=""/>
                                </div>
                                <h4>{{OneLevelList.nickname}}</h4>
                                <!--<p>{{OneLevelList.leveltitle}}</p>-->
                                <p>{{OneLevelList.spreat_at}}</p>
                            </div>
                            <div v-if="members.is_auth == 1" class="right">
                                <div class="right-left">
                                    <h4>{{OneLevelList.levelnum}}人</h4>
                                </div>
                                <div class="right-right">
                                    <i class="icon-right"></i>
                                </div>
                            </div>
                        </div>
                        <div v-if="allLoaded" class="load">- 已加载全部 -</div>
                    </div>
                    <div v-else class="load">- 暂无记录 -</div>
                </div>
            </mt-loadmore>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: 'MyTeam',
        data () {
            return {
                members:{},
                HierarchyTeamNums:{},
                OneLevelLists:[],

                page:1,
                lastPage:'',
                allLoaded: false,
                wrapperHeight: 0,
                autoFill:false,
                bottomPullText:'加载更多',
                topPullText:'刷新',
            }
        },
        created(){
            this.getTeamData();
        },
        methods:{
            getTeamData(){
                let sign = this.getEncrypt('page='+this.page+'&token='+this.getToken())
                this.$http.post('/app.Teams/team.html',{
                    sign: sign,
                    token: this.getToken(),
                    page: this.page,
                }).then( response => {
                    if(response.data.code == 1){
                        this.members = response.data.data.member;
                        this.HierarchyTeamNums = response.data.data.HierarchyTeamNum;
                        if(this.page == 1){
                            this.OneLevelLists = response.data.data.OneLevelList;
                        }else{
                            this.OneLevelLists = this.OneLevelLists.concat(response.data.data.OneLevelList);
                        }
                        this.lastPage = response.data.data.last_page;
                        if(this.page >= this.lastPage){
                            this.allLoaded = true;
                        }else{
                            this.allLoaded = false;
                        }
                    }else{

                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            //上拉加载
            loadBottom() {
                this.page += 1;
                this.getTeamData()
                this.$refs.loadmore.onBottomLoaded();
            },
            //下拉刷新
            loadTop() {
                this.page = 1;
                this.getTeamData()
                this.$refs.loadmore.onTopLoaded();
            },
            getBack(isAuth,id){
                if(isAuth == 1){
                    this.$router.push('/secondteam/'+id+'/1');
                }else{
                    return
                }
            },
        },
        mounted(){
            setTimeout(()=>{
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar){
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - 34;
                    return
                }else{
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
                    return
                }
            },50)
        },
    }
</script>
<style lang="scss" scoped>
    .wrapper{
        overflow:scroll;
        -webkit-overflow-scrolling: touch;
        position: relative;
    }
    .load{
        line-height: 60px;
        text-align: center;
        font-size:26px;
        background: $c8-color;
    }
    .user{
        height: 200px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 0 20px;
        border-bottom: 1px solid $c6-color;/*no*/
        .headimg{
            width:130px;
            height:130px;
            border-radius: 5px;
            overflow: hidden;
            img{
                width: 100%;
            }
        }
        .info{
            flex:1;
            padding: 0 20px;
            h4{
                line-height: 35px;
                font-size: 30px;
                margin-bottom: 50px;
            }
            h5{
                margin-top:25px;
                color: $c4-color;
                font-size: 26px;
                span{
                    color: #000;
                }
            }
            p{
                color: $c4-color;
                margin-top:10px;
                font-size: 26px;
                span{
                    font-size: 30px;
                    color: #000;
                }
            }
        }
    }
    .tabs{
        height:150px;
        border-bottom: 1px solid $c6-color;/*no*/
        display: flex;
        justify-content: center;
        align-items: stretch;
        text-align: center;
        li{
            flex:1;
            border-right:1px solid $c6-color;/*no*/
            &:last-child{
                border: none;
            }
            h4{
                font-size: 40px;
                margin-bottom: 10px;
                margin-top: 38px;
            }
            p{
                font-size: 26px;
                color: $c4-color;
            }
        }
    }
    .list{
        padding-top: 20px;
        background: $c8-color;
        .item{
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 150px;
            background: #fff;
            border-bottom: 1px solid $c6-color;/*no*/
            padding: 0 40px;
            &:first-child{
                border-top: 1px solid $c6-color;/*no*/
            }
            .left{
                position:relative;
                padding-left: 120px;
                min-height: 100px;
                .image{
                    position: absolute;
                    left:0;
                    top:0;
                    z-index: 2;
                    width:100px;
                    height:100px;
                    overflow: hidden;
                    border-radius: 5px;
                }
                h4{
                    font-size: 26px;
                    line-height: 28px;
                    min-height: 28px;
                    margin-top: 4px;
                }
                p{
                    color: $c4-color;
                    font-size: 22px;
                    margin-top:40px;
                }
            }
            .right{
                width: 180px;
                text-align: center;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                .right-left{
                    font-size: 36px;
                }
                .right-right{
                    font-size: 50px;
                    margin-top: 4px;
                }
            }
            .middle{
                font-size: 26px;
            }
        }
    }

</style>



