<template>
    <div class="has-topbar">
    <div class="reset-password bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="getBack"></span>
            <p>{{routerValue | title}}</p>
        </div>
        <div class="list">
            <div class="item" v-if="routerValue == 'phone'">
                <input type="text" v-model.number="replacePhone" placeholder="输入手机号"/>
            </div>
            <div class="item" v-else>
                <p>{{PhoneShow}}</p>
            </div>
            <div class="item">
                <input placeholder="输入验证码" type="text" v-model="userVerify"/>
                <button class="btn" @click="getVerification">{{getCodeName}}</button>
            </div>
            <div class="item" v-if="routerValue == 'pay'">
                <input placeholder="输入身份证" type="text" v-model="identityCard ">
            </div>
            <div class="btn-ground">
                <button class="btn-next" @click="next()">{{routerValue | btnText}}</button>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui'
    export default {
        name: 'resetPassword',
        data () {
            return {
                getCode: false,
                userPhone:'',
                PhoneShow:'',
                userVerify:'',
                getCodeName: '获取验证码',
                time: 60,
                routerValue:'',
                identityCard:'',
                orderId:'',
                replacePhone:'',
            }
        },
        created(){
            this.routerValue = this.$route.params.value;
            if( this.routerValue == 'paySubNew' ){
                this.orderId = this.$route.params.orderId;
            }
            this.getUserInfo()
        },
        methods:{
            //获取用户手机号
            getUserInfo(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.Member/index.html',{
                    sign: sign,
                    token: this.getToken()
                }).then( response => {
                    if(response.data.code == 1){
                        this.userPhone = response.data.data.member.phone;
                        this.PhoneShow = response.data.data.member.phonedeal;
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            getBack(){
                if(this.routerValue == 'paySubNew'){
                    this.$router.push(`/orderDetails/${this.orderId}`);
                }else{
                    this.$router.push(`/Set`);
                }
            },
            //下一步
            next(){
                if(this.userVerify == ''){
                    Toast({
                        message: '请输入验证码！',
                        duration: 2000
                    })
                }else{
                    if(this.routerValue == 'phone'){
                        let sign = this.getEncrypt('phone='+this.replacePhone+'&token='+this.getToken()+'&verify='+this.userVerify)
                        this.$http.post('/app.member/untiedphonecheck.html',{
                            sign: sign,
                            token: this.getToken(),
                            phone: this.replacePhone,
                            verify: this.userVerify,
                        }).then( response => {
                            if(response.data.code == 1){
                                this.goBack(-1);
                            }
                            Toast({
                                message: response.data.msg,
                                duration: 2000
                            })

                        }).catch( error => {
                            console.log(error)
                        })
                        return
                    }else if(this.routerValue == 'login'){
                        let sign = this.getEncrypt('phone='+this.userPhone+'&token='+this.getToken()+'&verify='+this.userVerify)
                        this.$http.post('/app.Member/resetCheckVerify.html',{
                            sign: sign,
                            token: this.getToken(),
                            phone: this.userPhone,
                            verify: this.userVerify,
                        }).then( response => {
                            if(response.data.code == 1){
                                this.$router.push(`/NewPassword/${this.userPhone}/${this.userVerify}`)
                            }else{
                                Toast({
                                    message: response.data.msg,
                                    duration: 2000
                                })
                            }
                        }).catch( error => {
                            console.log(error)
                        })
                        return
                    }else{
                        if(this.routerValue == 'pay'){
                            let sign = this.getEncrypt(
                                'idcard='+this.identityCard+
                                '&phone='+this.userPhone+
                                '&token='+this.getToken()+
                                '&verify='+this.userVerify)
                            this.$http.post('/app.member/resetPayCodeCheckVerify.html',{
                                sign: sign,
                                token: this.getToken(),
                                phone:this.userPhone,
                                verify:this.userVerify,
                                idcard: this.identityCard
                            }).then( response => {
                                if(response.data.code == 1){
                                    this.$router.push({name:'PayNewPassword',params:{codeId:'2',userVerify:this.userVerify,identityCard:this.identityCard}})
                                }else{
                                    Toast({
                                        message: response.data.msg,
                                        duration: 2000
                                    })
                                }
                            }).catch( error => {
                                console.log(error)
                            })
                        }else{
                            let sign = this.getEncrypt('phone='+this.userPhone+'&token='+this.getToken()+'&verify='+this.userVerify)
                            this.$http.post('/app.member/resetPayCodeCheckVerify.html',{
                                sign: sign,
                                token: this.getToken(),
                                phone:this.userPhone,
                                verify:this.userVerify,
                            }).then( response => {
                                if(response.data.code == 1){
                                   if(this.routerValue == 'payNew'){
                                        this.$router.push({name:'PayNewPassword',params:{codeId:'3',userVerify:this.userVerify}})
                                        return
                                    }else{
                                        this.$router.push({name:'PayNewPassword',params:{codeId:'5',userVerify:this.userVerify,orderId:this.orderId}})
                                    }
                                }else{
                                    Toast({
                                        message: response.data.msg,
                                        duration: 2000
                                    })
                                }
                            }).catch( error => {
                                console.log(error)
                            })

                        }
                        return
                    }
                }
            },
            //获取验证码
            getVerification(){
                if(this.routerValue == 'login'){
                    this.getLoginVerification()
                    return;
                }else if(this.routerValue == 'phone'){
                    this.getPhoneVerification()
                    return;
                }else{
                    this.getPayVerification();
                }
            },
            //获取登录密码短信
            getLoginVerification(){
                let sign = this.getEncrypt('phone='+this.userPhone+'&token='+this.getToken())
                this.$http.post('/app.Member/resetVerify.html',{
                    sign: sign,
                    token: this.getToken(),
                    phone:this.userPhone
                }).then( response => {
                    if(response.data.code == 1){
                        this.time = 60;
                        this.timer();
                    }else{
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        });
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            //获取更换手机短信
            getPhoneVerification(){
                let sign = this.getEncrypt('phone='+this.replacePhone+'&token='+this.getToken())
                this.$http.post('/app.member/untiedphonesend.html',{
                    sign: sign,
                    token: this.getToken(),
                    phone:this.replacePhone
                }).then( response => {
                    if(response.data.code == 1){
                        this.time = 60;
                        this.timer();
                        console.log(response.data.data.verify)
                    }else{
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        });
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            //获取支付密码短信
            getPayVerification(){
                let sign = this.getEncrypt('phone='+this.userPhone+'&token='+this.getToken())
                this.$http.post('/app.member/resetPayCodeVerify.html',{
                    sign: sign,
                    token: this.getToken(),
                    phone:this.userPhone
                }).then( response => {
                    if(response.data.code == 1){
                        this.time = 60;
                        this.timer();
                    }else{
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        });
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            //计时
            timer(){
                if (this.time > 1) {
                    this.time--;
                    this.getCodeName = this.time+'s后重新获取';
                    setTimeout(this.timer, 1000);
                }else if(this.time == 1){
                    this.getCodeName = '获取验证码';
                }
            }
        },
        filters:{
            title(value){
                if(value == 'pay'){
                    return '修改支付密码'
                }else if(value == 'login'){
                    return '修改登录密码'
                }else if(value == 'phone'){
                    return '更换手机号'
                }else {
                    return '设置支付密码'
                }
            },
            btnText(value){
                if(value == 'phone'){
                    return '确认更改'
                }else {
                    return '下一步'
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
.list{
    background: $c8-color;
    padding-top: 20px;
    .item{
        height: 120px;
        background: #fff;
        display: flex;
        justify-content:flex-start;
        align-items: stretch;
        border-bottom: 1px solid $c6-color; /*no*/
        position:relative;
        &:first-child{
            border-top: 1px solid $c6-color; /*no*/
        }
        input{
            border:none;
            background: none;
            flex:1;
            font-size:24px;
            padding: 0 40px;
            &::-webkit-input-placeholder{
               color: $c4-color;
            }
            &::-moz-placeholder{
                color: $c4-color;
            }
            &::-ms-input-placeholder{
                color: $c4-color;
            }
        }
        p{
            padding: 0 40px;
            width:100%;
            line-height: 120px;
        }
        .btn{
            position: absolute;
            right:40px;
            top:35px;
            height:50px;
            min-width: 154px;
            background: $main-color;
            border: none;
            border-radius: 5px;
            font-size: 26px;
            color: #fff;
        }
    }
    .btn-ground{
        padding:80px 40px 0;
        background: #fff;
        .btn-next{
            width: 100%;
            height: 80px;
            background: $main-color;
            border: none;
            border-radius: 5px;
            font-size: 30px;
            color: #fff;
            &:disabled{
                background: $c6-color;
            }
        }
    }
}


</style>



