<template>
    <div class="has-topbar">
        <div class="information-index bar_top">
            <div class="nav_bar">
                <router-link to="/Information/1" class="tab_active" >
                    <div><i class="icon-cell-circle"></i><span>细胞圈</span></div>
                </router-link>
                <router-link v-if="userType != 1 && isAuth == 1 && payGoods == 1" to="/PhotoGallery/PhotoProduct/0" class="photo-gallery">
                    <div><i class="icon-picture"></i><span>图片库</span></div>
                </router-link>
            </div>
            <!--细胞圈-->
            <div class="wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
                <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText"
                             :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"
                             :auto-fill="autoFill">
                    <div class="cell_con">
                        <div @click="toRelease()" class="release"><i class="icon-add"></i><span>我要发布动态</span></div>
                        <div class="index-list" v-for="(list,index) in lists" :key="index">
                            <div class="item" @touchstart="showDeleteButton(list.content.replace(/<br\s*\/?>/gi,'\r\n'))"
                                 @touchmove="clearLoop()" @touchend="clearLoop()">
                                <i @click="shieldShow(list.mid,list.id)" class="icon-left"></i>
                                <div class="headimg">
                                    <img class="img" v-lazy="list.headimg" :key="list.headimg"/>
                                </div>
                                <h4>{{list.nickname}}</h4>
                                <p :class="{isOpen: list.is_open != 0}" ref="listP"
                                   v-html="list.content.replace(/\r?\n/g,'<br />')"></p>
                                <div class="btn" v-if="list.is_show"><span
                                        @click.stop="isOpen(index)">{{unfold[list.is_open]}}</span></div>
                                <!--视频-->
                                <div v-if="list.videourl != ''" @click="openVideoPlay(list.videourl,isLogin)" class="file-video">
                                    <img :src="list.images" alt="视频"/>
                                    <span class="icon-play"></span>
                                </div>
                                <!--图片-->
                                <div v-else class="img-group">
                                    <img :class="[{'thumbnail':list.images.length >1},{'sola':list.images.length == 1}]"
                                         @click.stop="openImg(list.images,ind,isLogin)"
                                         v-for="(item,ind) in list.images" :key="item+'-EquaCompression'"
                                         v-lazy="item+'-EquaCompression'"/>
                                </div>
                                <div class="time">{{list.create_at}}</div>
                            </div>
                            <div class="bottom">
                                <div @click.stop="tags(index,list.id)">
                                    <p><i class="icon-like" :class="{'active':list.isLike}"></i>
                                        <span>点赞（{{list.praise_num}}）</span></p>
                                </div>
                                <div @click.stop="comment(list.id)">
                                    <p><i class="icon-comment"></i> <span>评论（{{list.comment_num}}）</span></p>
                                </div>
                            </div>
                        </div>
                        <div class="loadEnd" v-if="allLoaded">-- 已加载全部 --</div>
                    </div>
                </mt-loadmore>
            </div>
            <!--屏蔽选择弹窗-->
            <div v-if="sheetShow" @click="closeComment" class="sheet-box">
                <div class="sheet has-fixed-bottom" ref="commentBox">
                    <div @click="BlackCircle(2)" class="list">屏蔽此条细胞圈</div>
                    <div @click="BlackCircle(1)" class="list">屏蔽此人所有细胞圈</div>
                    <div @click="goBack(`/Report/${CircleId}`)">举报</div>
                    <div class="list-bottom" @click="sheetShow=false">取消</div>
                </div>
            </div>
            <Nav></Nav>
        </div>
    </div>
</template>

<script>
    import Nav from '@/base/nav/nav'
    import {Toast} from 'mint-ui';
    export default {
        components: {
            Nav
        },
        data(){
            return {
                tab_num: 0,
//                tabs: [],
                unfold: [
                    '全文', '收起'
                ],

                lists: [],
                sign: '',
                page: 1,
                lastPage: 1,
                token: '',
                UserId: '',
                CircleId: '',
                userType:'',

                sheetShow: false,
                allLoaded: false,
                wrapperHeight: 0,
                autoFill: false,
                bottomPullText: '加载更多',
                topPullText: '刷新',
                isLogin: '',
                isAuth:'',
                payGoods:'',

                loop: ''
            }
        },
        created(){
            this.token = this.getToken();
        },
        activated(){
            if (this.$route.params.id == 2) {
                this.page = 1;
                this.getData();
            }
            this.token = this.getToken();
            this.userTypeFn();
        },
        methods: {
            // 复制黏贴功能
            showDeleteButton(txt){
                clearTimeout(this.loop)
                this.loop = setTimeout(function () {
                    var clipBoard = api.require("clipBoard");
                    clipBoard.set(
                        {
                            value: txt
                        },
                        function (ret, err) {
                            if (ret) {
                                Toast({
                                    message: '复制成功',
                                    duration: 1000
                                });
                            } else {
                                alert(JSON.stringify(err));
                            }
                        }
                    );
                }.bind(this), 700)
            },
            clearLoop(){
                clearTimeout(this.loop)
            },
            //判断用户类型
            userTypeFn(){
                let sign = this.getEncrypt("token="+this.token);
                this.$http.post("/app.member/userinfo.html",{
                    sign: sign,
                    token: this.token,
                }).then(res => {
                    if(res.data.code == 1){
                        this.isLogin = true;
                        this.userType = res.data.data.type;
                        this.isAuth = res.data.data.is_auth;
                        this.payGoods = res.data.data.pay_goods;
                        this.getData();
                    }else{
                        this.isLogin = false;
                        this.userType = 1;
                        this.isAuth = 0;
                        this.getData();
                    }
                })
            },
            //获取细胞圈数据
            getData(){
                if (this.token) {
                    if (this.page == 1) {
                        this.sign = this.getEncrypt("page=" + this.page + "&token=" + this.token);
                        this.$http.post('/app.bbs/getinfo.html', {
                            sign: this.sign,
                            token: this.token,
                            page: this.page
                        }).then(response => {
                            this.lists = response.data.data.data;
                            this.lastPage = response.data.data.last_page;
                            if (this.page >= this.lastPage) {
                                this.allLoaded = true;
                            }
                            this.isOverFrom();
                        }).catch(error => {

                        })
                    } else {
                        this.sign = this.getEncrypt("page=" + this.page + "&token=" + this.token);
                        this.$http.post('/app.bbs/getinfo.html', {
                            sign: this.sign,
                            token: this.token,
                            page: this.page
                        }).then(response => {
//                        this.lists = response.data.data.data;
                            this.lists = this.lists.concat(response.data.data.data);
                            this.lastPage = response.data.data.last_page;
                            if (this.page >= this.lastPage) {
                                this.allLoaded = true;
                            }
                            this.isOverFrom();
                        }).catch(error => {

                        })
                    }
                } else {
                    if (this.page == 1) {
                        this.sign = this.getEncrypt("page=" + this.page);
                        this.$http.post('/app.bbs/getinfo.html', {
                            sign: this.sign,
                            page: this.page
                        }).then(response => {
                            this.lists = response.data.data.data;
                            this.lastPage = response.data.data.last_page;
                            if (this.page >= this.lastPage) {
                                this.allLoaded = true;
                            }
                            this.isOverFrom();
                        }).catch(error => {

                        });
                    } else {
                        this.sign = this.getEncrypt("page=" + this.page);
                        this.$http.post('/app.bbs/getinfo.html', {
                            sign: this.sign,
                            page: this.page
                        }).then(response => {
                            this.lists = this.lists.concat(response.data.data.data);
                            this.lastPage = response.data.data.last_page;
                            if (this.page >= this.lastPage) {
                                this.allLoaded = true;
                            }
                            this.isOverFrom();
                        }).catch(error => {

                        });
                    };
                }
            },
            //发布细胞圈
            toRelease(){
                if (this.isLogin) {
                    this.$router.push('/Release')
                    return
                }else {
                    this.$router.push('/Login')
                    return
                }
            },
            isOpen(index){
                if (this.lists[index].is_open == 0) {
                    this.lists[index].is_open = 1;
                } else {
                    this.lists[index].is_open = 0;
                }
            },
            //屏蔽显示
            shieldShow(UserId, CircleId){
                if (!this.isLogin) {
                    this.$router.push('/Login')
                }else {
                    this.sheetShow = true;
                    this.UserId = UserId;
                    this.CircleId = CircleId;
                }
            },
            //屏蔽功能
            BlackCircle(type){
                this.sheetShow = false;
                if (type == 1) {
                    let sign = this.getEncrypt("shieldinfo=" + this.UserId + "&token=" + this.token + "&type=" + type);
                    this.$http.post("/app.Shield/shield.html", {
                        sign: sign,
                        token: this.token,
                        type: type,
                        shieldinfo: this.UserId,
                    }).then(res => {
                        Toast({
                            message: res.data.msg,
                            duration: 1500
                        });
                        if (res.data.code == 1) {
                            setTimeout(() => {
                                document.location.reload();
                            }, 1000)
                        }
                    })
                } else if (type == 2) {
                    let sign = this.getEncrypt("shieldinfo=" + this.CircleId + "&token=" + this.token + "&type=" + type);
                    this.$http.post("/app.Shield/shield.html", {
                        sign: sign,
                        token: this.token,
                        type: type,
                        shieldinfo: this.CircleId,
                    }).then(res => {
                        if (res.data.code == 1) {
                            setTimeout(() => {
                                document.location.reload();
                            }, 1000)
                        }
                        Toast({
                            message: res.data.msg,
                            duration: 1500
                        });
                    })
                }

            },
            //点击空白关闭弹框
            closeComment(){
                var sp = this.$refs.commentBox;
                if (sp) {
                    if (!sp.contains(event.target)) { //这句是说如果我们点击到了id为myPanel以外的区域
                        this.sheetShow = false;
                    }
                }
            },
            //上拉加载
            loadBottom() {
                this.page += 1;
                this.getData()
                this.$refs.loadmore.onBottomLoaded();
            },
            //下拉刷新
            loadTop() {
                this.page = 1;
                this.allLoaded = false;
                this.getData()
                this.$refs.loadmore.onTopLoaded();
            },
            //点赞
            tags(index, recordId){
                if (!this.isLogin) {
                    this.$router.push('/Login')
                    return;
                } else {
                    this.sign = this.getEncrypt("record_id=" + recordId + "&token=" + this.token);
                    this.$http.post("/app.bbs/praise.html", {
                        sign: this.sign,
                        record_id: recordId,
                        token: this.token
                    }).then(response => {
                        if (response.data.msg == "点赞成功") {
                            this.lists[index].isLike = true;
                            this.lists[index].praise_num += 1;
                            Toast({
                                message: response.data.msg,
                                iconClass: 'icon-like',
                                duration: 500
                            });
                        } else if (response.data.msg == "取消点赞成功") {
                            this.lists[index].isLike = false;
                            this.lists[index].praise_num -= 1;
                            Toast({
                                message: response.data.msg,
                                duration: 500
                            });
                        }
                    })
                }
            },
            //评论
            comment(cellId){
                if (!this.isLogin) {
                    this.$router.push('/Login');
                    return;
                } else {
                    this.$router.push('/InfComment/' + cellId + '/1')
                }
            },
            //收起判断
            isOverFrom(){
                if (this.lists.length > 0) {
                    setTimeout(() => {
                        if (this.lists != '') {
                            this.$refs.listP.forEach((e, index) => {
                                if (e.scrollHeight > (1.28 * window.rem)) {
                                    this.lists[index].is_show = true;
                                }
                            })
                        }
                    }, 50)
                }

            },
        },
        mounted(){
            setTimeout(() => {
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar) {
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - (1.7 * window.rem) - 34;
                    return
                } else {
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - (1.7 * window.rem);
                    return
                }
            }, 50)
        }
    }
</script>

<style lang="scss" scoped>
    .information-index {
        padding-bottom: 125px;
    }

    .nav_bar {
        a {
            flex: 1;
            text-align: center;
            font-size: 36px;
            padding: 20px;
            position: relative;
            color: $c4-color;
            &.photo-gallery {
                border-left: 1px solid $c6-color; /*no*/
            }
            &.tab_active {
                color: #000;
            }
            >div{
                display: flex;
                justify-content: center;
                align-items: center;
                span {
                    height: 36px;
                    display: inline-block;
                    padding-left: 10px;
                }
                i {
                    font-size: 36px;
                }
            }


        }
    }

    .cell_con {
        background: $c8-color;
        .release {
            height: 100px;
            font-size: 35px;
            display: flex;
            color: $main-color;
            justify-content: center;
            align-items: center;
            background: #fff;
            i {
                font-size: 50px;
            }
        }
    }

    .index-list {
        margin-bottom: 20px;
        border-top: 1px solid $c6-color; /*no*/
        border-bottom: 1px solid $c6-color; /*no*/
        background: #fff;
    }

    .item {
        padding: 20px 20px 20px 130px;
        position: relative;
        .icon-left {
            position: absolute;
            right: 20px;
            top: 15px;
            font-size: 28px;
            transform: rotate(-90deg);
        }
        .headimg {
            width: 90px;
            height: 90px;
            left: 20px;
            top: 20px;
            position: absolute;
            z-index: 88;
            img {
                border-radius: 5px;
            }
        }
        h4 {
            font-size: 26px;
            margin-bottom: 20px;
        }
        p {
            font-size: 24px;
            line-height: 32px;
            text-align: justify;
            padding-right: 28px;
            color: $c4-color;
            max-height: 1.26rem;
            @include ellipsis-3h;
            &.isOpen {
                display: block;
                max-height: none;
            }
        }
        .btn {
            margin-top: 50px;
            font-size: 30px;
        }
        .file-video {
            margin-top: 20px;
            position: relative;
            display: inline-block;
            width: 35%;
            img {
                width: 100%;
            }
            .icon-play {
                position: absolute;
                top: 50%;
                left: 50%;
                margin: -50px 0 0 -50px;
                font-size: 100px;
                color: #fff;
            }
        }
        .img-group {
            margin-top: 20px;
            max-width: 500px;
            .sola {
                width: 350px;
                height: 250px;
                object-fit: cover;
            }
            .thumbnail {
                display: inline-block;
                height: 150px;
                width: 150px;
                margin: 0 10px 10px 0;
                object-fit: cover;
            }
        }
        .time {
            font-size: 22px;
            color: $c4-color;
            margin-top: 5px;
        }
    }

    .bottom {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100px;
        border-top: 1px solid $c6-color; /*no*/
        text-align: center;
        div {
            flex: 1;
            font-size: 26px;
            color: $c4-color;
            /*position:relative;*/
            p {
                display: flex;
                justify-content: center;
                align-items: center;
                span {
                    margin-left: 5px;
                }
                i {
                    font-size: 35px;
                    &.active {
                        color: $main-color;
                    }
                }
            }
        }
    }

    .wrapper {
        overflow: scroll;
        -webkit-overflow-scrolling: touch;
        position: relative;
    }

    .loadEnd {
        color: #999;
        text-align: center;
        font-size: 24px;
        padding-bottom: 20px;
    }

    .sheet-box {
        position: fixed;
        left: 0;
        bottom: 0;
        z-index: 2003;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        .sheet {
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            background: #e0e0e0;
            text-align: center;
            font-size: 26px;
            & > div {
                background: #fff;
                height: 80px;
                line-height: 80px;
                &.list {
                    border-bottom: 1px solid $c6-color; /*no*/
                }
                &.delete {
                    color: #df5000;
                }
                &.list-bottom {
                    margin-top: 20px;
                }
            }
        }
    }
</style>



