<template>
    <div class="has-topbar">
    <div class="information-index bar_top">
        <div class="nav_bar">
            <div class="icon-left fast-click" @click="goBack(-1)"></div>
            <p>我的细胞圈</p>
        </div>
        <!--细胞圈-->
        <div class="wrapper"  ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
            <div class="cell_con">
                <!-- <router-link to="/Release" class="release"><i class="icon-add"></i><span>我要发布动态</span></router-link> -->
                <div class="list" v-for="(list,index) in lists" :key="index">
                    <div class="item">
                        <span @click.stop="deleteShow(list.id)" class="icon-trash"></span>
                        <div class="headimg">
                            <img class="img" v-lazy="list.headimg" :key="list.headimg" />
                        </div>
                        <h4>{{list.nickname}}</h4>
                        <p :class="{isOpen: list.is_open != 0}" ref="listP" v-html="list.content.replace(/\r?\n/g,'<br />')"></p>
                        <div class="btn" v-if="list.is_show"><span  @click.stop="isOpen(index)">{{unfold[list.is_open]}}</span></div>
                        <div v-if="list.videourl != ''" class="file-video">
                            <img :src="list.images" @click="openVideoPlay(list.videourl)" alt="视频"/>
                            <span class="icon-play"></span>
                        </div>
                        <div v-else class="img-group">
                            <img :class="[{'thumbnail':list.images.length >1},{'sola':list.images.length == 1}]"
                                 @click.stop="openImg(list.images,ind,true)" v-for="(item,ind) in list.images" :key="ind" v-lazy="item"/>
                        </div>
                        <div class="time">{{list.create_at}}</div>
                    </div>
                    <div class="bottom">
                        <div @click.stop="tags(index,list.id)">
                            <p><i class="icon-like" :class="{'active':list.isLike}"></i> <span>点赞（{{list.praise_num}}）</span></p>
                        </div>
                        <div @click.stop="comment(list.id)">
                            <p><i class="icon-comment"></i> <span>评论（{{list.comment_num}}）</span></p>
                        </div>
                    </div>
                </div>
                <div class="loadEnd" v-if="allLoaded">-- 已加载全部 --</div>
            </div>
            </mt-loadmore>
            <div class="message-box" v-if="messageBoxShow">
                <div class="confirm-box">
                    <div>
                        <div class="header">
                            <h4>确定删除吗？</h4>
                        </div>
                        <div class="btn-group">
                            <button class="btn" @click.stop="deleteHide">取消</button>
                            <button @click.stop="deleteList" class="btn btn-main">删除</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
import Nav from '@/base/nav/nav'
import { Toast } from 'mint-ui';
export default {

    data(){
        return {
            unfold:[
                '全文','收起'
            ],

            lists:[],
            sign:'',
            page:1,
            lastPage:1,
            token:'',
            deleteId:'',

            messageBoxShow: false,
            allLoaded: false,
            wrapperHeight: 0,
            autoFill:false,
            bottomPullText:'加载更多',
            topPullText:'刷新'
        }
    },
    created(){
        this.token = this.getToken();
        this.sign = this.getEncrypt("is_mine=1&page="+this.page+"&token="+this.token);
        this.$http.post('/app.bbs/getinfo.html',{
            sign: this.sign,
            token: this.token,
            page: this.page,
            is_mine:1,
        }).then( response => {
            this.lists = response.data.data.data;
            this.lastPage = response.data.data.last_page;
            if(this.page >= this.lastPage){
                this.allLoaded = true;
            }
            this.isOverFrom();
        }).catch( error => {

        })
    },
    methods:{
        isOpen(index){
            if(this.lists[index].is_open == 0){
                this.lists[index].is_open = 1;
            }else{
                this.lists[index].is_open = 0;
            }
        },
        //删除细胞圈
        deleteList(){
            let sign = this.getEncrypt("id="+this.deleteId+"&token="+this.token);
            this.$http.post('/app.bbs/delRecord.html',{
                sign: sign,
                id: this.deleteId,
                token: this.token
            }).then( res => {
                if(res.data.code == 1){
                    Toast({
                        message: res.data.msg,
                        duration: 1500
                    });
                    this.deleteId = '';
                    document.location.reload();
                }
                this.messageBoxShow = false;
            }).catch(err => {
                Toast({
                    message: err,
                    duration: 1500
                });
            })
        },
        deleteShow(id){
            this.deleteId = id;
            this.messageBoxShow = true;
        },
        deleteHide(){
            this.deleteId = '';
            this.messageBoxShow = false;
            console.log(this.deleteId)
        },
        //上拉加载
        loadBottom() {
            this.page += 1;
            this.sign = this.getEncrypt("is_mine=1&page="+this.page+"&token="+this.token);
            if(this.page <= this.lastPage) {
                this.$http.post('/app.bbs/getinfo.html',{
                    sign: this.sign,
                    token: this.token,
                    page: this.page,
                    is_mine: 1,
                }).then( response => {
                    this.lists = this.lists.concat(response.data.data.data);
                    this.isOverFrom();
                }).catch( error => {
                })
            }else {
                this.allLoaded = true;
            }
            this.$refs.loadmore.onBottomLoaded();
        },
        //下拉刷新
        loadTop() {
            this.page = 1;
            this.sign = this.getEncrypt("is_mine=1&page="+this.page+"&token="+this.token);
            this.$http.post('/app.bbs/getinfo.html',{
                sign: this.sign,
                token: this.token,
                page: this.page,
                is_mine: 1,
            }).then( response => {
                this.lists = response.data.data.data;
                this.lastPage =  response.data.data.last_page;
                if(this.lastPage > this.page ){
                    this.allLoaded = false;
                }
                this.isOverFrom();
            }).catch( error => {

            })
            this.$refs.loadmore.onTopLoaded();
        },
        //点赞
        tags(index,recordId){
            this.sign = this.getEncrypt("record_id="+recordId+"&token="+this.token);
            this.$http.post("/app.bbs/praise.html",{
                sign: this.sign,
                record_id: recordId,
                token: this.token
            }).then( response => {
                if(response.data.msg == "点赞成功"){
                    this.lists[index].isLike = true;
                    this.lists[index].praise_num += 1;
                    Toast({
                        message: response.data.msg,
                        iconClass: 'icon-like',
                        duration: 1500
                    });
                }else if( response.data.msg == "取消点赞成功"){
                    this.lists[index].isLike = false;
                    this.lists[index].praise_num -= 1;
                    Toast({
                        message: response.data.msg,
                        duration: 1500
                    });
                }
            })

        },
        comment(cellId){
            this.$router.push('/InfComment/'+cellId+'/1')
        },
        isOverFrom(){
            if(this.lists.length > 0){
                setTimeout(()=>{
                    this.$refs.listP.forEach((e,index)=>{
                        if(e.scrollHeight > (1.28*window.rem)){
                            this.lists[index].is_show = true;
                        }
                    })
                },100)
            }

        },
    },
    mounted(){
        setTimeout(()=>{
            let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
            if (hasBottombar){
                this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - 34;
                return
            }else{
                this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
                return
            }
        },50)
    }
}
</script>

<style lang="scss" scoped>
.nav_bar{
    a{
        flex:1;
        text-align: center;
        font-size:36px;
        padding:20px;
        position:relative;
        color: $c4-color;
        &:first-child{
            border-right:1px solid $c6-color;/*no*/
        }
        &.tab_active{
            color: #000;
        }
        span{
            height:36px;
            display: inline-block;
            padding-left:50px;
        }
        i{
            font-size:36px;
            position: absolute;
            left:110px;
            top:22px;
            &.icon-cell-circle{
                left:118px;
                top:20px;
            }
        }

    }
}

.cell_con{
    background: $c8-color;
    .release{
        height:100px;
        font-size:26px;
        display: flex;
        color: $main-color;
        justify-content: center;
        align-items: center;
        background:#fff;
        i{
            font-size:46px;
        }
    }
}
.list{
    margin-bottom:20px;
    border-top:1px solid $c6-color;/*no*/
    border-bottom:1px solid $c6-color;/*no*/
    background :#fff;
}
.item{
    padding:20px 20px 20px 130px;
    position: relative;
    .icon-trash{
        position: absolute;
        right: 20px;
        top: 20px;
        font-size: 30px;
        color: $main-color;
    }
    .headimg{
        width:90px;
        height:90px;
        left:20px;
        top:20px;
        position: absolute;
        z-index: 88;
        img{
            border-radius: 5px;
        }
    }
    h4{
        font-size:26px;
        margin-bottom:20px;
        padding-right: 34px;
    }
    p{
        font-size:24px;
        line-height: 32px;
        text-align: justify;
        color: $c4-color;
        max-height:1.26rem;
        padding-right: 34px;
        @include ellipsis-3h;
        &.isOpen{
            display: block;
            max-height:none;
        }
    }
    .btn{
        margin-top:50px;
        font-size:30px;
    }
    .file-video {
        margin-top: 20px;
        position: relative;
        display: inline-block;
        width: 35%;
        img {
            width: 100%;
        }
        .icon-play {
            position: absolute;
            top: 50%;
            left: 50%;
            margin: -50px 0 0 -50px;
            font-size: 100px;
            color: #fff;
        }
    }
    .img-group{
        margin-top:20px;
        max-width:500px;
        .sola{
            width:350px;
            height:250px;
            object-fit: cover;
        }
        .thumbnail{
            display: inline-block;
            height: 150px;
            width: 150px;
            margin:0 10px 10px 0;
            object-fit: cover;
        }
    }
    .time{
        font-size:22px;
        color: $c4-color;
        margin-top:5px;
    }
}
.bottom{
    display: flex;
    justify-content: center;
    align-items: center;
    height:100px;
    border-top:1px solid $c6-color;/*no*/
    text-align: center;
    div{
        flex:1;
        font-size:26px;
        color: $c4-color;
        /*position:relative;*/
        p{
            display: flex;
            justify-content: center;
            align-items: center;
            span{
                margin-left: 5px;
            }
            i{
                font-size: 35px;
                &.active{
                     color: $main-color;
                }
            }
        }
    }
}
.wrapper{
    overflow:scroll;
    -webkit-overflow-scrolling: touch;
    position: relative;
}
.loadEnd{
    color: #999;
    text-align: center;
    font-size:24px;
    padding:20px;
}
.message-box{
    background: rgba(0,0,0,0.5);
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 2000;
    .confirm-box{
        position: absolute;
        margin-top: -120px;
        top:50%;
        width: 100%;
        box-sizing: border-box;
        padding:0 60px;
        &>div{
              background: #fff;
              border-radius: 10px;
              overflow: hidden;
            .header{
                /*min-height:120px;*/
                padding: 40px;
                box-sizing: border-box;
                h4{
                    font-weight: bold;
                    font-size: 30px;
                    text-align: center;
                }
            }
            .btn-group{
                height:80px;
                box-sizing: border-box;
                border-top: 1px solid $c6-color;/*no*/
                display: flex;
                align-items: stretch;
                .btn{
                    flex: 1;
                    background: #fff;
                    border: none;
                    padding: 0;
                    font-size: 26px;
                }
                .btn-main{
                    color: $main-color;
                    border-left: 1px solid $c6-color;
                }
            }
        }
    }
}

</style>



