// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import VueAxios from 'vue-axios'
import base from '@/common/js/base.js'
import 'lib-flexible'
import md5 from 'js-md5'
import Cache from "@/common/js/cache"
import FastClick from 'fastclick'
import VueLazyload from 'vue-lazyload'

Vue.use( VueAxios,axios )
Vue.prototype.httpUrl = () => {
    // return 'http://app.mysterycell.cn/store';//正式环境
    return 'http://192.168.0.66:803/store';//测试环境
}
// axios.defaults.baseURL = 'http://app.mysterycell.cn/store';//正式环境
axios.defaults.baseURL = 'http://192.168.0.66:803/store';//测试环境
// mint-ui
import 'mint-ui/lib/style.css';
import { Swipe, SwipeItem, Picker, Popup, InfiniteScroll,Spinner,Loadmore } from 'mint-ui';
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.component(Picker.name, Picker);
Vue.component(Popup.name, Popup);
Vue.component(Spinner.name, Spinner );
Vue.component(Loadmore .name, Loadmore  );
Vue.use(InfiniteScroll )
Vue.use(VueLazyload)

import gallery from 'img-vuer'
Vue.use(gallery)

// axios.defaults.headers.common['token'] = "f4c902c9ae5a2a9d8f84868ad064e706"
// axios.defaults.headers.post["Content-type"] = "application/json"

Vue.use(base)
Vue.prototype.$md5 = md5;
Vue.prototype.$cache = Cache;
FastClick.attach(document.body);

// 客服基础地址消息
Vue.prototype.getBaseEchatUrl = () => {
    return 'http://vs.rainbowred.com/visitor/mobile/chat.html?companyId=376&echatTag='+encodeURI('细胞之迷');
};
import '@/common/scss/index.scss'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



