<template>
    <div class="has-topbar">
    <div class="set-password bar_top">
        <!--修改密码选择-->
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack('/set')"></span>
            <p>修改密码</p>
        </div>
        <div class="list">
            <!--<router-link v-if="setPayCode == 0" to="/ResetPassword/payNew" class="item"><span>设置新支付密码</span><i class="icon-right"></i></router-link>-->
            <div @click="getBack(setPayCode)" class="item"><span>{{setPayCode | payText}}</span><i class="icon-right"></i></div>
            <router-link to="/ResetPassword/login" class="item"><span>修改登录密码</span><i class="icon-right"></i></router-link>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        data () {
            return {
                setPayCode:'1'
            }
        },
        created(){
            this.newPasswordShow()
        },
        methods:{
            newPasswordShow(){
                let sign = this.getEncrypt("token="+this.getToken());
                this.$http.post("/app.Member/index.html",{
                    sign: sign,
                    token: this.getToken(),
                }).then(res => {
                    if(res.data.code == 1){
                        this.setPayCode = res.data.data.member.setpayceode
                    }
                }).catch(err => {
                    console.log(err)
                })
            },
            getBack(){
                if(this.setPayCode == 0){
                    this.goBack('/ResetPassword/payNew')
                }else{
                    this.goBack('/PayChoosePassword')
                }
            }
        },
        filters:{
            payText(value){
                if(value == 1){
                    return "修改支付密码"
                }else if(value == 0){
                    return "设置新支付密码"
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
.list{
    background: $c8-color;
    padding-top:20px;
    .item{
        height:100px;
        display: flex;
        background: #fff;
        border-bottom: 1px solid $c6-color;/*no*/
        font-size: 26px;
        position: relative;
        padding:0 40px;
        justify-content: space-between;
        align-items: center;
        &:first-child{
             border-top: 1px solid $c6-color;/*no*/
        }
        i{
            font-size: 30px;
        }
    }

}
</style>



