<template>
    <div class="has-topbar">
    <!--公告详情-->
    <div class="notice-details bar_top">
        <div class="nav_bar">
            <div class="icon-left fast-click" @click="goBack(-1)"></div>
            <p>{{lists.title}}</p>
        </div>
        <div class="con">
            <div class="lists" v-html="lists.content"></div>
            <p class="time">{{lists.create_at}}</p>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: 'NoticeDetails',
        data () {
            return {
                lists:[],
                noticeId:'',
            }
        },
        created(){
            let noticeId = this.$route.params.details;
            // let token = this.getToken();
            let sign = this.getEncrypt("id="+noticeId);
            this.$http.post('/app.Placard/get.html',{
                id:noticeId,
                sign:sign,
                // token: token
            }).then(response => {
                this.lists = response.data.data
            }).catch(error => {
                console.log(error)
            })
        }
    }
</script>
<style lang="scss" scoped>
    .con{
        padding:30px 40px;
        font-size:26px;
        line-height: 34px;
        word-wrap:break-word;
        .time{
            text-align: right;
            padding-top:20px;
            color: $c4-color;
        }
        .lists p{
            margin: 10px 0;
        }
    }
</style>



