<template>
    <div class="has-topbar">
        <div class="service bar_top">
            <div class="nav_bar">
                <span class="icon-left fast-click" @click="backFunc"></span>
                <p>客服中心</p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                headerH:'',
                member:[],
                token:'',
                serviceId:'',
                goodId:'',
                price:'',
                banner:'',
                lists:[],
            }
        },
        created(){
            this.token = this.getToken();
            this.serviceId = this.$route.params.id;
            if(this.serviceId == 2){
                this.getGoodInfo()
            }
            this.getUserInfo();
        },
        mounted(){
            if (  screen.width == 1125 && screen.height === 2436){
                this.headerH = (1.334*window.rem) + 44;
            return;
            }else{
                this.headerH = (1.334*window.rem) + 20;
            }
        },
        methods:{
            //获取用户信息
            getUserInfo(){
                let sign = this.getEncrypt('token='+this.token)
                this.$http.post('/app.Member/index.html',{
                    sign: sign,
                    token: this.token
                }).then( response => {
                    if(response.data.code == 1){
                        this.member = response.data.data.member
                        setTimeout(()=>{
                            this.service();
                        },50)
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            //获取商品信息
            getGoodInfo(){
                this.goodId = this.$route.params.goodId;
                let sign = this.getEncrypt("goods_id="+this.goodId+"&token="+this.token);
                this.$http.post('/app.Goods/getinfo.html',{
                    goods_id: this.goodId,
                    sign: sign,
                    token: this.token,
                }).then(res => {
                    this.lists = res.data.data[0];
                    this.price = this.lists.spec[0].selling_price;
                    this.banner = this.lists.goods_image.split("|");
                })
            },
            //客服
            service(){
                let EchatUrl = '';
                if(this.serviceId == 2){
                    var data = {
                        visEvt:{
                            eventId: this.$route.params.goodId,
                            title: this.lists.goods_title,
                            imageUrl: this.banner[0],
                            content: '售价：' + this.price,
                        }
                    }
                }
                let sign = this.getEncrypt(
                    "address="+ this.member.area+
                    "&city="+ this.member.city+
                    "&gender="+ (this.member.sex === '男' ? 1 : 2)+
                    "&name="+this.member.realname+
                    "&nickName="+ this.member.nickname+
                    "&phone="+ this.member.phone+
                    "&photo="+ this.member.headimg+
                    "&province="+ this.member.province+
                    "&token="+ this.token+
                    "&uid="+ this.member.id
                )
                this.$http.post("/app.Echat/handleEchat.html",{
                    uid: this.member.id,
                    nickName: this.member.nickname,
                    name:this.member.realname,
                    gender: (this.member.sex === '男' ? 1 : 2),
                    phone: this.member.phone,
                    photo: this.member.headimg,
                    province: this.member.province,
                    city: this.member.city,
                    address: this.member.area,
                    token: this.token,
                    sign: sign,
                }).then(res=>{
                    if(res.data.code == 1){
                        if(this.serviceId == 2){
                            EchatUrl = this.getBaseEchatUrl()+"&visEvt="+encodeURI(JSON.stringify(data.visEvt))+ "&metaData=" + res.data.data.Encrypt;
                        }else {
                            EchatUrl = this.getBaseEchatUrl()+ "&metaData=" + res.data.data.Encrypt;
                        }
//                        window.location.href = EchatUrl
                        api.openFrame({
                            name: 'cs',
                            url: EchatUrl,
                            rect: {
                                x: 0,
                                y: this.headerH,
                                w: 'auto',
                                h: 'auto'
                            },
                            bounces: false,
                            reload: true,
                            bgColor: '#fff'
                        });
                    }
                })
            },
            //关闭客服
            backFunc(){
                this.goBack(-1)
                api.closeFrame({
                    name: 'cs'
                });
            },
        }
    }
</script>
<style lang="scss" scoped>

</style>



