<template>
<div class="tourism-welfare">
    <div class="has-topbar">
    <div class="message-lists bar_top">
        <div class="nav_bar">
            <span class="icon-left" @click="goBack('/MyTourism')"></span>
            <p>车辆奖励</p>
        </div>

        <div class="con">
             <div class="totalbonus teamAwardWrap">
                <ul class="teamAward">
                    <li>
                        <p>{{teamAwards.content}}</p>
                        <!-- <p>我的团队直属总经理人数：{{directmanager}}</p> -->
                        <img v-lazy="teamAwards.imgs" :key="teamAwards.imgs" alt="">
                        <h3>{{teamAwards.title}}</h3>
                        <!-- <p>已领取数量：{{receivenum}}</p> -->
                        <div @click="toReceive(teamAwards.id)" class="btn-group" v-if="isavailable==1">
                            <button class="btn">{{isavailable | myfilters}}</button>
                        </div>
                        <div class="btn-group is-btn-group" v-else>
                            <button class="btn">{{isavailable | myfilters}}</button>
                        </div>
                    </li>
                </ul>
            </div>
        </div>

        <transition name="fade">
            <div v-show="Pop">
                <div class="pop-bg"  @click="Pop=false"></div>
                <div class="pop-wrap">
                    <div class="pop">
                        <h3>{{rule.title}}</h3>
                        <p>{{rule.content}}</p>
                        <div class="pop-btn-wrap">
                            <button @click="receive()">领取</button>
                            <button class="pop-btn" @click="Pop=false">关闭</button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
    </div>
</div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return{
            teamAwards:{},
            directmanager:'',
            receivenum:'',
            isavailable:'',
            receiveId:'',
            Pop:false,
            rule:{},
        }
    },
    created(){
        this.getTeamAward()
    },
    methods:{
        getTeamAward(){
            let sign = this.getEncrypt('token='+this.getToken())
            this.$http.post('/app.Wallet/teamAward.html',{
                sign: sign,
                token: this.getToken()
            }).then( response => {
                if(response.data.code == 1){
                    // this.directmanager = response.data.data.directmanager
                    this.teamAwards = response.data.data.prize
                    this.receivenum = response.data.data.receivenum
                    this.rule = response.data.data.rule
                    this.isavailable = response.data.data.isavailable
                }
            }).catch( error => {
                console.log(error)
            })
        },
        toReceive(id){
            this.Pop = true
            this.receiveId = id
        },
        receive(){
            let sign = this.getEncrypt('goods='+this.receiveId+'&token='+this.getToken())
            this.Pop = false;
            this.$http.post('/app.Wallet/teamAwardReceive.html',{
                sign: sign,
                token: this.getToken(),
                goods: this.receiveId
            }).then( response => {
                if(response.data.code == 1){
                    setTimeout(()=>{
                        document.location.reload();
                    },1000)
                }
                Toast({
                    message: response.data.msg,
                    duration: 1500
                });
            }).catch( error => {
                console.log(error)
            })
        }
    },
    filters:{
        myfilters(value){
            if(value == 1){
                return '领取奖励'
            }else if(value == 2){
                return '已领取'
            }else{
                return '暂无领取资格'
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.menu{
    border-top: 1px solid $c6-color;/*no*/
    .list{
        height:100px;
        display: flex;
        padding:0 40px;
        line-height: 100px;
        border-bottom: 1px solid $c6-color;/*no*/
        font-size: 26px;
        align-items: center;
        p{
            flex: 1;
            padding-left: 20px;
        }
        i{
            font-size:40px;
        }
    }
}
.totalbonus{
    width: 100%;
    // height: 350px;
    text-align: center;
    border-bottom: 1px solid $c6-color;/*no*/
    padding-top: 58px;
    box-sizing: border-box;
    h3{
        font-size: 50px;
        margin-bottom: 58px;
    }
    p{
        font-size: 58px;
        margin-top: 80px;
        margin-bottom: 20px;
    }
    span{
        font-size: 36px;
        color: $c4-color;
    }
}
.teamAwardWrap{
    border: 0;
    .teamAward{
        li{
            width: 100%;
            // height: 600px;
            text-align: center;
            // border-bottom: 1px solid $c6-color;
            img{
                width: 500px
            }
            h3{
                font-size: 30px;
                margin-bottom: 20px;
            }
            p{
                font-size: 30px;
                color: $c4-color;
            }
            .btn-group{
                width: 660px;
                height: 80px;
                margin: 0 auto;
                margin-top: 30px;
                margin-bottom: 30px;
                background-color: $main-color;
                border-radius: 5px;
                .btn{
                    height: 80px;
                    line-height: 80px;
                    color: #fff;
                    font-size: 30px;
                    outline: none;
                    background: transparent;
                    border: 0;
                }
            }
            .is-btn-group{
                background-color:$c4-color
            }
        }
    }
}
.pop-wrap{
    width: 550px;
    height: 550px;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -275px;
    margin-top: -275px;
    z-index: 9999;
    .pop{
        width: 450px;
        margin: 0 auto;
        margin-top: 60px;
        h3{
            font-size: 36px;
            margin-bottom: 50px;
            text-align: center
        }
        P{
            font-size: 26px;
            margin-bottom: 60px;
            height: 240px;
        }
        button{
            width: 170px;
            height: 56px;
            background-color: $main-color;
            color: #fff;
            margin-right: 70px;
            margin-left: 16px;
            border: 1px solid $main-color;/*no*/
            border-radius: 5px;
        }
        .pop-btn{
            width: 168px;
            height: 54px;
            background-color: #fff;
            color: $main-color;
            margin: 0;
        }
    }
}
.pop-bg{
    position:fixed;
    top:0;
    bottom:0;
    width:100%;
    background-color:$color-background-d;
    z-index: 999;
}
</style>



