export default{
    prefix:"xbzm-",
    set(key,value){
        if (!key) return;
        localStorage.setItem(this.prefix + key, JSON.stringify(value));
    },
    get(key){
        if (!localStorage.hasOwnProperty(this.prefix+key)) {
            return false
        }else{
            return JSON.parse(localStorage.getItem(this.prefix + key));
        }
    },
    delete(key){
        localStorage.removeItem(this.prefix + key);
    },
    clear(){
        localStorage.clear()
    }
}



















