<template>
    <div class="take-order" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
     <!--待收货-->
         <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
         <div class="menu">
            <div class="list" v-for="(list,index) in lists" :key="index">
                <div class="header">
                    <span>订单编号：{{list.order_no}}</span>
                    <span>待收货</span>
                </div>
                <div class="item" v-for="(item,ind) in list.goods" :key="ind" @click="toDetails(list.id)">
                    <div class="img-group">
                        <img v-lazy="item.goods_image" :key="item.goods_image"  class="img"/>
                    </div>
                    <div class="info">
                        <h4>{{item.goods_title}}</h4>
                        <h5>{{item.goods_spec}}</h5>
                        <p><span class="price">￥{{item.selling_price}}</span> <span>数量：{{item.number}}</span></p>
                    </div>
                </div>
                <div class="bottom border">
                    <p>共<i>{{list.goods_number_all}}</i>件商品，<span>合计<i>￥{{list.real_price}}</i></span></p>
                </div>
                <div class="bottom">
                    <button class="btn" @click="tracking(list.id)">查看物流</button>
                    <button class="btn" @click="notarize(list.order_no,index)">确认收货</button>
                </div>
            </div>
         </div>
         </mt-loadmore>
        <div v-if="lists == ''" class="unList">
            <h4>您还没有相关订单</h4>
            <p>可以去看看有哪些想买的</p>
        </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return{
            sign:'',
            token:'',
            page:1,
            lists:[],
            lastPage:'',

            allLoaded: false,
            wrapperHeight: 0,
            autoFill:false,
            bottomPullText:'加载更多',
            topPullText:'刷新',
        }
    },
    created(){
        this.token = this.getToken();
        this.requestFn();
    },
    methods:{
        //查看物流
        tracking(orderId){
            this.$router.push({name:'OrderTracking',params:{orderId:orderId}})
        },
        //确认收货
        notarize(orderNo,index){
            let sign = this.getEncrypt("order_no="+orderNo+"&token="+this.token)
            this.$http.post("/app.Order/confirm.html",{
                sign: sign,
                order_no: orderNo,
                token: this.token
            }).then( response => {
                if(response.data.code == 1){
                    if(response.data.msg == '确认收货成功！'){
                        this.lists.splice(index,1);
                    }
                    Toast({
                        message: response.data.msg,
                        duration: 1000
                    });
                }
            })
        },
        //请求
        requestFn(){
            this.sign = this.getEncrypt('page='+this.page+'&status=3&token='+this.token);
            this.$http.post('/app.Order/get.html',{
                sign: this.sign,
                page: this.page,
                status: 3,
                token: this.token
            }).then( res => {
                let message = res.data.data;
                if( this.page == 1){
                    this.lists = message.data;
                    this.lastPage = message.last_page;
                }else{
                    this.lists = this.lists.concat(message.data);
                }
                if(this.page >= this.lastPage){
                    this.allLoaded = true;
                }
            })
        },
        //上拉加载更多
        loadBottom(){
            this.page += 1;
            this.requestFn()
            this.$refs.loadmore.onBottomLoaded();
        },
        //下拉刷新
        loadTop(){
            this.page = 1;
            this.allLoaded = false;
            this.requestFn();
            this.$refs.loadmore.onTopLoaded();
        },
        toDetails(value){
            this.$router.push({
                path: `/orderDetails/${value}`
            })
        }
    },
    mounted(){
        setTimeout(()=>{
            let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
            if (hasBottombar){
                this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top  - 34;
                return
            }else{
                this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top ;
                return
            }
        },50)
    },
}
</script>
<style lang="scss" scoped>
.take-order{
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    position: relative;
}
.menu{
    background: #f5f5f5;
    padding-top: 20px;
    .list{
        margin-bottom: 20px;
        background: #fff;
        .header{
            height: 80px;
            display: flex;
            font-size: 26px;
            justify-content: space-between;
            align-items: center;
            padding: 0 40px;
            border-bottom: 1px solid $c6-color;/*no*/
        }
        .item{
            display: flex;
            justify-content: flex-start;
            padding: 20px 40px;
            border-bottom: 1px solid $c6-color;/*no*/
            .img-group{
                width: 150px;
                height: 150px;
                margin-right: 20px;
            }
            .info{
                flex: 1;
                position: relative;
                h4{
                    font-size: 26px;
                    margin-bottom: 10px;
                }
                h5{
                    font-size: 22px;
                    color: $c4-color;
                }
                p{
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width:100%;
                    display: flex;
                    justify-content: space-between;
                    font-size: 24px;
                    .price{
                        font-size: 30px;
                    }
                }
            }
        }
        .bottom{
            display: flex;
            justify-content: flex-end;
            align-items: center;
            height: 80px;
            padding: 0px 40px;
            &.border{
                 border-bottom: 1px solid $c6-color;/*no*/
             }
            p{
                font-size: 24px;
                i{
                    color: $main-color;
                }
                span{
                    font-size: 30px;
                }
            }
            .btn{
                height: 50px;
                background: $main-color;
                border-radius: 5px;
                border: 0;
                color: #fff;
                font-size: 26px;
                margin-left: 20px;
            }
        }
    }
}
.unList{
    padding-top: 100px;
    text-align: center;
    h4{
        font-size: 36px;
        padding-bottom: 30px;
    }
    p{
        font-size: 24px;
        color: $c4-color;
    }
}
</style>



