<template>
    <div class="has-topbar">
        <div class="wallet-pay bar_top">
            <div class="nav_bar">
                <div @click="getBack" class="icon-left fast-click"></div>
                <p>积分充值</p>
            </div>
            <div class="list">
                <div class="item">
                    <div class="name">银行：</div>
                    <input type="text" v-model="backName"/>
                </div>
                <div class="item">
                    <div class="name">账号名：</div>
                    <input type="text" v-model="backUser"/>
                </div>
                <div class="item">
                    <div class="name">卡号：</div>
                    <input type="text" v-model="backCard"/>
                </div>
                <div class="item">
                    <div class="name">金额：</div>
                    <input type="text" v-model="money"/>
                </div>
                <div class="notes-group">
                    <div class="notes">备注：</div>
                    <textarea name="" rows="3" v-model="notes"></textarea>
                </div>
            </div>
            <div class="pay-bill">
                <h4>支付流水截图</h4>
                <!--<div class="img-group"  v-if="imgList != ''">-->
                    <!--<img v-gallery class="img" :src="list" alt=""/>-->
                    <!--<span class="icon-cross" @click="deleteImg(index)"></span>-->
                <!--</div>-->
                <!--<div class="upload" @click="readFile">-->
                    <!--<i>+</i>-->
                    <!--&lt;!&ndash;<input type="file" @change="readFile" accept="image/*" >&ndash;&gt;-->
                <!--</div>-->
                <div class="img-item">
                    <div class="img-group" v-for="(list,index) in imgList" :key="index">
                        <img class="thumbnail" v-gallery v-lazy="list" :key="list"/>
                        <span class="icon-cross" @click="deleteImg(index)"></span>
                    </div>
                    <div v-if="imgList.length < 9" class="upload" @click="readFile">
                        <i>+</i>
                    </div>
                </div>
                <div>暂不支持线上支付，请用银行转账。</div>
                <button @click="rechargeSave" class="btn">充 值</button>
            </div>
        </div>
    </div>
</template>

<script>
    import {Toast} from 'mint-ui';
    import { Indicator } from 'mint-ui';
    export default {
        data () {
            return {
                backName:'',
                backUser:'',
                backCard:'',
                money:'',
                notes:'',
                imgList:[],
                payId:'',
                photoNum:9,
                files:'',
            }
        },
        created(){
            this.payId = this.$route.params.id;
            console.log(this.payId)
        },
        methods:{
            getBack(){
                Indicator.close();
                if(this.payId == 1){
                    this.goBack(-1)
                }else{
                    this.goBack("/login")
                }
            },
            rechargeSave(){
                if(this.backName == ''){
                    Toast({
                        message: '请输入银行',
                        duration: 1500
                    });
                    return
                }else if(this.backUser == ''){
                    Toast({
                        message: '请输入账号名',
                        duration: 1500
                    });
                    return
                }else if(this.backCard == ''){
                    Toast({
                        message: '请输入卡号',
                        duration: 1500
                    });
                    return
                }else if(this.money == ''){
                    Toast({
                        message: '请输入金额',
                        duration: 1500
                    });
                    return
                }else if(this.imgList == ''){
                    Toast({
                        message: '请上传支付流水截图',
                        duration: 1500
                    });
                    return
                }else{
                    this.files = this.imgList.join("|||");
                    Indicator.open({
                        text: '充值中...',
                        spinnerType: 'fading-circle'
                    });
                    let sign = this.getEncrypt("account="+this.backUser+
                        "&bank="+this.backName+
                        "&bankcard="+this.backCard+
                        "&images="+ this.files+
                        "&money="+this.money+
                        "&remarks="+this.notes+
                        "&token="+this.getToken())
                    this.$http.post("/app.integral/rechargeSave.html",{
                        sign: sign,
                        token: this.getToken(),
                        bank: this.backName,
                        account: this.backUser,
                        bankcard: this.backCard,
                        money: this.money,
                        remarks: this.notes,
                        images:  this.files,
                    }).then(res => {
                        if(res.data.code == 1){
                            if(this.payId == 1){
                                Indicator.close();
                                this.goBack(-1)
                            }else{
                                this.goBack("/Index")
                            }
                        }
                        Toast({
                            message: res.data.msg,
                            duration: 2000
                        });
                    })
                }
            },
            deleteImg(index){
                this.imgList.splice(index,1)
            },
//            readFile(e){
//                let _this = this;
//                var file=e.target.files || e.dataTransfer.files;
//                if(_this.imgList.length > 1-file.length){
//                    Toast({
//                        message: "只能添加1张图片",
//                        duration: 1000
//                    });
//                    return;
//                }
//                var reader=new FileReader();
//                reader.readAsDataURL(file[0]);
//                reader.onload=function(){
//                    _this.imgList = this.result
//                }
//            },
            //选择照片
            readFile(){
                let _this = this;
                this.sheetShow = false;
                //base64转码函数
                this.photoNum -= this.imgList.length;
                function convertImgToBase64(url, callback, outputFormat){
                    var canvas = document.createElement('canvas'),
                        ctx = canvas.getContext('2d'),
                        img = new Image;
                    img.crossOrigin = 'Anonymous';//它开启了本地的跨域允许。当然服务器存储那边也要开放相应的权限才行，如果是设置了防盗链的图片在服务端就没有相应的权限的话你本地端开启了权限也是没有用的
                    img.onload = function(){
                        canvas.width = 800;
                        canvas.height = img.height/(img.width/800);
                        ctx.drawImage(img,0,0,800,img.height/(img.width/800));
                        var dataURL = canvas.toDataURL('image/jpg',0.1);//没权限的跨域图片在使用canvas.toDataURL()数据导出时会报错
                        callback.call(this, dataURL);
                        canvas = null;
                    };
                    img.src = url;
                };
                var UIMediaScanner = api.require('UIMediaScanner');
                UIMediaScanner.open({
                    type: 'picture',
                    column: 4,
                    max: this.photoNum,
                    texts:{
                        selectedMaxText:'最多选择9张照片！'
                    },
                    exchange: true,
                    rotation: false
                }, function(ret) {
                    if (ret) {
                        if(ret.list){
                            if(ret.list.length > 0){
                                for (var i=0; i<ret.list.length; i++){
                                    convertImgToBase64(ret.list[i].path,function(base64Img){
                                        _this.imgList.push(base64Img)
                                    })
                                }
                            }
                        }
                    }
                });
            },
        }
    }
</script>
<style lang="scss" scoped>
    .list{
        .item{
            min-height: 100px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding: 0 40px;
            border-bottom: 1px solid $c6-color;
            font-size: 24px;
            input{
                flex: 1;
                font-size: 24px;
                height: 80px;
                border: 0;
            }
        }
        .notes-group{
            padding: 0 40px;
            border-bottom: 1px solid $c6-color;
            font-size: 24px;
            display: flex;
            textarea{
                margin: 30px 0px;
                padding:0;
                flex: 1;
                resize:  none;
                border: 0;
            }
            .notes{
                padding-top: 30px;
                color: $c4-color;
            }
        }
    }
    .img-item{
        /*padding-left:60px;*/
        overflow: hidden;
        .img-group{
            float: left;
            position: relative;
            span{
                position: absolute;
                right:10px;
                top:0px;
                color: #fff;
                background: rgba(0,0,0,0.3);
                font-size:30px;
                padding:5px;
            }
        }
    }

    .pay-bill{
        padding: 30px 40px 0;
        h4{
            font-size: 24px;
            margin-bottom: 10px;
        }
        p{
            margin-top: 30px;
            color: $c4-color;
        }
        .btn{
            width: 100%;
            height: 80px;
            background: $main-color;
            border:none;
            color: #fff;
            font-size: 30px;
            margin-top: 80px;
        }
        /*.img-group{*/
            /*width: 200px;*/
            /*height: 200px;*/
            /*position: relative;*/
            /*span{*/
                /*position: absolute;*/
                /*right:0px;*/
                /*top:0px;*/
                /*color: #fff;*/
                /*background: rgba(0,0,0,0.3);*/
                /*font-size:30px;*/
                /*padding:5px;*/
            /*}*/
            /*img{*/
                /*width: 200px;*/
                /*height: 200px;*/
                /*display: inline-block;*/
                /*margin:0 10px 10px 0;*/
                /*object-fit: cover;*/
            /*}*/

        /*}*/
    }
    .upload {
        height: 198px;
        width: 198px;
        line-height: 185px;
        position: relative;
        margin:0 10px 10px 0;
        cursor: pointer;
        color: #888;
        background: #e6e6e6;
        overflow: hidden;
        float: left;
        text-align: center;
        i{
            font-size:100px;
        }
        input {
            position: absolute;
            /*font-size: 100px;*/
            display: inline-block;
            width:198px;
            height:198px;
            right: 0;
            top: 0;
            opacity: 0;
            filter: alpha(opacity=0);
            cursor: pointer
        }
    }
    .thumbnail{
        display: inline-block;
        height: 198px;
        width: 198px;
        margin:0 10px 10px 0;
        object-fit: cover;
    }


</style>



