<template>
    <div class="has-topbar">
    <div class="set-detail bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p v-html="isTrue?'新增收货地址':'修改收货地址'"></p>
        </div>
        <div class="list">
            <div class="item">
                <label for="name">收货人：</label>
                <input id="name" type="text" v-model="userName"/>
            </div>
            <div class="item">
                <label for="phone">手机号：</label>
                <input id="phone" type="text" v-model="userPhone" />
            </div>
            <div class="item" @click="showPopup">
                <span class="site">{{myAddressProvince}} <i>&#9660;</i></span>
                <span class="site">{{myAddressCity}} <i>&#9660;</i></span>
                <span class="site">{{myAddresscounty}} <i>&#9660;</i></span>
            </div>
            <div class="item site-details">
                <textarea placeholder="请详细填写地址" rows="4" v-model="address"></textarea>
            </div>
            <div class="item">
                <span>默认地址</span>
                <div class="switch-group">
                    <div :class="['switch_btn',{ switch_on:is_default}]" @click="setDefault()"></div>
                </div>
            </div>
            <div class="btn-group">
                <div class="btn" @click="userAddress">保 存</div>
            </div>
        </div>
        <mt-popup v-model="popupVisible" position="bottom">
            <mt-picker :slots="mySlots" :show-toolbar="true"  ref="picker" @change="onMyAddressChange"><span @click="handleConfirm()" class="btn">确定</span></mt-picker>
        </mt-popup>
    </div>
    </div>
</template>

<script>
    import myaddress from '@/common/js/city.json';
    import {Toast} from 'mint-ui';
    export default {
        name: 'setDetail',
        data () {
            return {
                isTrue: 0,
                is_default:0,
                myAddressProvince:'省',
                myAddressCity:'市',
                myAddresscounty:'区/县',
                popupVisible:false,
                mySlots:[
                    {
                        flex: 1,
                        defaultIndex: 1,
                        values:  Object.keys(myaddress),    //省份数组
                        className: 'slot1',
                        textAlign: 'center'
                    }, {
                        flex: 1,
                        values:[],
                        className: 'slot2',
                        textAlign: 'center'
                    },
                    {
                        flex: 1,
                        values: [],
                        className: 'slot3',
                        textAlign: 'center'
                    }
                ],
                userName: '',
                userPhone: '',
                address: '',
                address_id:''
            }
        },
        created(){
            let setDetailId = this.$route.params.id;
            if( setDetailId == 0 ){
                this.isTrue = true;
            }else {
                this.isTrue = false;
            }

            this.userModify()
        },
        mounted(){
            this.$nextTick(() => {
                this.mySlots[0].defaultIndex = 0
            })
        },
        methods:{
            setDefault(){
                if(this.is_default){
                    this.is_default = 0
                }else{
                    this.is_default = 1
                }
                
            },
            // 修改地址时获取数据
            userModify(){
                if(this.isTrue == false){
                    let sign = this.getEncrypt('token='+this.getToken())
                    this.$http.post('/app.Address/get.html',{
                        sign: sign,
                        token: this.getToken()
                    }).then( response => {
                        if(response.data.code == 1){
                            this.userName = response.data.data[this.$route.params.address_id].username
                            this.userPhone = response.data.data[this.$route.params.address_id].phone
                            this.address = response.data.data[this.$route.params.address_id].address
                            this.myAddressProvince = response.data.data[this.$route.params.address_id].province
                            this.myAddressCity = response.data.data[this.$route.params.address_id].city
                            this.myAddresscounty = response.data.data[this.$route.params.address_id].area
                            this.is_default = response.data.data[this.$route.params.address_id].is_default
                            this.address_id = response.data.data[this.$route.params.address_id].address_id
                        }
                    }).catch( error => {
                        console.log(error)
                    })
                }
            },
            userAddress(){
                let reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
                if(this.userName == '') {
                    Toast({
                        message: '请输入收货人',
                        duration: 1500
                    });
                    return;
                }else if(this.userPhone == ''){
                    Toast({
                        message: '请输入手机号码',
                        duration: 1500
                    });
                    return;
                }else if(!reg.test(this.userPhone)){
                    Toast({
                        message: '请输入正确的手机号码',
                        duration: 1500
                    });
                    return;
                }else if(this.myAddressProvince == '省'){
                    Toast({
                        message: '请选择您收货地址的城市',
                        duration: 1500
                    });
                    return;
                }else if(this.address == ''){
                    Toast({
                        message: '请输入详情地址',
                        duration: 1500
                    });
                    return;
                }else{
                    if(this.isTrue){
                        // 新增地址
                        let sign = this.getEncrypt('address='+this.address+'&area='+this.myAddresscounty+'&city='+this.myAddressCity+'&is_default='+this.is_default+'&name='+this.userName+'&phone='+this.userPhone+'&province='+this.myAddressProvince+'&token='+this.getToken())
                        this.$http.post('/app.Address/set.html',{
                            sign: sign,
                            token: this.getToken(),
                            name: this.userName,
                            phone: this.userPhone,
                            province: this.myAddressProvince,
                            city: this.myAddressCity,
                            area: this.myAddresscounty,
                            address:this.address,
                            is_default:this.is_default
                        }).then( response => {
                            if(response.data.code == 1){
                                this.goBack(-1)
                            }
                            Toast({
                                message: response.data.msg,
                                duration: 1500
                            });
                        }).catch( error => {
                            console.log(error)
                        })
                    }else{
                        // 修改地址
                        let sign = this.getEncrypt('address='+this.address+'&address_id='+this.address_id+'&area='+this.myAddresscounty+'&city='+this.myAddressCity+'&is_default='+this.is_default+'&name='+this.userName+'&phone='+this.userPhone+'&province='+this.myAddressProvince+'&token='+this.getToken())
                        this.$http.post('/app.Address/set.html',{
                            sign: sign,
                            token: this.getToken(),
                            name: this.userName,
                            phone: this.userPhone,
                            province: this.myAddressProvince,
                            city: this.myAddressCity,
                            area: this.myAddresscounty,
                            address:this.address,
                            is_default:this.is_default,
                            address_id:this.address_id
                        }).then( response => {
                            if(response.data.code == 1){
                                this.goBack(-1)
                            }
                            Toast({
                                message: response.data.msg,
                                duration: 1500
                            });
                        }).catch( error => {
                            console.log(error)
                        })
                    }
                }
            },
            onMyAddressChange(picker, values) {
                if(myaddress[values[0]]){
                    picker.setSlotValues(1,Object.keys(myaddress[values[0]]));
                    picker.setSlotValues(2,myaddress[values[0]][values[1]]);
                }
            },
            showPopup(){
                this.popupVisible = true;
            },
            handleConfirm(){
                this.myAddressProvince = this.$refs.picker.getValues()[0];
                this.myAddressCity = this.$refs.picker.getValues()[1];
                this.myAddresscounty = this.$refs.picker.getValues()[2];
                this.popupVisible = false;
            },
        }
    }
</script>
<style lang="scss" scoped>
.list{
    background: $c8-color;
    padding-top: 20px;
    font-size: 26px;
    .item{
        display: flex;
        min-height:100px;
        background: #fff;
        border-bottom: 1px solid $c6-color;/*no*/
        padding:0 40px;
        justify-content: flex-start;
        align-items: center;
        position: relative;
        &:first-child{
             border-top: 1px solid $c6-color;/*no*/
         }
        label{
            line-height: 100px;
            min-width:100px;
        }
        input[type=text]{
            flex:1;
            border:0;
            height:80px;
        }
        .site{
            margin-right: 60px;
            color: $main-color;
        }
        &.site-details{
            height:auto;
            padding: 0;
            textarea{
                padding: 20px 40px;
                width:100%;
                resize: none;
                border:none;
                &::-webkit-input-placeholder{
                     color: $c4-color;
                     font-size:22px;
                 }
                &::-moz-placeholder{
                     color: $c4-color;
                     font-size:22px;
                 }
                &::-ms-input-placeholder{
                     color: $c4-color;
                     font-size:22px;
                 }
            }
        }
        .switch-group{
            position: absolute;
            left:180px;
            top:20px;
        }
    }
    .btn-group{
        background: #fff;
        padding: 80px 40px 0;
        .btn{
            line-height:80px;
            text-align: center;
            color: #fff;
            background: $main-color;
            border-radius: 5px;
            font-size: 30px;
        }
    }
}
</style>



