<template>
    <div class="photo-product" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <!--产品图-->
        <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
            <div class="list">
                <div class="list-header">已屏蔽细胞圈</div>
                <div class="item" @click="sheetFn(OneLevelList.id,index)" v-for="(OneLevelList,index) in lists" :key="index" >
                    <div class="left">
                        <div class="image">
                            <img class="img" v-lazy="OneLevelList.headimg" :key="OneLevelList.headimg" alt=""/>
                        </div>
                        <h4>{{OneLevelList.nickname}}</h4>
                        <p>{{OneLevelList.content}}</p>
                    </div>
                </div>
                <div class="loadEnd" v-if="allLoaded">- 已加载全部 -</div>
            </div>
        </mt-loadmore>
        <!--接触屏蔽-->
        <div v-if="sheetShow" @click="closeComment" class="sheet-box">
            <div class="sheet has-fixed-bottom" ref="commentBox">
                <div @click="relieve">解除屏蔽</div>
                <div class="list-bottom" @click="sheetShow=false">取消</div>
            </div>
        </div>
    </div>

</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        data () {
            return {
                sign:'',
                page:1,
                lists:[],
                token:'',
                blackId:'',
                listsIndex:[],
                wrapperHeight:'',

                sheetShow: false,
                lastPage:'',
                allLoaded: false,
                autoFill:false,
                bottomPullText:'加载更多',
                topPullText:'刷新'
            }
        },
        created(){
            this.token = this.getToken()
            this.request();
        },
        mounted(){
            setTimeout(()=>{
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar){
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - 34;
                    return
                }else{
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
                    return
                }
            },50)
        },
        methods:{
            request(){
                this.sign = this.getEncrypt("page="+this.page+"&token="+this.token+"&type=2")
                this.$http.post('/app.shield/myshield.html',{
                    sign:this.sign,
                    page:this.page,
                    token: this.token,
                    type: 2,
                }).then( response => {
                    if(this.page == 1){
                        this.lists = response.data.data.data;
                        this.lastPage = response.data.data.last_page;
                        if(this.page >= this.lastPage){
                            this.allLoaded = true;
                        }
                    }else{
                        this.lists = this.lists.concat(response.data.data.data);
                    }
                })
            },
            sheetFn(id,index){
                this.sheetShow = true;
                this.blackId = id;
                this.listsIndex = index;
            },
            relieve(){
                this.sheetShow = false;
                let sign = this.getEncrypt("id="+this.blackId+"&token="+this.getToken());
                this.$http.post("/app.shield/cancelshield.html",{
                    sign: sign,
                    token: this.getToken(),
                    id: this.blackId
                }).then(res => {
                    if(res.data.code == 1){
                        this.lists.splice(this.listsIndex,1)
                    }
                    Toast({
                        message: res.data.msg,
                        duration: 1500
                    });
                })
            },
            //点击空白关闭弹框
            closeComment(){
                var sp = this.$refs.commentBox;
                if(sp){
                    if(!sp.contains(event.target)){ //这句是说如果我们点击到了id为myPanel以外的区域
                        this.sheetShow = false;
                    }
                }
            },
            loadBottom(){
                this.page += 1;
                if(this.page <= this.lastPage){
                    this.request()
                }else {
                    this.allLoaded = true;
                }
                this.$refs.loadmore.onBottomLoaded();
            },
            loadTop(){
                this.page = 1;
                this.allLoaded = false;
                this.request()
                this.$refs.loadmore.onTopLoaded();
            },
        },

    }
</script>
<style lang="scss" scoped>
    .photo-product{
        background: $c8-color;
        overflow: scroll;
        -webkit-overflow-scrolling: touch;
        position: relative;
    }
    .list{
        padding-top: 20px;
        background: $c8-color;
        .list-header{
            color: $c4-color;
            padding-left: 40px;
            padding-bottom: 20px;
            border-bottom: 1px solid $c6-color;/*no*/
        }
        .item{
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 150px;
            background: #fff;
            border-bottom: 1px solid $c6-color;/*no*/
            padding: 0 40px;
            &:first-child{
                border-top: 1px solid $c6-color;/*no*/
            }
            .left{
                position:relative;
                padding-left: 100px;
                .image{
                    position: absolute;
                    left:0;
                    top:0;
                    z-index: 2;
                    width:80px;
                    height:80px;
                    overflow: hidden;
                    border-radius: 5px;
                    background: #000;
                }
                h4{
                    font-size: 26px;
                    line-height: 28px;
                    min-height: 28px;
                }
                p{
                    color: $c4-color;
                    font-size: 22px;
                    margin-top:20px;
                }
            }
            .right{
                width: 180px;
                text-align: center;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                .right-left{
                    font-size: 36px;
                }
                .right-right{
                    font-size: 50px;
                    margin-top: 4px;
                }
            }
            .middle{
                font-size: 26px;
            }
        }
    }

    .loadEnd{
        color: #999;
        text-align: center;
        font-size:24px;
        padding:20px 0 20px;
    }
    .sheet-box{
        position: fixed;
        left: 0;
        bottom: 0;
        z-index: 2003;
        width: 100%;
        height:100%;
        background: rgba(0,0,0,0.5);
        .sheet{
            position:absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            background: #e0e0e0;
            text-align: center;
            font-size: 26px;
            &>div{
                background: #fff;
                height: 80px;
                line-height: 80px;
                &.list{
                    border-bottom: 1px solid $c6-color;/*no*/
                }
                &.delete{
                    color: #df5000;
                }
                &.list-bottom{
                    margin-top: 20px;
                }
            }
        }
    }

</style>



