<template>
    <div class="has-topbar">
    <div class="index-goods bar_top">
        <div class="nav_bar">
            <div class="icon-left fast-click" @click="goBack(-1)"></div>
            <p>商品详情</p>
        </div>
        <mt-swipe :auto="2000">
            <mt-swipe-item v-for="(list,index) in banner" :key="index"><img class="img" :src="list" alt=""></mt-swipe-item>
        </mt-swipe>
        <div class="info">
            <h4>{{lists.goods_title}}</h4>
            <h5>{{lists.goods_second_title}}</h5>
            <div class="info_bottom">
                <div class="price">￥{{price}}</div>
                <!--<div class="sale">已销售：{{lists.package_sale}}</div>-->
            </div>
        </div>
        <div class="goods_dels" v-html="lists.goods_content">
        </div>
        <div class="main-footer">
            <div class="tab-small" @click="toCart()">
                <i class="icon icon-cart"></i>
                <p>购物车</p>
            </div>
            <!--<div @click="goToChat" class="tab-small">-->
                <!--<i class="icon icon-service"></i>-->
                <!--<p>客服</p>-->
            <!--</div>-->
            <div class="tab" @click="showPopup('cart')">加入购物车</div>
            <div class="tab main-color" @click="showPopup('buy')">立刻购买</div>
        </div>
        <mt-popup  v-model="popupVisible" position="bottom">
            <div class="po-box has-padding-bottom">
                <div class="po-goods">
                    <div class="po-img">
                        <img class="img" v-lazy="banner[0]" :key="banner[0]" alt="">
                    </div>
                    <h4>{{lists.goods_title}}</h4>
                    <p>￥{{cartPrice}}</p>
                </div>
                <div class="po-spec">
                    <div class="spec-list" v-for="(list,key) in goodsSpec" :key="key">
                        <h4>{{list.name}}</h4>
                        <button :class="{'active':sel[key] == index}" v-for="(item,index) in list.speckey" :key="index" @click="activeFn(key,index,list.name,item.msg)">{{item.msg}}</button>
                    </div>
                </div>
                <div class="num-btn">
                    <h4>数量</h4>
                    <div class="btn-group">
                        <span class="fast-click"  @click="less">-</span>
                        <input class="num" @input="handleInput" type="tel" v-model="cartNum"/>
                        <span class="fast-click"  @click="add">+</span>
                    </div>
                </div>
                <div v-if="popupContent == 'cart'" @click="addCart" class="btn">确定加入购物车</div>
                <div v-if="popupContent == 'buy'" @click="confirmBuy" class="btn">确定购买</div>
            </div>
        </mt-popup>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        name: 'IndexGoods',
        data () {
            return {
                goodsId:'',

                lists:[],
                banner:[],
                price:'',

                popupVisible: false,
                popupContent:'',

                goodsSpec:[],

                sel:[],
                selArr:{},
                sellingPriceObj:{},
                //规格
                sellingPriceKey:'',
                cartPrice:'',

                cartNum:1,
                maxNumObj:{},
                maxNum:0,

                cartList:[],
                token:'',
                isPay:'',
                isNeed:'',
                goodsPurchase: 1,

                userType:'',
                isAuth:'',
                isLogin:'',
                payGoods:'',
            }
        },
        created(){
            this.goodsId = this.$route.params.id;
             this.token = this.getToken();
            this.userTypeFn();
            //关闭客服
//            api.closeFrame({
//                name: 'cs'
//            });
        },
        methods:{
            //购物车跳转
            toCart(){
                if(!this.isLogin){
                    this.$router.push('/Login')
                    return;
                }else {
                    this.$router.push('/Cart')
                    return;
                }
            },
            //判断用户类型
            userTypeFn(){
                let sign = this.getEncrypt("token="+this.token);
                this.$http.post("/app.member/userinfo.html",{
                    sign: sign,
                    token: this.token,
                }).then(res => {
                    if(res.data.code == 1){
                        this.isLogin = true;
                        this.userType = res.data.data.type;
                        this.isAuth = res.data.data.is_auth;
                        this.payGoods = res.data.data.pay_goods;
                        this.getData();
                        return
                    }else{
                        this.userType = 1;
                        this.isAuth = 0;
                        this.getData();
                    }
                })
            },
            //获取商品信息
            getData(){
                if(this.token){
                    let sign = this.getEncrypt("goods_id="+this.goodsId+"&token="+this.token);
                    this.$http.post('/app.Goods/getinfo.html',{
                        goods_id: this.goodsId,
                        sign: sign,
                         token: this.token,
                    }).then(response => {
                        this.lists = response.data.data[0];
                        this.banner = this.lists.goods_image.split("|");
                        if(this.userType != 1 && this.isAuth == 1){
                            this.price = this.lists.spec[0].cb_selling_price;
                            this.cartPrice = this.lists.spec[0].cb_selling_price;
                            //起购数
                            this.goodsPurchase = this.lists.goods_purchase
                        }else{
                            this.price = this.lists.spec[0].selling_price;
                            this.cartPrice = this.lists.spec[0].selling_price;
                        }
                        this.maxNum = this.lists.spec[0].goods_stock - this.lists.spec[0].goods_sale;
                        this.goodsSpec = this.lists.speclist;
                        this.sellingPriceKey = this.lists.spec[0].goods_spec
                        this.isNeed = this.lists.is_need

                        this.cartNum = this.goodsPurchase;
                        //获取价格数组
                        for (var i = 0; i < this.lists.spec.length; i++){
                            let msg = this.lists.spec[i];
                            this.sellingPriceObj[msg.goods_spec] = msg.selling_price;
                            this.maxNumObj[msg.goods_spec] = msg.goods_stock - msg.goods_sale;
                        }
                        //列表默认状态
                        for (var i = 0; i < this.lists.speclist.length; i++){
                            let msg = this.lists.speclist[i];
                            this.sel[i] = 0;
                            this.selArr[msg.name] =msg.speckey[0].msg
                        }
                    }).catch(error => {
                        console.log(error)
                    });
                }else{
                    let sign = this.getEncrypt("goods_id="+this.goodsId);
                    this.$http.post('/app.Goods/getinfo.html',{
                        goods_id: this.goodsId,
                        sign: sign,
                        // token: this.token,
                    }).then(response => {
                        this.lists = response.data.data[0];
                        this.banner = this.lists.goods_image.split("|");
                        this.price = this.lists.spec[0].selling_price;
                        this.cartPrice = this.lists.spec[0].selling_price;
                        this.maxNum = this.lists.spec[0].goods_stock - this.lists.spec[0].goods_sale;
//                        this.goodsSpec = this.lists.speclist;
                        this.sellingPriceKey = this.lists.spec[0].goods_spec
                        this.isNeed = this.lists.is_need
                        this.isPay = this.lists.member.is_pay

                        this.goodsPurchase = this.lists.goods_purchase
                        this.cartNum = this.goodsPurchase;
                        //获取价格数组
                        for (var i = 0; i < this.lists.spec.length; i++){
                            let msg = this.lists.spec[i];
                            this.sellingPriceObj[msg.goods_spec] = msg.selling_price;
                            this.maxNumObj[msg.goods_spec] = msg.goods_stock - msg.goods_sale;
                        }
                        //列表默认状态
                        for (var i = 0; i < this.lists.speclist.length; i++){
                            let msg = this.lists.speclist[i];
                            this.sel[i] = 0;
                            this.selArr[msg.name] =msg.speckey[0].msg
                        }
                    }).catch(error => {
                        console.log(error)
                    });
                }
            },
            //判断是购物车还是立刻购买
            showPopup( value ){
                if(!this.isLogin){
                    this.$router.push('/Login')
                    return;
                }else{
                    this.popupVisible = true;
                    this.popupContent = value;
                }
            },
            // 跳转到客服
            goToChat(){
                if(!this.isLogin){
                    this.$router.push('/Login');
                    return;
                }else{
                    this.$router.push({name:'Service',params:{id:'2',goodId:this.goodsId}})
                }
            },
            //规格选择
            activeFn(key,index,name,msg){
                this.sel[key] = index;
                this.sel = this.sel.concat([]);
                this.selArr[name] = msg;
                this.sellingPriceKey = "";
                for(var data in this.selArr){
                    this.sellingPriceKey += data+":"+ this.selArr[data]+",";
                }
                this.sellingPriceKey = this.sellingPriceKey.substring(0,this.sellingPriceKey.length-1);
                this.cartPrice = this.sellingPriceObj[this.sellingPriceKey];
                this.maxNum = this.maxNumObj[this.sellingPriceKey];
//                console.log(this.maxNum)
            },
            //数量添加
            add(){
//                if(this.popupContent == 'buy'){
                    if(this.cartNum >= this.maxNum){
                        Toast({
                            message: '数量超出范围',
                            duration: 1000
                        });
                        return;
                    }
//                }else{
//                    let addCartList = this.$cache.get('cartList').reverse()
//                    for (var item of addCartList) {
//                        if (item.id == this.lists.id) {
//                            if( item.alias == this.sellingPriceKey ){
//                                item.num = parseInt(item.num) + parseInt(this.cartNum)
//                                if(item.num  >= this.maxNum){
//                                    Toast({
//                                        message: '数量超出范围',
//                                        duration: 1000
//                                    });
//                                    return
//                                }
//                            }
//                        }
//                    }
//                }
                this.cartNum = parseInt(this.cartNum) + 1;
            },
            //数量减少
            less(){
                if( this.cartNum <= this.goodsPurchase ){
                    Toast({
                        message: '该商品最低限购数为'+this.goodsPurchase,
                        duration: 1000
                    });
                    return;
                }
                this.cartNum = parseInt(this.cartNum) - 1;
            },
            //数量输入
            handleInput(e){
                this.cartNum=e.target.value.replace(/[^\d]/g,'');
                if(this.cartNum > this.maxNum){
                    Toast({
                        message: '数量超出范围',
                        duration: 1000
                    });
                    this.cartNum = this.maxNum;
                }else if(this.cartNum == '' || this.cartNum <= this.goodsPurchase){
                    this.cartNum = this.goodsPurchase;
                }else{
                    this.cartNum = this.cartNum.replace(/\b(0+)/gi,"");
                }
            },
            //加入购物车检查库存
            checkLimitbuy(value){
                let token = this.getToken();
                let sign = this.getEncrypt("goods_id="+this.lists.id+"&goods_spec="+this.sellingPriceKey+"&number="+this.cartNum+"&token="+token);
                this.$http.post("/app.Order/checkLimitbuy.html",{
                    sign: sign,
                    goods_id: this.lists.id,
                    goods_spec: this.sellingPriceKey,
                    number: this.cartNum,
                    token: token
                }).then(response => {
                    //确认购买库存检查
                    if(value == 1){
                        let goodInfo = [{
                            id: this.lists.id,
                            img: this.banner[0],
                            num: this.cartNum,
                            price: this.cartPrice,
                            goods_purchase: this.goodsPurchase,
                            is_need: this.isNeed,
                            title: this.lists.goods_title,
                            alias: this.sellingPriceKey,
                            spec: this.lists.spec,
                            isSelect:false,
                        }]
                        if( response.data.msg != "你可以购买"){
                            Toast({
                                message: response.data.msg,
                                duration: 1000
                            });
                            return;
                        }else{
                            this.$cache.set('placeOrder', goodInfo);
                            this.$cache.set('placeOrderIndex','');
                            this.$cache.delete('SiteIndex');
                            this.$cache.set('SiteIndex','');
                            this.$router.push({name:'SubmitOrder'});
                        }
                    }else if(value == 2){
                    //购物车库存检查
                        let goodInfo = {
                            id: this.lists.id,
                            img: this.banner[0],
                            num: this.cartNum,
                            price: this.cartPrice,
                            goods_purchase: this.goodsPurchase,
                            is_need: this.isNeed,
                            title: this.lists.goods_title,
                            alias: this.sellingPriceKey,
                            spec: this.lists.spec,
                            isSelect:false,
                        }
                        if( response.data.msg != "你可以购买"){
                            Toast({
                                message: response.data.msg,
                                duration: 1000
                            });
                            return;
                        }else{
                            if(!this.$cache.get('cartList')){
                                this.cartList.push(goodInfo)
                            }else{
                                this.cartList = this.$cache.get('cartList').reverse()
                                let hasExit = false //是否已经存在同规格的产品
                                for (var item of this.cartList) {
                                    if (item.id == goodInfo.id) {
                                        if( item.alias == goodInfo.alias ){
                                            item.num = parseInt(item.num) + parseInt(goodInfo.num)
                                            item.img = goodInfo.img
                                            item.price = goodInfo.price
                                            item.title = goodInfo.title
                                            if(item.num  > this.maxNum){
                                                Toast({
                                                    message: '商品库存不足',
                                                    duration: 1000
                                                });
                                                return
                                            }else{
                                                hasExit = true
                                            }
                                        }
                                    }
                                }
                                if (!hasExit) {
                                    this.cartList.push(goodInfo)
                                }
                            }
                            this.$cache.set("cartList",this.cartList.reverse());
                            Toast({
                                message: '加入购物车成功',
                                duration: 1000
                            });
                            this.popupVisible = false;
                        }
                    }
                })
            },
            //确认购买
            confirmBuy(){
                this.checkLimitbuy(1)
            },
            //加入购物车
            addCart(){
                this.checkLimitbuy(2)
            }
        }
    }
</script>

<style lang="scss" scoped>
.index-goods{
    padding-bottom:125px;
}
.mint-swipe{
    height:660px;
    .mint-swipe-item img{
        width:100%;
    }
}
.info{
    padding:20px;
    h4{
        font-size:36px;
        padding-bottom:20px;
    }
    h5{
        font-size:26px;
        padding-bottom:45px;
    }
    .info_bottom{
        display: flex;
        justify-content: space-between;
        align-items:flex-end;
        .price{
            color: $main-color;
            font-size:40px;
        }
        .sale{
            color: $c4-color;
            font-size:24px;
        }
    }
}
.main-footer{
    position: fixed;
    left:0;
    bottom:0;
    width:100%;
    height:125px;
    background: #fff;
    box-shadow: $top-shadow;
    display: flex;
    flex-wrap: wrap;
    text-align:center;
    .tab-small{
        height:100%;
        padding-top:16px;
        flex: 1;
        border-right:1px solid $c6-color;/* no */
        .icon{
            font-size: 60px;
        }
        p{
            font-size:26px;
        }
    }
    .tab{
        flex:2;
        height:100%;
        line-height: 125px;
        font-size:36px;
        &.main-color{
            color: #fff;
            background: $main-color;
        }
    }
}
.po-box{
    padding-top:60px;
    position: relative;
    .btn{
        background: $main-color;
        display: block;
        text-align: center;
        color: #fff;
        font-size:34px;
        height:120px;
        line-height:120px;
        border: none;
    }
    .po-goods{
        padding: 0 40px 0 210px;
        position: relative;
        height: 150px;
        font-size:36px;
        line-height: 40px;
        .po-img{
            width:150px;
            height:150px;
            overflow: hidden;
            border-radius: 5px;
            position: absolute;
            left:40px;
            top:0;
        }
        p{
            margin-top: 80px;
        }
    }
    .po-spec{
        margin:80px 40px 40px;
        .spec-list{
            padding-bottom: 20px;
            h4{
                font-size: 30px;
                margin-bottom: 10px;
            }
            button{
                background: #fff;
                border: 1px solid $c4-color;/*no*/
                color: $c4-color;
                padding:6px 20px;
                border-radius: 5px;
                margin-right: 20px;
                &.active{
                    border-color: $main-color;
                    color: $main-color;
                }
            }
        }
    }
    .num-btn{
        padding:0 40px 60px;
        h4{
            font-size: 30px;
            margin-bottom: 20px;
        }
        .btn-group{
            display: flex;
            justify-content: flex-start;
            align-items: center;
            span{
                width:60px;
                height:55px;
                background: #f2f2f2;
                text-align: center;
                /*line-height: 60px;*/
                padding-top:5px;
                font-size:46px;
                margin-left: 8px;
            }
            .num{
                width:70px;
                display: inline-block;
                padding:0;
                height:60px;
                line-height:60px;
                background: #f2f2f2;
                text-align: center;
                font-size:36px;
                border:none;
                margin-left: 8px;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

    }
}

</style>



