<template>
    <div class="has-topbar">
    <div class="sign-in">
        <div class="nav"><i class="icon-left fast-click" @click="goBack(-1)"></i> 上一步</div>
        <div class="input-group">
            <label for="password">输入新密码:</label>
            <input id="password" class="input" type="password" v-model="password">
        </div>
        <div class="input-group">
            <label for="fix-psd">确认新密码:</label>
            <input id="fix-psd" class="input" type="password" v-model="confirmPassword">
        </div>

        <div class="btn-group">
            <button class="btn btn-main" @click="confirm()">确定</button>
        </div>
    </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return{
            password:'',
            confirmPassword:'',
            phone:'',
            verify:''
        }
    },
    created(){
        this.phone = this.$route.params.phone;
        this.verify = this.$route.params.verify;
    },
    methods:{
        confirm(){
            if( this.password != this.confirmPassword){
                Toast({
                    message: '两次密码不一致，请重新输入',
                    duration: 1000
                })
            }else{
                let sign = this.getEncrypt('password='+this.confirmPassword+'&phone='+this.phone+'&verify='+this.verify)
                this.$http.post('/app.Member/resetPassword.html',{
                    sign: sign,
                    phone:this.phone,
                    verify:this.verify,
                    password: this.confirmPassword
                }).then( response => {
                    if(response.data.code == 1){
                        Toast({
                            message: response.data.msg,
                            duration: 2000
                        })
                        this.$router.push('/Login')
                    }else{
                        Toast({
                            message: response.data.msg,
                            duration: 1000
                        })
                    }
                }).catch( error => {
                    console.log(error)
                })
            }
        },
    }
}
</script>

<style lang="scss" scoped>
.sign-in{
    padding:40px;
    .nav{
        font-size: 52px;
        color: $main-color;
        padding-bottom: 30px;
        i{
            font-size: 46px;
        }
    }
    .input-group{
        position: relative;
        margin-top: 110px;
        font-size: 30px;
        color: #ccc;
        height: 60px;

        label{
            position: absolute;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            line-height: 60px;
            top: 0px;
            left: 5px;
            i{
                font-size: 36px;
            }
        }
        input{
            height: 100%;
            box-sizing: border-box;
            width: 100%;
            font-size: 30px;
            border:0;
            padding-left: 175px;
            border-bottom: 1px solid $c6-color;/*no*/
        }
    }
    .btn-group{
        margin-top: 90px;
        .btn{
            width: 100%;
            height: 80px;
            font-size: 30px;
            background: #fff;
            border: 1px solid $main-color;/*no*/
            color: $main-color;
            border-radius: 5px;
        }
        .btn-main{
            background: $main-color;
            color: #fff;
            margin-bottom: 20px;
        }
    }
}
</style>



