<template>
    <div class="has-topbar">
    <div class="set-personage bar_top">
        <div class="nav_bar">
            <router-link to="./set" class="icon-left fast-click"></router-link>
            <p>个人资料</p>
        </div>
        <div class="list">
            <div @click="goAvater" class="item">
                <div class="left">
                    <img v-if="userHeadimg != ''" class="img" v-lazy="userHeadimg" :key="userHeadimg" alt="">
                </div>
                <p>头像</p>
                <i class="icon-right"></i>
            </div>
            <router-link tag="div" to="./setUserData/0" class="item">
                <div class="left"><p>昵称</p></div>
                <p>{{userNickname}}</p>
                <i class="icon-right"></i>
            </router-link>
            <!-- <router-link to="./setUserData/1" class="item">
                <div class="left"><p>性别</p></div>
                <p>{{userSex}}</p>
            </router-link> -->
             <!-- <div class="item">
                <div class="left"><p>性别</p></div>
                <p>{{userSex}}</p>
            </div>
            <div class="item">
                <div class="left"><p>身份证</p></div>
                <p>{{userIdCard}}</p>
            </div> -->
            <div class="item" @click="showPopup">
                <div class="left"><p>地区</p></div>
                <p><span>{{myAddressProvince}}</span><span>{{myAddressCity}}</span><span>{{myAddresscounty}}</span></p>
                <i class="icon-right"></i>
            </div>
        </div>
        <mt-popup v-model="popupVisible" position="bottom">
            <mt-picker :slots="mySlots" :show-toolbar="true"  ref="picker" @change="onMyAddressChange"><span @click="handleConfirm()" class="btn">确定</span></mt-picker>
        </mt-popup>
    </div>
    </div>
</template>

<script>
    import myaddress from '@/common/js/city.json';
    import {Toast} from 'mint-ui';
    export default {
        name: 'HelloWorld',
        data () {
            return {
                myAddressProvince:'省',
                myAddressCity:'市',
                myAddresscounty:'区/县',
                popupVisible:false,
                mySlots:[
                    {
                        flex: 1,
                        defaultIndex: 1,
                        values:  Object.keys(myaddress),    //省份数组
                        className: 'slot1',
                        textAlign: 'center'
                    }, {
                        flex: 1,
                        values:[],
                        className: 'slot2',
                        textAlign: 'center'
                    },
                    {
                        flex: 1,
                        values: [],
                        className: 'slot3',
                        textAlign: 'center'
                    }
                ],
                userNickname: '',
                userSex: '',
                userHeadimg: '',
                userIdCard:'',
                isShield:'',
            }
        },
        created(){
            this.getUserInfo()
        },
        methods:{
            onMyAddressChange(picker, values) {
                if(myaddress[values[0]]){
                    picker.setSlotValues(1,Object.keys(myaddress[values[0]]));
                    picker.setSlotValues(2,myaddress[values[0]][values[1]]);
                }
            },
            goAvater(){
                if(this.isShield == 1){
                    return;
                }else{
                    this.goBack('/avater')
                }
            },
            showPopup(){
                this.popupVisible = true;
            },
            handleConfirm(){
                this.myAddressProvince = this.$refs.picker.getValues()[0];
                this.myAddressCity = this.$refs.picker.getValues()[1];
                this.myAddresscounty = this.$refs.picker.getValues()[2];
                this.popupVisible = false
                this.setUserAddress()
            },
            getUserInfo(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.Member/index.html',{
                    sign: sign,
                    token: this.getToken()
                }).then( response => {
                    if(response.data.code == 1){
                        // console.log(response.data.data.member.type)
                        this.userNickname = response.data.data.member.nickname
                        this.userSex = response.data.data.member.sex
                        this.isShield = response.data.data.member.isshield
                        this.userIdCard = response.data.data.member.idcarddeal
                        this.userHeadimg = response.data.data.member.headimg
                        this.myAddressProvince = response.data.data.member.province
                        this.myAddressCity = response.data.data.member.city
                        this.myAddresscounty = response.data.data.member.area
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            setUserAddress(){
                let sign = this.getEncrypt('area='+this.myAddresscounty + '&city='+this.myAddressCity + '&province=' + this.myAddressProvince + '&token='+this.getToken())
                this.$http.post('/app.Member/update.html',{
                    sign: sign,
                    token: this.getToken(),
                    province: this.myAddressProvince,
                    city: this.myAddressCity,
                    area: this.myAddresscounty,
                }).then( response => {
                    if(response.data.code==1){
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        });
                    }
                }).catch( error => {
                    console.log(error)
                })
            }
        },
        mounted(){
            this.$nextTick(() => {
                this.mySlots[0].defaultIndex = 0
            })
        },

    }
</script>
<style lang="scss" scoped>
.list{
    background: $c8-color;
    padding-top:20px;
    .item{
        height:100px;
        padding:0 40px;
        display: inline-block;
        background: #fff;
        border-bottom: 1px solid $c6-color;/*no*/
        display: flex;
        justify-content: flex-start;
        align-items: center;
        &:first-child{
           border-top: 1px solid $c6-color;
        }
        .left{
            width: 80px;
            margin-right: 40px;
            img{
                background: #000;
                height: 80px;
                border-radius: 5px;
            }
        }
        p{
            flex: 1;
            font-size:24px;
            line-height:26px;
            span{
                margin-right: 30px;
            }
        }
        i{
            font-size:40px;
        }
    }
}
</style>



