<template>
    <div class="has-topbar">
    <div class="my-secondteam bar_top">
        <div class="nav_bar" @click="goBack(-1)">
            <span class="icon-left fast-click"></span>
            <p>返回</p>
        </div>
        <div class="wrapper"  ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
                <div class="header">{{secondteams.teamnum}}人</div>
                <div class="list">
                    <div class="item" v-for="(secondteam,index) in secondteams.data" :key="index">
                        <div class="left">
                            <div class="image">
                                <img class="img" v-lazy="secondteam.headimg" :key="secondteam.headimg" alt=""/>
                            </div>
                            <h4>{{secondteam.nickname}}</h4>
                            <p>{{secondteam.leveltitle}}</p>
                            <p>{{secondteam.spreat_at}}</p>
                        </div>
                    </div>
                </div>
                <div v-if="allLoaded && secondteams.data != ''" class="load">- 已加载全部 -</div>
                <div v-if="secondteams.data == ''" class="load">- 暂无记录 -</div>
            </mt-loadmore>
        </div>
    </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            secondteams:{},
            page:1,
            lastPage:'',
            allLoaded: false,
            wrapperHeight: 0,
            autoFill:false,
            bottomPullText:'加载更多',
            topPullText:'刷新',
        }
    },
    created(){
        this.getTeamData();
    },
    methods:{
        getTeamData(){
            let sign = this.getEncrypt(
                'id='+this.$route.params.id+
                '&page='+this.page+
                '&token='+this.getToken()
            )
            this.$http.post('/app.Teams/teamlower.html',{
                sign: sign,
                token: this.getToken(),
                id: this.$route.params.id,
                page: this.page
            }).then( response => {
                if(response.data.code == 1){
                    this.secondteams = response.data.data;
                    this.lastPage = this.secondteams.last_page;
                    if(this.page >= this.lastPage){
                        this.allLoaded = true;
                    }else{
                        this.allLoaded = false;
                    }
                }else{
                    
                }
            }).catch( error => {
                console.log(error)
            })
        },
        //上拉加载
        loadBottom() {
            this.page += 1;
            this.getTeamData()
            this.$refs.loadmore.onBottomLoaded();
        },
        //下拉刷新
        loadTop() {
            this.page = 1;
            this.getTeamData()
            this.$refs.loadmore.onTopLoaded();
        },
    },
    mounted(){
        setTimeout(()=>{
            let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
            if (hasBottombar){
                this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - 34;
                return
            }else{
                this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
                return
            }
        },50)
    },
}
</script>

<style lang='scss' scoped>
.wrapper{
    overflow:scroll;
    -webkit-overflow-scrolling: touch;
    position: relative;
    .load{
        line-height: 60px;
        text-align: center;
        font-size:26px;
        background: $c8-color;
    }

}
.header{
    height:150px;
    line-height:150px;
    text-align: center;
    font-size: 40px;
}
.list{
    .item{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 150px;
        background: #fff;
        border-bottom: 1px solid $c6-color;/*no*/
        padding: 0 40px;
        &:first-child{
            border-top: 1px solid $c6-color;/*no*/
        }
        .left{
            position:relative;
            padding-left: 120px;
            min-height: 100px;
            .image{
                position: absolute;
                left:0;
                top:0;
                z-index: 2;
                width:100px;
                height:100px;
                overflow: hidden;
                border-radius: 5px;
                background: #000;
            }
            h4{
                font-size: 26px;
                line-height: 28px;
                min-height: 28px;
                margin-top: 4px;
            }
            p{
                color: $c4-color;
                font-size: 22px;
                margin-top:40px;
            }
        }
    }
}
</style>



