<template>
    <div class="has-topbar">
    <div class="my-wallet bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>我的钱包</p>
        </div>
        <div class="header">
            <div class="image"><img class="img" v-lazy="headimg" :key="headimg" alt=""></div>
            <h4>
                <p><span>总金额：</span>￥{{rebate_amount}}</p>
            </h4>
        </div>
        <ul class="tabs">
            <li>
                <p>可提现金额</p>
                <h4>￥{{rebate_money}}</h4>
            </li>
            <li @click="showPop()">
                <p>待到账金额</p>
                <h4>￥{{no_account_amount}}</h4>
            </li>
        </ul>
        <div class="list">
            <!--<div class="bonus">-->
                <!--<p @click="goBack('/BonusDetails')">奖金明细</p>-->
                <!--&lt;!&ndash;<div>&ndash;&gt;-->
                    <!--&lt;!&ndash;<router-link :to="{ name: 'BonusDetails', params: { id: index+1 } }" v-for="(bonusType,index) in bonusTypes" :key="index" class="bonus-type">{{bonusType}}</router-link>&ndash;&gt;-->
                <!--&lt;!&ndash;</div>&ndash;&gt;-->
            <!--</div>-->
            <router-link to="/BonusDetails" class="item"><span>奖金明细</span><i class="icon-right"></i></router-link>
            <router-link to="/WithdrawDetails/1" class="item"><span>提现明细</span><i class="icon-right"></i></router-link>
            <!--<router-link to="/WithdrawDetails/2" class="item"><span>充值明细</span><i class="icon-right"></i></router-link>-->
            <div class="btn-group">
                <router-link to="/Withdraw" class="btn">我要提现</router-link>
                <!--<router-link to="/Pay" class="btn">积分充值</router-link>-->
            </div>
        </div>

        <transition name="fade">
            <div v-show="Pop">
                <div class="pop-bg"  @click="Pop=false"></div>
                <div class="pop-wrap">
                    <div class="pop">
                        <h3>{{description.title}}</h3>
                        <p v-html="description.content"></p>
                        <div class="pop-btn-wrap">
                            <button @click="Pop=false">确定</button>
                            <!--<button class="pop-btn" @click="Pop=false">关闭</button>-->
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
    </div>
</template>

<script>
    export default {
        name: 'MyWallet',
        data () {
            return {
                headimg:'',
                rebate_amount: '',
                rebate_money: '',
                no_account_amount: '',
                bonusTypes:[
                    '开发奖',
                    '团队奖',
                    '管理奖',
                    '领袖奖'
                ],
                Pop:false,
                description:{}
            }
        },
        created(){
            this.getUserInfo()
            this.toDescription()
        },
        methods:{
            getUserInfo(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.Wallet/bonus.html',{
                    sign: sign,
                    token: this.getToken()
                }).then( response => {
                    if(response.data.code == 1){
                        // console.log(response.data.data)
                        this.headimg = response.data.data.headimg
                        this.rebate_amount = response.data.data.rebate_amount
                        this.rebate_money = response.data.data.rebate_money
                        this.no_account_amount = response.data.data.no_account_amount
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            showPop(){
                this.Pop = true
            },
            toDescription(){
                let sign = this.getEncrypt('id=8&token='+this.getToken())
                this.$http.post('/app.Placard/get.html',{
                    sign: sign,
                    token: this.getToken(),
                    id:8
                }).then( response => {
                    if(response.data.code == 1){
                        this.description = response.data.data
                    }
                }).catch( error => {
                    console.log(error)
                })
            }
        }
    }
</script>
<style lang="scss" scoped>
.header{
    padding: 40px;
    border-bottom: 1px solid $c6-color;
    .image{
        width: 130px;
        height:130px;
        border-radius: 5px;
        overflow: hidden;
        margin: 0 auto;
    }
    h4{
        font-size: 30px;
        text-align: center;
        padding-top: 40px;
        padding-bottom: 20px;
        h5{
            margin-bottom: 20px;
        }
        span{
            display: inline-block;
        }
    }
}
.tabs{
    height:150px;
    border-bottom: 1px solid $c6-color;/*no*/
    display: flex;
    justify-content: center;
    align-items: stretch;
    text-align: center;
    li{
        flex:1;
        border-right:1px solid $c6-color;/*no*/
        &:last-child{
             border: none;
         }
        h4{
            font-size: 40px;
        }
        p{
            font-size: 26px;
            color: $c4-color;
            margin-top: 38px;
            margin-bottom: 10px;
        }
    }
}
.list{
    background: $c8-color;
    padding-top: 20px;
    .item{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 120px;
        background: #fff;
        padding: 0 40px;
        border-bottom: 1px solid $c6-color;/*no*/
        font-size: 26px;
        &:first-child{
             border-top: 1px solid $c6-color;/*no*/
        }
        i{
            font-size:40px;
        }
    }
    .btn-group{
        background: #fff;
        padding: 80px 40px 0;
        .btn{
            line-height:80px;
            text-align: center;
            display: block;
            color: #fff;
            background: $main-color;
            border-radius: 5px;
            font-size: 30px;
        }
    }
}
.list{
    .bonus{
        height: 150px;
        padding: 20px 40px 0;
        background: #fff;
        border-top: 1px solid $c6-color;/*no*/
        border-bottom: 1px solid $c6-color;/*no*/
        &>div{
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-top: 20px;
            .bonus-type{
                height: 60px;
                line-height: 60px;
                text-align: center;
                font-size: 22px;
                border: 2px solid $main-color;
                color: $main-color;
                border-radius: 4px;
                flex: 1;
                margin-right: 20px;
                &:last-child{
                     margin-right: 0;
                 }
            }
        }
    }
}

.pop-wrap{
    width: 550px;
    height: 550px;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -275px;
    margin-top: -275px;
    z-index: 9999;
    .pop{
        width: 450px;
        margin: 0 auto;
        margin-top: 60px;
        h3{
            font-size: 36px;
            margin-bottom: 50px;
            text-align: center
        }
        P{
            font-size: 26px;
            margin-bottom: 60px;
            height: 240px;
            overflow: auto;
        }
        button{
            width: 100%;
            height: 56px;
            background-color: $main-color;
            color: #fff;
            border: 1px solid $main-color;/*no*/
            /*margin-right: 70px;*/
            /*margin-left: 16px;*/
            border-radius: 5px;
        }
        .pop-btn{
            width: 168px;
            height: 54px;
            background-color: #fff;
            color: $main-color;
            margin: 0;
            border: 2px solid $main-color;
        }
    }
}
.pop-bg{
    position:fixed;
    top:0;
    bottom:0;
    width:100%;
    background-color:$color-background-d;
    z-index: 999;
}
</style>



