<template>
    <div class="has-topbar">
    <div class="inf-comment bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>评论列表</p>
        </div>
        <div class="con" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">

            <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
            <ul class="cell_con">
                <li class="list">
                    <div class="item">
                        <div class="headimg">
                            <img class="img" v-lazy="artLists.headimg" :key="artLists.headimg" />
                        </div>
                        <h4 v-text="artLists.nickname"></h4>
                        <p :class="{isOpen: artLists.is_open != 0}" ref="listP" v-html="artListsContent" @touchstart="showDeleteButton(artListsContent.replace(/<br\s*\/?>/gi,'\r\n'))" @touchmove="clearLoop()" @touchend="clearLoop()"></p>
                        <div class="btn" v-if="artLists.is_show"><span  @click="isOpen()">{{unfold[artLists.is_open]}}</span></div>
                        <!--视频-->
                        <div v-if="videourl != ''" @click="openVideoPlay(videourl)" class="file-video">
                            <img :src="artListsImg[0]" alt="视频"/>
                            <span class="icon-play"></span>
                        </div>
                        <!--图片-->
                        <div v-else class="img-group" >
                            <img :class="[{'thumbnail':artListsImg.length >1},{'sola':artListsImg.length == 1}]" @click.stop="openImg(artListsImg,index,true)" v-for="(item,index) in artListsImg" :key="item" v-lazy="item"/>
                        </div>
                        <div class="time">{{artLists.create_at}}</div>
                    </div>
                </li>
            </ul>
            <div class="criticism">
                <div class="tab">
                    <div :class="{active:tabs == 0 }" @click="tabsFn(0)"><span>评论({{artLists.comment_num}})</span></div>
                    <div :class="{active:tabs == 1 }" @click="tabsFn(1)"><span>点赞({{artLists.praise_num}})</span></div>
                </div>
                <!--评论列表-->
                <div v-if="tabs == 0">
                    <div class="noCri" v-if="criLists.length == 0">- 无评论内容 -</div>
                    <div class="cri-list" v-else v-for="(list,index) in criLists" :key="index" @click.stop="sheetShowFn(index,'',list.is_mine,2,list.id,list.mid)">
                        <div class="headImg">
                            <img class="img" v-lazy="list.headimg" :key="list.headimg" alt=""/>
                        </div>
                        <h4 v-text="list.nickname"></h4>
                        <p v-html="list.content.replace(/\r?\n/g,'<br />')"></p>
                        <div ref="criItem">
                            <div class="item" v-if="list.replys != ''">
                                <div v-if="ind < list.is_show" class="info" v-for="(item,ind) in list.replys" :key="ind" @click.stop="sheetShowFn(index,ind,item.is_mine,2,list.id,list.mid,item.id,'info')">
                                    <span class="name"><i>{{item.nickname}}</i><span v-if="item.from_mid != 0">回复<i>{{item.from_nickname}}</i></span></span>：
                                    <p v-html="item.content.replace(/\r?\n/g,'<br />')"></p>
                                </div>
                                <div v-if="list.replys.length > 3" class="more">
                                    <p @click="allShow(list.replys.length,index)">
                                        <span v-if="list.is_show == 3">共<span>{{list.replys.length}}</span>条回复 <i class="icon-right"></i></span>
                                        <span v-if="list.is_show != 3">收起</span>
                                    </p>
                                </div>
                            </div>
                            <div class="bottom">
                                <div class="time">{{list.create_at}}</div>
                                <div class="icon-group">
                                    <i class="icon-like" :class="{'active':list.isLike}" @click="tags(2,list.id,list.mid,index)"></i><span>{{list.zannum}}</span>
                                    <i class="icon-comment" @click="commentFn(2,list.id,list.mid)"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!--点赞列表-->
                <div v-if="tabs == 1" class="inf-tags-list" v-for="(list,index) in tagsLists" :key="index">
                    <div class="headimg">
                        <img v-lazy="list.headimg" :key="list.headimg" class="img" />
                    </div>
                    <div class="item">
                        <h4>{{list.nickname}}</h4>
                        <p>{{list.create_at}}</p>
                    </div>
                </div>
            </div>
            </mt-loadmore>
        </div>
        <!--main-footer-->
        <div class="main-footer">
            <div @click="tags(1)">
                <p><i class="icon-like" :class="{'active':artLists.isLike}"></i> <span>点赞</span></p>
            </div>
            <div @click="commentFn(1)"><p><i class="icon-comment"></i> <span>评论</span></p></div>
        </div>
        <!--写评论-->
        <div v-if="commentBoxShow" class="comment-box has-fixed-top">
            <div class="nav_bar">
                <div @click="commentBoxShow = false">取消</div>
                <div class="title">发布评论</div>
                <div><span class="btn" @click="submitComment(typeNum)">发送</span></div>
            </div>
            <div class="text-area">
                <textarea rows="8" placeholder="写评论" v-model="CommentInfo"></textarea>
            </div>
        </div>
        <!--回复选择弹窗-->
        <div v-if="sheetShow" @click="closeComment" class="sheet-box">
            <div class="sheet has-fixed-bottom" ref="commentBox">
                <div v-if="isMine == '0'" class="list" @click="sheetShowHide">回复</div>
                <div v-if="isMine == '1'" class="delete" @click="deleteComment(deleteId)">删除</div>
                <div class="list-bottom" @click="sheetShow=false">取消</div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        data () {
            return {
                unfold:[
                    '全文','收起'
                ],
                artLists:[],
                artListsContent:'',
                artListsImg:[],
                videourl:'',
                recordId:'',
                token:'',
                sign:'',
                tabs:0,
//                orderInfo:'',
                commentBoxShow: false,
                sheetShow: false,
                CommentInfo:'',
                //评论列表
                page:'',
                moreShow: true,
                isShowNum:3,
                criSign:'',
                criLists:'',
                criIndex:'',
                criInd:'',
                isMine:'',
                //回复评论参数
                typeNum:'',
                commentId:'',
                fromMid:'',
                commReplyId:'',
                deleteId:'',
                //点赞列表
                tagsLists:[],
                tagPage: '',
                tagsLastPage:'',
                //刷新
                wrapperHeight:0,
                allLoaded: false,
                autoFill:false,
                bottomPullText:'加载更多',
                topPullText:'刷新',
            }
        },
        created(){
            this.recordId = this.$route.params.id;
//            this.orderInfo = this.recordId;
            this.token = this.getToken();
            this.sign = this.getEncrypt("id="+this.recordId+"&token="+this.token);
            this.$http.post("/app.bbs/getinfo.html",{
                sign: this.sign,
                id: this.recordId,
                token: this.token
            }).then( response => {
                this.artLists = response.data.data;
                this.artListsImg = response.data.data.images;
                this.videourl = this.artLists.videourl;
                this.artListsContent = this.artLists.content.replace(/\r?\n/g,'<br />');
                this.isOverFrom()
            })

            this.requestComment();
            this.requestFags();
        },
        methods:{
            // 复制黏贴功能
            showDeleteButton(txt){
                clearTimeout(this.loop)
                this.loop = setTimeout(function () {
                    var clipBoard = api.require("clipBoard");
                    clipBoard.set(
                        {
                            value: txt
                        },
                        function (ret, err) {
                            if (ret) {
                                Toast({
                                    message: '复制成功',
                                    duration: 1000
                                });
                            } else {
                                alert(JSON.stringify(err));
                            }
                        }
                    );
                }.bind(this), 700)
            },
            clearLoop(){
                clearTimeout(this.loop)
            },
            //上拉加载更多
            loadBottom(){
                if(this.tabs == 0){
                    this.page += 1;
                    this.criSign = this.getEncrypt("page="+this.page+"&record_id="+this.recordId+"&token="+this.token);
                    this.$http.post("/app.bbs/get_comment.html",{
                        sign: this.criSign,
                        record_id: this.recordId,
                        token: this.token,
                        page:this.page
                    }).then( response => {
                        this.criLists =  this.criLists.concat(response.data.data.data)
                        if( this.page >= this.lastPage){
                            this.allLoaded = true;
                        }
                    })
                }else{
                    this.tagPage += 1;
                    let sign = this.getEncrypt("page="+this.tagPage+"&record_id="+this.recordId+"&token="+this.token);
                    this.$http.post("/app.Bbs/praiselist.html",{
                        sign: sign,
                        record_id: this.recordId,
                        token: this.token,
                        page: this.tagPage,
                    }).then( response => {
                        this.tagsLists = this.tagsLists.concat(response.data.data.data)
                        if( this.tagPage >= this.tagsLastPage){
                            this.allLoaded = true;
                        }
                    }).catch( error =>{
                        console.log(error)
                    })
                }
                this.$refs.loadmore.onBottomLoaded();
            },
            //下拉刷新
            loadTop(){
                if(this.tabs == 0){
                    this.requestComment();
                }else{
                    this.requestFags();
                }
                this.$refs.loadmore.onTopLoaded();
            },
            //全文展开、收起
            isOpen(){
                if(this.artLists.is_open == 0){
                    this.artLists.is_open = 1;
                }else{
                    this.artLists.is_open = 0;
                }
            },
            //判断显示全文
            isOverFrom(){
                setTimeout(()=>{
                    let height = this.$refs.listP.scrollHeight
                    if(height > (1.28*window.rem)){
                        this.artLists.is_show = true;
                    }
                },100)
            },
            //tab
            tabsFn(num){
                this.tabs = num;
                if(num == 0){
                    if( this.page < this.lastPage){
                        this.allLoaded = false;
                    }
                }else{
                    if( this.tagPage < this.tagsLastPage){
                        this.allLoaded = false;
                    }
                }
            },
            //评论列表请求
            requestComment(){
                this.page = 1;
                this.criSign = this.getEncrypt("page="+this.page+"&record_id="+this.recordId+"&token="+this.token);
                this.$http.post("/app.bbs/get_comment.html",{
                    sign: this.criSign,
                    record_id: this.recordId,
                    token: this.token,
                    page:this.page
                }).then( response => {
                    this.criLists = response.data.data.data;
                    this.lastPage = response.data.data.last_page;
                    this.allLoaded = false;
                })
            },
            //点赞列表请求
            requestFags(){
                this.tagPage = 1;
                let sign = this.getEncrypt("page="+this.tagPage+"&record_id="+this.recordId+"&token="+this.token);
                this.$http.post("/app.Bbs/praiselist.html",{
                    sign: sign,
                    record_id: this.recordId,
                    token: this.token,
                    page: this.tagPage,
                }).then( response => {
                    this.tagsLists = response.data.data.data;
                    this.tagsLastPage = response.data.data.last_page;
                    this.allLoaded = false;
                }).catch( error =>{
                    console.log(error)
                })
            },
            //更多评论显示
            allShow(num,index){
                if(this.moreShow){
                    this.criLists[index].is_show = num;
                }else{
                    this.criLists[index].is_show = 3;
                }
                this.moreShow = !this.moreShow;
            },
            //点赞
            tags(type,commentId,fromMid,index){
                if(type == 1){
                    this.sign = this.getEncrypt("record_id="+this.recordId+"&token="+this.token);
                    this.$http.post("/app.bbs/praise.html",{
                        sign: this.sign,
                        record_id: this.recordId,
                        token: this.token
                    }).then( response => {
                        if( response.data.code == 1){
                            this.requestFags();
                            if(response.data.msg == "点赞成功"){
                                this.artLists.isLike = true;
                                this.artLists.praise_num += 1;
                                Toast({
                                    message: response.data.msg,
                                    iconClass: 'icon-like',
                                    duration: 1500
                                });
                            }else if( response.data.msg == "取消点赞成功"){
                                this.artLists.isLike = false;
                                this.artLists.praise_num -= 1;
                                Toast({
                                    message: response.data.msg,
                                    duration: 1500
                                });
                            }
                        }

                    })
                }else if(type == 2){

                    this.sign = this.getEncrypt("comment_id="+commentId+"&from_mid="+fromMid+"&record_id="+this.recordId+"&token="+this.token);
                    this.$http.post("/app.bbs/praise.html",{
                        sign: this.sign,
                        record_id: this.recordId,
                        token: this.token,
                        comment_id: commentId,
                        from_mid: fromMid,
                    }).then( response => {
                        if(response.data.msg == "评论点赞成功"){
                            this.criLists[index].isLike = true;
                            this.criLists[index].zannum += 1;
                            Toast({
                                message: response.data.msg,
                                iconClass: 'icon-like',
                                duration: 1500
                            });
                        }else if( response.data.msg == "评论取消点赞成功"){
                            this.criLists[index].isLike = false;
                            this.criLists[index].zannum -= 1;
                            Toast({
                                message: response.data.msg,
                                duration: 1500
                            });
                        }
                    })
                }

            },
            //评论显示
            commentFn(num,commentId,fromMid,commReplyId=''){
                this.commentBoxShow = true;
                this.CommentInfo = '';
                this.typeNum = num;
                if(num == 2){
                    this.commentId = commentId;
                    this.fromMid = fromMid;
                    this.commReplyId = commReplyId;
                }
            },
            sheetShowFn(index,ind,mine,num,commentId,fromMid,commReplyId='',info){
                let criItem = this.$refs.criItem[index];
                if(criItem){
                    if(!criItem.contains(event.target)){
                        this.sheetShow = true;
                        this.deleteId = 0;
                    }
                }
                if(info == 'info'){
                    this.sheetShow = true;
                    this.deleteId = 1;
                }
                this.isMine = mine;
                this.typeNum=num;
                this.commentId=commentId;
                this.fromMid=fromMid;
                this.commReplyId=commReplyId;
                this.criIndex = index;
                this.criInd = ind;
            },
            //删除评论
            deleteComment(id){
                if(id == 0){
                    this.sign = this.getEncrypt("comment_id="+this.commentId+"&token="+this.token);
                    this.$http.post("/app.Bbs/del_Comment.html",{
                        sign: this.sign,
                        token: this.token,
                        comment_id: this.commentId
                    }).then(response => {
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        });
                        this.criLists.splice(this.criIndex,1)
                        this.sheetShow = false;
                    })
                }else if( id == 1){
                    this.sign = this.getEncrypt("reply_id="+this.commReplyId+"&token="+this.token);
                    this.$http.post("/app.Bbs/del_Reply.html",{
                        sign: this.sign,
                        token: this.token,
                        reply_id: this.commReplyId
                    }).then(response => {
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        });
                        this.criLists[this.criIndex].replys.splice(this.criInd,1)
                        this.sheetShow = false;
                    })
                }
            },
            //点击回复
            sheetShowHide(){
                this.commentBoxShow = true;
                this.CommentInfo = '';
                this.sheetShow = false;
            },
            //点击空白关闭弹框
            closeComment(){
                var sp = this.$refs.commentBox;
                if(sp){
                    if(!sp.contains(event.target)){ //这句是说如果我们点击到了id为myPanel以外的区域
                        this.sheetShow = false;
                    }
                }
            },
            //发送评论
            submitComment(type){
                if(type == 1){
                    this.sign = this.getEncrypt("content="+this.CommentInfo+"&record_id="+this.recordId+"&token="+this.token+"&type="+type)
                    this.$http.post("/app.bbs/comment.html",{
                        sign: 'xibaozhimi   ',
                        token: this.token,
                        type: type,
                        record_id: this.recordId,
                        content: this.CommentInfo,
                    }).then(response => {
                        this.commentBoxShow = false;
                        if(response.data.msg == "评论成功！"){
                            this.requestComment()
                            document.location.reload();
                        }
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        });
                    })

                }else if(type == 2){
                    this.sign = this.getEncrypt("comm_reply_id="+this.commReplyId+"&comment_id="+this.commentId+"&content="+this.CommentInfo+"&from_mid="+this.fromMid+"&record_id="+this.recordId+"&token="+this.token+"&type="+type)
                    this.$http.post("/app.bbs/comment.html",{
                        sign: this.sign,
                        token: this.token,
                        type: type,
                        record_id: this.recordId,
                        content: this.CommentInfo,
                        comment_id: this.commentId,
                        from_mid: this.fromMid,
                        comm_reply_id: this.commReplyId
                    }).then(response => {
                        this.commentBoxShow = false;

                        if(response.data.msg == "回复成功！"){
//                            this.criLists[this.criIndex].replys.push(response.data.data);
                            document.location.reload();
//                            this.$router.go(0)
                        }
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        });
                    })
                }

            }
        },
        mounted(){
            setTimeout(()=>{
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar){
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - (1.7*window.rem) - 34;
                    return
                }else{
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - (1.7*window.rem);
                    return
                }
            },50)
        },
    }
</script>
<style lang="scss" scoped>
.inf-comment{
    padding-bottom: 100px;
}
.con{
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    position: relative
}
.cell_con{
    background: $c8-color;
    padding: 20px 0;
    .release{
        height:100px;
        font-size:26px;
        display: flex;
        color: $main-color;
        justify-content: center;
        align-items: center;
        background:#fff;
        i{
            font-size:46px;
        }
    }
    .list{
        border-top:1px solid $c6-color;/*no*/
        border-bottom:1px solid $c6-color;/*no*/
        background :#fff;
        .item{
            padding:20px 20px 40px 130px;
            position: relative;
            .headimg{
                width:90px;
                height:90px;
                left:20px;
                top:20px;
                position: absolute;
                z-index: 88;
                img{
                    border-radius: 5px;
                }
            }
            h4{
                font-size:26px;
                margin-bottom:20px;
            }
            p{
                font-size:24px;
                line-height: 32px;
                text-align: justify;
                color: $c4-color;
                max-height:1.26rem;
                @include ellipsis-3h;
                &.isOpen{
                     display: block;
                     max-height:none;
                 }
            }
            .btn{
                margin-top:50px;
                font-size:30px;
            }
            .file-video {
                margin-top: 20px;
                position: relative;
                display: inline-block;
                width: 35%;
                img {
                    width: 100%;
                }
                .icon-play {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    margin: -50px 0 0 -50px;
                    font-size: 100px;
                    color: #fff;
                }
            }
            .img-group{
                margin-top:20px;
                max-width:500px;
                .sola{
                    width:350px;
                    height:250px;
                    object-fit: cover;
                }
                .thumbnail{
                    display: inline-block;
                    height: 150px;
                    width: 150px;
                    margin:0 10px 10px 0;
                    object-fit: cover;
                }
            }
            .time{
                font-size:22px;
                color: $c4-color;
                margin-top:5px;
            }
        }
    }
}

.criticism{
    .tab{
        display: flex;
        /*justify-content: center;*/
        align-items: stretch;
        text-align: center;
        font-size: 26px;
        height: 100px;
        line-height: 100px;
        border-bottom: 1px solid $c6-color;/*no*/
        div{
            flex:1;
            span{
                padding:0 10px;
            }
            &.active span{
                display: inline-block;
                height:100%;
                border-bottom: 3px solid $main-color;/*no*/
                box-sizing: border-box;
            }
        }
    }
}

.main-footer{
    position: fixed;
    left:0;
    bottom:0;
    width:100%;
    height:100px;
    background: #ffff;
    box-shadow:$top-shadow;
    display: flex;
    color: $c4-color;
    z-index: 99;
    &>div{
        flex: 1;
        font-size: 26px;
        p{
            display: flex;
            justify-content: center;
            align-items: center;
            line-height: 100px;
            i{
                font-size: 35px;
                &.active{
                    color: $main-color;
                 }
            }
        }

    }
}

.sheet-box{
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 99;
    width: 100%;
    height:100%;
    background: rgba(0,0,0,0.5);
    .sheet{
        position:absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        background: #e0e0e0;
        text-align: center;
        font-size: 26px;
        &>div{
            background: #fff;
            height: 80px;
            line-height: 80px;
            &.list{
                 border-bottom: 1px solid $c6-color;/*no*/
             }
            &.delete{
                color: #df5000;
            }
            &.list-bottom{
                margin-top: 20px;
            }
        }
    }
}

.comment-box{
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 99;
    width: 100%;
    height:100%;
    background: #fff;
    box-sizing: border-box;
    .nav_bar{
        padding: 0 20px;
        box-sizing: border-box;
        .title{
            flex:1;
            text-align: center;
        }
        .btn{
            color: $main-color;
        }
    }
    .text-area{
        padding-top: 100px;
        textarea{
            width: 100%;
            box-sizing: border-box;
            font-size: 26px;
            padding: 20px 20px 0;
            resize: none;
            border: none;
        }
    }
}

.cri-list{
    position: relative;
    padding: 20px 20px 20px 130px;
    border-bottom: 1px solid $c6-color;/*no*/
    .headImg{
        width:90px;
        height:90px;
        left:20px;
        top:20px;
        position: absolute;
        z-index: 88;
        img{
            border-radius: 5px;
        }
    }
    h4{
        font-size:26px;
        margin-bottom:20px;
    }
    p{
        font-size:24px;
        line-height: 32px;
        text-align: justify;
        color: $c4-color;
    }
    .item{
        padding: 20px 20px 10px;
        margin: 20px 0;
        background: $c8-color;
        font-size: 22px;
        .info{
            padding-bottom: 10px;
            .name{
                i{
                    color: $main-color;
                }
            }
            p{
                color: $c4-color;
                display: inline-block;
            }
        }
        .more{
            padding:10px 0 10px;
            i{
                color: #000;
            }
        }
    }
    .bottom{
        display: flex;
        margin-top: 20px;
        justify-content: space-between;
        color: $c4-color;
        font-size: 22px;
        .icon-group{
            display: flex;
            justify-content: center;
            align-items: center;
            span{
                margin-right: 20px;
            }
            i{
                font-size: 40px;
                &.active{
                    color: $main-color;
                }
            }
        }
    }
}
.noCri{
    padding: 20px 0;
    text-align: center;
    background: $c8-color;
    color: $c4-color;
}
/*点赞列表*/
.inf-tags-list{
    height:90px;
    display: flex;
    padding: 20px 40px;
    border-bottom: 1px solid $c6-color;/*no*/
    .headimg{
        width: 90px;
        height:90px;
    }
    .item{
        flex: 1;
        padding-left: 20px;
        position: relative;
        h4{
            font-size: 26px;
        }
        p{
            font-size: 22px;
            color: $c4-color;
            bottom: 0;
            left: 20px;
            position: absolute;
        }
    }
}

</style>



