<template>
    <div class="has-topbar">
    <div class="wallet-withdraw bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>提现</p>
        </div>
        <div class="header">
            <div class="way">
                <div class="box">
                    <p class="box-p">提现金额</p>
                    <div class="input-group">
                        <span>￥</span><input type="number" v-model="money" placeholder=""/>
                    </div>
                </div>
                
                <p class="header-way-p">可提现金额：{{rebate_money}}</p>
                <!-- <i class="icon-right"></i> -->
            </div>
        </div>
        <div class="sum-group">
            <h4>选择提现方式</h4>
            <div class="type">
                <!--<div :class="['type-input',{check_on: check==1}]" @click="payType(1)">-->
                    <!--<input type="radio">-->
                    <!--<img src="@/common/images/zhifubao.png" alt="">-->
                <!--</div>-->
                <div :class="['type-input',{check_on: check==2}]" class="type-input"  @click="payType(2)">
                    <input type="radio">
                    <p>线下微信转账</p>
                </div>
            </div>
            <!-- <div class="box">
                <span>￥</span>
                <input type="text"/>
            </div>
            <p>可用金额 1566.00元</p> -->
        </div>
        <div class="btn-group">
            <button class="btn" @click="submit()">确认提取</button>
        </div>
    </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
    export default {
        name: 'WalletWithdraw',
        data () {
            return {
                rebate_money:'',
                check: 2,
                money: ''
            }
        },
        created(){
            this.getUserInfo()
        },
        methods:{
            getUserInfo(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.Wallet/bonus.html',{
                    sign: sign,
                    token: this.getToken()
                }).then( response => {
                    if(response.data.code == 1){
                        this.rebate_money = response.data.data.rebate_money
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            payType(value){
                this.check = value
            },
            submit(){
                if(this.money==0){
                    Toast({
                        message: '请输入金额',
                        duration: 2000
                    });
                    return
                }
                if(this.check==0){
                    Toast({
                        message: '请选择提现方式',
                        duration: 2000
                    });
                    return
                }
                let sign = this.getEncrypt('money='+this.money+'&token='+this.getToken()+'&type='+this.check)
                this.$http.post('/app.Wallet/withdrawCash.html',{
                    sign: sign,
                    token: this.getToken(),
                    type: this.check,
                    money: this.money
                }).then( response => {
                    Toast({
                        message: response.data.msg,
                        duration: 2000
                    });
                }).catch( error => {
                    console.log(error)
                })
            }
        },
        watch:{
            money(newQuestion,oldQuestion){
                if(parseFloat(newQuestion)>parseFloat(this.rebate_money)){
                    this.money = this.rebate_money
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
.header{
    padding-bottom: 20px;
    background: $c8-color;
    .way{
        min-height: 140px;
        background: #fff;
        padding: 20px 40px;
        font-size: 26px;
        border-bottom: 1px solid $c6-color;/*no*/
        box-sizing: border-box;

        .box{
            font-size: 40px;
            display: flex;
            align-items: flex-end;
            .box-p{
                font-size: 26px;
                padding-bottom: 8px;
            }
            .input-group{
                position: relative;
                flex: 1;
                padding-left: 50px;
                input{
                    border: none;
                    border-bottom: 1px solid $c6-color;/*no*/
                    font-size: 40px;
                    height: 40px;
                    width: 100%;
                }
                span{
                    padding-left: 10px;
                    position: absolute;
                    left: 0;
                    top: 2px;
                }
            }


        }
        .header-way-p{
            margin-top: 20px;
            color: $c4-color
        }
    }
}
.sum-group{
    padding: 20px 40px;
    border-bottom: 1px solid $c6-color;/*no*/
    border-top: 1px solid $c6-color;/*no*/
    font-size:26px;
    .box{
        position: relative;
        font-size: 40px;
        border-bottom: 1px solid $c6-color;/*no*/
        margin-top: 10px;
        padding-left: 40px;
        span{
            position: absolute;
            left:0px;
            top:10px;
        }
        input{
            border: none;
            font-size: 40px;
            height: 60px;
            width: 100%;
        }
    }
    p{
        margin-top: 10px;
        color: $c4-color;
    }
    h4{
        margin-top: 20px
    }
    .type{
        padding-top: 30px;
        .type-input{
            // margin-bottom: 30px;
            overflow: hidden;
            p{
                line-height: 70px;
                // color: #000;
                margin-left: 60px;
                color: $main-color;
            }
            input{
                float: left;
                margin-top: 30px;
                width: 30px;
                height: 30px;
                line-height: 30px;
                border: 1px solid #ccc; /*no*/
                border-radius: 50%;
                -webkit-appearance: none;
                text-align: center;
                background: none;
            }
            // .check_on:after {
            //     content: '\2713';
            //     font-size: 20px;
            //     color: $main-color !important;
            //     -webkit-appearance: none;
            // }
            img{
                width: 200px;
                margin-left: 30px;
                border: 1px solid #ccc; /*no*/
            }
        }
        .check_on{
            input{
                border-color: $main-color;
            }
            img{
                border-color: $main-color;
            }
        }
    }
}
.btn-group{
    background: #fff;
    padding: 80px 40px 0;
    .btn{
        line-height:80px;
        text-align: center;
        width: 100%;
        border:0;
        color: #fff;
        background: $main-color;
        border-radius: 5px;
        font-size: 30px;
    }
}
</style>



