<template>
    <div class="has-topbar">
    <div class="cart-cart bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>购物车</p>
        </div>
        <h4 class="title_bar">
            <p>购物车（{{cartAllNum}}）</p>
            <span class="icon icon-trash" @click="deleteCart()"></span>
        </h4>
        <ul class="list" v-for="(list,index) in cartList" :key="index">
            <li class="item">
                <div :class="['choose',{check_on: list.isSelect}]" @click="single(index)"></div>
                <div class="image">
                    <img class="img" v-lazy="list.img" :key="list.img" alt=""/>
                </div>
                <div class="info">
                    <h4>
                        <p>{{list.title}}</p>
                        <h5>{{list.alias}}</h5>
                    </h4>
                    <div class="box">
                        <p>￥{{list.price}}</p>
                        <div class="btn-group">
                            <span class="btn fast-click" @click="less(index)">-</span>
                            <input class="num" onkeyup="this.value=this.value.replace(/\D/g,'')" @input="handleInput(index,list.id,list.alias)"  type="tel" v-model="list.num"/>
                            <span class="btn fast-click" @click="add(index,list.id,list.alias)">+</span>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
        <div class="main-footer">
            <div @click="allSelect">
                <div :class="['choose',{ check_on: allChecked }]" ></div>
                <label>全选</label>
            </div>
            <div class="right">
                <span>合计：<i>￥{{totalPrice}}</i></span>
                <div class="btn-primary" @click="OrderGenerated">立刻购买</div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        name: 'CartCart',
        data () {
            return {
                select: false,
                checked: false,
                cartList:[],

                maxNumObj:{},
                goodList:[],
            }
        },
        created(){
            this.cartList = this.$cache.get("cartList");
            let token = this.getToken()
            let goodsId = '';
            if (this.cartList){
                for( var list of this.cartList ){
                    if(goodsId == ''){
                        goodsId = list.id
                    }else{
                        goodsId += ','+list.id
                    }
                }
            }
            let sign = this.getEncrypt("goods_id="+goodsId+"&token="+token);
            this.$http.post("/app.Goods/getinfo.html",{
                sign: sign,
                goods_id: goodsId,
                token: token
            }).then(response => {
                this.goodList = response.data.data;
                for (var list of this.goodList){
                    for (var item of list.spec){
                        this.maxNumObj[item.goods_id+','+item.goods_spec] = item.goods_stock - item.goods_sale;
                    }
                }

            }).catch(error => {
                console.log(error)
            })
        },
        methods:{
            // 单个商品选择
            single(key) {
                this.cartList[key].isSelect = !this.cartList[key].isSelect;
            },
            // 全选按钮
            allSelect() {
                if(this.cartList == ''){
                    Toast({
                        message: '请先将商品加入购物',
                        duration: 1500
                    });
                    return
                }else if (this.allChecked) {
                    if(this.cartList.length > 0){
                        this.cartList.forEach((item, index) => {
                            item.isSelect = false
                        })
                    }
                    return
                } else {
                    if(this.cartList.length > 0){
                        this.cartList.forEach((item, index) => {
                            item.isSelect = true
                        })
                    }
                    return
                }
            },
            add(index,id,goodsSpec){
                if(this.cartList[index].num >= this.maxNumObj[id+','+goodsSpec]){
                    Toast({
                        message: '数量超出范围',
                        duration: 1500
                    });
                    return;
                }
                this.cartList[index].num = parseInt(this.cartList[index].num) + 1;
                this.$cache.set('cartList',this.cartList)
            },
            less(index){
                if( this.cartList[index].num <= this.cartList[index].goods_purchase ){
                    Toast({
                        message: '该商品最低限购数为'+this.cartList[index].goods_purchase,
                        duration: 1500
                    });
                    return;
                }else{
                    this.cartList[index].num = parseInt(this.cartList[index].num) - 1;
                    this.$cache.set('cartList',this.cartList)
                    return;
                };
            },
            handleInput(index,id,goodsSpec){
                if(this.cartList[index].num > this.maxNumObj[id+','+goodsSpec]){
                    Toast({
                        message: '数量超出范围',
                        duration: 1500
                    });
                    this.cartList[index].num = this.maxNumObj[id+','+goodsSpec];
                    return
                }else if(this.cartList[index].num == '' || this.cartList[index].num <= this.cartList[index].goods_purchase){
                    this.cartList[index].num = this.cartList[index].goods_purchase;
                    return
                }else{
                    this.cartList[index].num = this.cartList[index].num.replace(/\b(0+)/gi,"");
                }
                this.$cache.set('cartList',this.cartList)
            },
            deleteCart(){
                let isSelect = false;
                for (var i = this.cartList.length - 1;i >= 0;i--) {
                    var index = this.cartList[i];
                    if (index.isSelect) {
                        this.cartList.splice(i,1);
                        isSelect = true;
                    }
                }
                if(isSelect){
                    Toast({
                        message: '删除成功！',
                        duration: 1500
                    });
                }else{
                    Toast({
                        message: '请选择需要删除的商品！',
                        duration: 1500
                    });
                }
                this.$cache.set('cartList',this.cartList)
            },
            OrderGenerated(){
                let OrderGeneratedArr = [];
                let OrderGeneratedIndexArr = [];
                for (var i = 0;i <= this.cartList.length - 1;i++) {
                    var index = this.cartList[i];
                    if (index.isSelect) {
                        OrderGeneratedArr.push(index);
                        OrderGeneratedIndexArr.push(i);
                    }
                }
                if (OrderGeneratedArr == ''){
                    Toast({
                        message: '请选择您要购买的商品',
                        duration: 1500
                    });
                }else {
                    this.$cache.set('placeOrder', OrderGeneratedArr);
                    this.$cache.set('placeOrderIndex', OrderGeneratedIndexArr);
                    this.$cache.delete('SiteIndex')
                    this.$cache.set('SiteIndex','');
                    this.$router.push({name:'SubmitOrder'})
                }
            }
        },
        computed:{
            //总价
            totalPrice(){
                if (!this.cartList) return 0
                let _price = 0;
                if(this.cartList.length > 0){
                    this.cartList.forEach((item) => {
                        if (item.isSelect) {
                            _price += Number(item.num) * Number(item.price)
                        }
                    })
                }
                return _price.toFixed(2);
            },
            //全选
            allChecked(){
                let _all = true;
                if (!this.cartList || this.cartList=='') return false;
                if(this.cartList.length > 0){
                    this.cartList.forEach((item) => {
                        if (!item.isSelect) {
                            _all = false
                        } else {
                            this.select = true
                        }
                    })
                }

                return _all;
            },
            cartAllNum(){
                let num;
                if(!this.cartList){
                    num = 0
                }else{
                    num = this.cartList.length;
                }
                return num;
            }
        }
    }
</script>
<style lang="scss" scoped>
    .cart-cart{
        padding-bottom:125px;
    }
    .title_bar{
        height:100px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 40px;
        border-bottom: 1px solid $c6-color;/*no*/
        p{
            font-size:26px;
        }
        .icon{
            font-size:50px;
            color: $c4-color;
        }
    }
    .item{
        height:150px;
        border-bottom:1px solid $c6-color;/*no*/
        display: flex;
        justify-content: center;
        padding: 35px 40px;
        .choose{
            margin-top:45px;
        }
        .image{
            width:150px;
            height:150px;
            margin:0 20px;
            img{
                border-radius: 5px;
            }
        }
        .info{
            flex:1;
            h4{
                font-size:26px;
                height:80px;
                line-height: 30px;
                p{
                    @include ellipsis;
                }
                h5{
                    @include ellipsis;
                    color: $c4-color;
                    font-size:24px;
                    margin-top:10px;
                }
            }
            .box{
                margin-top:10px;
                display: flex;
                justify-content: space-between;
                align-items:flex-end;
                height:60px;
                p{
                    color: #000;
                    font-size:26px;
                }
                .btn-group {
                    span{
                        background: #f2f2f2;
                        float:left;
                        margin-left:8px;
                        text-align: center;
                        color: #666;
                    }
                    .btn{
                        width:60px;
                        height:55px;
                        font-size:46px;
                        padding-top:5px;
                    }
                    .num{
                        height:60px;
                        float:left;
                        width:66px;
                        text-align: center;
                        border: none;
                        background: #f2f2f2;
                        padding:0;
                        margin-left:8px;
                        font-size:36px;
                    }
                }
            }
        }
    }
    .main-footer{
        position: fixed;
        left:0;
        bottom:0;
        width:100%;
        height:125px;
        background: #fff;
        box-shadow:$top-shadow;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &>div{
            padding:0 40px;
        }
        .choose{
            float: left;
            &:after{
                 content: '\2713';
                 color: #ccc;
                 -webkit-appearance: none;
             }
        }
        div label{
            font-size: 26px;
            margin-left: 10px;
            line-height: 40px;
        }
        .right{
            span{
                margin-right: 15px;
                i{
                    font-size: 40px;
                }
            }
            .btn-primary{
                width:150px;
                height:60px;
                line-height:60px;
                text-align: center;
                display: inline-block;
                background: $main-color;
                color: #fff;
                border: 0;
                font-size: 30px;
                border-radius: 4px;
            }
        }
    }
</style>



