<template>
    <div class="has-topbar">
        <div class="video" @click="playFn(videoUrl,videoPic,4000)" :style="{backgroundImage: 'url(' + videoPic + ')'}">
            <span class="icon-play"></span>
        </div>
        <div class="model" v-if="modelShow"></div>
        <Nav></Nav>
    </div>
</template>

<script>
    import Nav from '@/base/nav/nav'
    export default {
        components:{
            Nav
        },
        data () {
            return {
                token:'',
                videoPic:'',
                videoUrl:'',
                wrapperHeight:'',
                headTop: '',
                modelShow: false,
            }
        },
        created(){
            this.token = this.getToken();
            this.getIndexData();
        },
        mounted(){
            setTimeout(() => {
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar) {
                    this.wrapperHeight = document.documentElement.clientHeight - 34;
                    this.headTop = 44;
                    return
                } else {
                    this.wrapperHeight = document.documentElement.clientHeight;
                    this.headTop = 20;
                    return
                }
            }, 50)
        },
        methods:{
            getIndexData(){
                let sign =  this.getEncrypt('');
                this.$http.post('/app.Index/getinfo.html',
                    {
                        sign: sign,
                    })
                    .then(response => {
                        if(response.data.code == 1){
                            this.videoPic = response.data.data.video.pic;
                            this.videoUrl = response.data.data.video.url;
                            return
                        }
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },
            playFn(url,imgUrl = '',time){
                var _this = this;
                this.modelShow = true;
                var moviePlayer = api.require('moviePlayer');
                moviePlayer.open({
                    rect: {
                        x: 0,
                        y: 0,
                        w: api.frameWidth,
                        h: _this.wrapperHeight,
                    },
                    texts:{
                        title:'宇文玥',
                    },
                    styles: {
                        head: {
                            bg: 'rgba(16,206,255,0.5)',
                            height: 44,
                            y: _this.headTop,
                            title: {
                                size: 20,
                                color: '#fff',
                                width: 100,
                                leftMargin: 20
                            },
                            backSize: 20,
                            backImg: 'widget://image/back.png',
                        },
                        foot: {
                            bg: 'rgba(16,206,255,0.5)',
                            height: 44,
                            palyBtn: {
                                size: 25,
                                playImg: 'widget://image/play.png',
                                pauseImg: 'widget://image/pause.png',
                                marginLeft: 5
                            },
                            currentTimeLabel: {
                                textSize: 12,
                                textColor: "#FFF",
                                textWidth: 43,
                                marginLeft: 5
                            },
                            seekBar: {
                                sliderImg: 'widget://image/slder.png',
                                sliderW: 15,
                                sliderH: 8,
                                progressColor: '#696969',
                                progressSelected: '#fff',
                                marginLeft: 10,
                                marginRight: 10
                            },
                            totalTimeLabel: {
                                textSize: 12,
                                textColor: "#FFF",
                                textWidth: 43,
                                marginRight: 5
                            },
                            fullscreenBtn: {
                                size: 25,
                                verticalImg: 'widget://image/fullscreen.png',
                                horizontalImg: 'widget://image/unfullscreen.png',
                            }
                        }
                    },
                    path: url,
                    autoPlay: false,
                     coverlmg: imgUrl,
                }, function(ret, err) {
                    if (ret) {
                        if(time > 0){
                            moviePlayer.seekTo({
                                position: time,
                            });
                        }
                        moviePlayer.play();
                        moviePlayer.addEventListener(function(res){
                            if(res.eventType == 'back'){
                                moviePlayer.getCurrentPosition(function(ret1){
                                    alert(JSON.stringify(ret1.position.toFixed(0)));
                                });
                                moviePlayer.close();
                                _this.modelShow = false;
//                                alert("触发了back" )
                            }
                        })
                    }
                });
            },
        }
    }
</script>
<style lang="scss" scoped>
    .video{
        height: 350px;
        position:relative;
        background-size:contain;
        background-repeat: no-repeat;
        background-position: center center;
        span{
            position: absolute;
            top: 50%;
            left: 50%;
            margin: -50px 0 0 -50px;
            font-size:100px;
            color: #fff;
        }
    }
    .model{
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index:1001;
        background: #000;
    }
</style>



