<template>
    <div class="has-topbar">
    <div class="pay-choose bar_top">
        <!--修改新密码-->
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="getBack"></span>
            <p>{{payNewPasswordId | title}}</p>
        </div>
        <div class="list">
            <div class="header">
                <div v-if="importState == 1">
                    <p>输入<span>支付密码，</span>完成身份验证</p>
                </div>
                <div v-if="importState == 3|| importState == 2 || importState == 5">
                    <p>请为账号{{PhoneShow}}</p>
                    <p><span>设置6位数字支付密码</span></p>
                </div>
                <div v-if="importState == 4">
                    <p><span>请再次输入</span></p>
                </div>
            </div>
            <div class="key-list">
                <div class="key-input">
                    <div class="input-group">
                        <div><span v-if="passwordList.length>=1"></span></div>
                        <div><span v-if="passwordList.length>=2"></span></div>
                        <div><span v-if="passwordList.length>=3"></span></div>
                        <div><span v-if="passwordList.length>=4"></span></div>
                        <div><span v-if="passwordList.length>=5"></span></div>
                        <div><span v-if="passwordList.length>=6"></span></div>
                    </div>
                    <div v-if="importState == 3|| importState == 2" class="hint-info">支付密码不能是重复、连续的数字</div>
                    <button v-if="importState == 4" @click="amendNewPassword" :class="{'btn-main':passwordList.length >= 6}">下一步</button>
                </div>
                <div class="input-pan bottom-menu-fixed">
                    <div class="pan-group" v-for="(list,index) in panNumber" :key="index">
                        <div class="pan-num fast-click" v-for="(item,ind) in list" @click="inputPsd(item)" :key="ind">{{item}}</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        data () {
            return {
                codeId:'',
                payNewPasswordId:'',
                passwordList:[],
                PhoneShow:'',
                token:'',
                oldPassword:'',
                newPassword:'',
                verifyPassword:'',
                orderId:'',

                //输入状态
                importState:'',
                userVerify:'',
                identityCard:'',
                //键盘
                panNumber:[
                    ['1','2','3'],
                    ['4','5','6'],
                    ['7','8','9'],
                    ['','0','删除'],
                ],
            }
        },
        created(){
            this.token = this.getToken();
            this.payNewPasswordId = this.$route.params.codeId;
            if(this.payNewPasswordId == 5){
                this.orderId = this.$route.params.orderId;
            }
            if(this.payNewPasswordId == 2){
                this.userVerify = this.$route.params.userVerify;
                this.identityCard = this.$route.params.identityCard;
            }
            if(this.payNewPasswordId == 3 || this.payNewPasswordId == 5){
                this.userVerify = this.$route.params.userVerify;
            }
            this.importState = this.$route.params.codeId;
            this.getUserInfo();
        },
        methods:{
            getBack(){
                if(this.payNewPasswordId == 5){
                    this.$router.push(`/orderDetails/${this.orderId}`);
                }else{
                    this.$router.push(`/Set`);
                }
            },
            //获取用户手机号
            getUserInfo(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.Member/index.html',{
                    sign: sign,
                    token: this.getToken()
                }).then( response => {
                    if(response.data.code == 1){
                        this.PhoneShow = response.data.data.member.phonedeal;
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            //输入密码
            inputPsd(value){
                if(value == '删除'){
                    this.passwordList.pop()
                    return
                }else if(value == ''){
                    return
                }else{
                    if(this.passwordList.length < 6) {
                        this.passwordList.push(value);
                        if (this.passwordList.length == 6 && this.importState == 1) {
                            this.oldPassword = this.passwordList.join("")
                            this.verifyOldPassword();
                        }else if((this.importState == 2 || this.importState == 3 || this.importState == 5) && this.passwordList.length == 6){
                            this.newPassword = this.passwordList.join("");
                            this.importState = 4;
                            this.passwordList = [];
                        }else if(this.passwordList.length == 6 && this.importState == 4){
                            this.verifyPassword = this.passwordList.join("");
                        }
                    }
                }
            },
            //验证旧密码
            verifyOldPassword(){
                let sign = this.getEncrypt("password="+this.oldPassword+"&token="+this.token)
                this.$http.post("/app.member/checkPayCode.html",{
                    sign: sign,
                    token: this.token,
                    password: this.oldPassword
                }).then(res => {
                    this.passwordList = [];
                    if(res.data.code == 1){
                        this.importState = 2;
                    }
                    Toast({
                        message: res.data.msg,
                        duration: 1000
                    });
                    console.log(res.data.msg)
                })
            },
            //修改新密码
            amendNewPassword(){
                if(this.passwordList.length >= 6){
                    this.passwordList = [];
                    if(this.newPassword != this.verifyPassword){
                        this.importState = 2;
                        Toast({
                            message: '两次输入的密码不一致，请重新输入',
                            duration: 2000
                        });
                    }else if(this.payNewPasswordId == 1){
                        this.rememberPasswordAmend()
                    }else if(this.payNewPasswordId == 2){
                        this.forgetPasswordAmend()
                    }else if(this.payNewPasswordId == 3 || this.payNewPasswordId == 5){
                        this.setPassword(this.payNewPasswordId)
                    }
                }
            },
            //记得密码修改
            rememberPasswordAmend(){
                let sign = this.getEncrypt("confirmpassword="+this.verifyPassword+
                    "&newpassword="+this.newPassword+
                    "&password="+this.oldPassword+
                    "&token="+this.token
                )
                this.$http.post("/app.member/modifyPayCodePassword.html",{
                    sign: sign,
                    token: this.token,
                    password: this.oldPassword,
                    newpassword: this.newPassword,
                    confirmpassword: this.verifyPassword
                }).then(res => {
                    Toast({
                        message: res.data.msg,
                        duration: 2000
                    });
                    if(res.data.code == 1){
                        this.$router.push("/SetPassword")
                        return;
                    }else{
                        this.importState = 2;
                    }
                }).catch(err => {
                    console.log(err)
                })
            },
            //忘记密码修改
            forgetPasswordAmend(){
                let sign = this.getEncrypt("confirmpassword="+this.verifyPassword+
                    "&idcard="+this.identityCard+
                    "&password="+this.newPassword+
                    "&token="+this.token+
                    "&verify="+this.userVerify
                )
                this.$http.post("/app.member/resetPayCodePassword.html",{
                    sign: sign,
                    token: this.token,
                    verify: this.userVerify,
                    idcard: this.identityCard,
                    password: this.newPassword,
                    confirmpassword: this.verifyPassword
                }).then(res =>{
                    Toast({
                        message: res.data.msg,
                        duration: 2000
                    });
                    if(res.data.code == 1){
                        this.$router.push("/SetPassword")
                        return;
                    }else{
                        this.importState = 2;
                    }
                }).catch(err => {
                    console.log(err)
                })
            },
            //设置新密码
            setPassword(value){
                let sign = this.getEncrypt("confirmpassword="+this.verifyPassword+
                    "&password="+this.newPassword+
                    "&token="+this.token+
                    "&verify="+this.userVerify
                )
                this.$http.post("/app.Member/setPayCode.html",{
                    sign: sign,
                    token: this.token,
                    verify: this.userVerify,
                    password: this.newPassword,
                    confirmpassword: this.verifyPassword
                }).then(res => {
                    Toast({
                        message: res.data.msg,
                        duration: 2000
                    });
                    if(res.data.code == 1){
//                        this.$router.push("/SubmitOrder")
                        if(value == 3){
                            this.$router.push('/Set');
                            return
                        }else{
                            this.$router.push(`/orderDetails/${this.orderId}`);
                        }
                        return;
                    }else{
                        this.importState = 2;
                    }
                })
            }
        },
        filters:{
            title(valuer){
                if(valuer <= 2){
                    return "修改支付密码"
                }else{
                    return "设置支付密码"
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
.list{
    .header{
        padding: 60px 40px 40px;
        text-align: center;
        border-bottom: 1px solid $c6-color;/*no*/
        p{
            color: $c4-color;
            margin-bottom: 20px;
            font-size: 30px;
            span{
                color: #000;
            }
        }
    }
    .key-input{
        padding: 40px 40px 360px;
        /*display: flex;*/
        position: relative;
        .input-group{
            height: 80px;
            margin: 0 auto 20px;
            border-left: 1px solid $c6-color;/*no*/
            border-top: 1px solid $c6-color;/*no*/
            width: 480px;
            display: flex;
            justify-content: center;
            &>div{
                  width: 80px;
                  flex: 1;
                  box-sizing: border-box;
                  border-right: 1px solid $c6-color;/*no*/
                  border-bottom: 1px solid $c6-color;/*no*/
                  position: relative;
                span{
                    width: 20px;
                    height: 20px;
                    position: absolute;
                    top: 30px;
                    left: 30px;
                    background: #000;
                    border-radius: 50%;
                }
            }
        }
        .hint-info{
            text-align: center;
            color: $c4-color;
        }
        button{
            height: 80px;
            width: 100%;
            background: $c6-color;
            border: none;
            font-size: 30px;
            color: $c4-color;
            border-radius: 5px;
            margin-top: 40px;
            &.btn-main{
                background: $main-color;
                color: #fff;
             }
        }
    }
    .input-pan{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        .pan-group{
            display: flex;
            justify-content: center;
            align-items: stretch;
            height: 110px;
            border-top: 1px solid $c6-color;/*no*/
            box-sizing: border-box;
            .pan-num{
                flex: 1;
                text-align: center;
                line-height: 110px;
                font-size: 36px;
                box-sizing: border-box;
                border-right: 1px solid $c6-color;/*no*/
                &:last-child{
                     border:none;
                 }
                &:active{
                     background: $c6-color;
                 }
            }
        }
    }
}
</style>



