<template>
    <div class="has-topbar">
    <div class="wechat-binding bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>绑定微信</p>
        </div>
        <div class="con">
            <p class="title">{{ wxIsBinding | title}}</p>
            <button v-if="wxIsBinding == 0" @click="bindingWX" class="btn">绑定微信</button>
            <button v-if="wxIsBinding == 1" @click="messageBoxShow = true" class="btn">解绑微信</button>
        </div>
        <div class="message-box" v-if="messageBoxShow">
            <div class="confirm-box">
                <div>
                    <div class="header">
                        <h4>提示</h4>
                        <p>确定解除微信吗？</p>
                    </div>
                    <div class="btn-group">
                        <button class="btn" @click.stop="messageBoxShow = false">否</button>
                        <button @click.stop="unbindWX" class="btn btn-main">是</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        name: 'WechatBinding',
        data () {
            return {
                wxData:[],
                wxIsBinding :'',
                Pop:false,
                messageBoxShow: false,
            }
        },
        created(){
            this.getUserInfo();
        },
        methods:{
            bindingWX(){
                var wx = api.require('wx');
                var token = this.getToken();
                var _this = this;
                //检查有没有安装微信
                wx.isInstalled(function(ret, err) {
                    if (!ret.installed) {
                        Toast({
                            message: '当前设备未安装微信客户端',
                            duration: 1500
                        });
                    } else {
                        wx.auth(function(ret, err) {
//                            alert('test:'+JSON.stringify(ret)+','+ JSON.stringify(err))
                            if (ret.status) {
                                //获取微信的token
                                wx.getToken({
                                    code: ret.code
                                }, function(ret, err) {
//                                    alert('test2:'+JSON.stringify(ret)+','+ JSON.stringify(err))
                                    if (ret.status) {
                                        //获取微信用户的信息
                                        wx.getUserInfo({
                                            accessToken: ret.accessToken,
                                            openId: ret.openId,
                                            lang: 'zh_CN'
                                        }, function(res, err) {
                                            if (res.status) {
                                                _this.wxData = res;
                                                requestBindingWX()
                                            } else {
                                                alert(err.code);
                                            }
                                        });
                                    } else {
                                        alert(err.code);
                                    }
                                });
                            } else {
                                var codeArr = ['用户同意登录', '用户取消', '用户拒绝授权', '当前设备未安装微信客户端'];
                                if (err.code != -1) {
                                    Toast({
                                        message: codeArr[err.code],
                                        duration: 1500
                                    });
                                } else {
                                    // return msg.toast('未知错误');
                                    Toast({
                                        message: "未知错误",
                                        duration: 1500
                                    });
                                }
                            }
                        });
                    }
                });
                function requestBindingWX(){
                    //发送绑定请求
                    let appId = "wx5ebbf0c30f8fd02f"
                    let sign = _this.getEncrypt(
                        "appid="+appId+
                        "&headimgurl="+ _this.wxData.headimgurl+
                        "&nickname="+ _this.wxData.nickname+
                        "&openid="+ _this.wxData.openid+
                        "&sex="+ _this.wxData.sex+
                        "&token="+ token+
                        "&unionid="+ _this.wxData.unionid
                    )
                    _this.$http.post("/app.member/wxBinding.html",{
                        sign: sign,
                        token: token,
                        appid: appId,
                        openid: _this.wxData.openid,
                        nickname: _this.wxData.nickname,
                        sex: _this.wxData.sex,
                        headimgurl: _this.wxData.headimgurl,
                        unionid: _this.wxData.unionid
                    }).then(res => {
                        Toast({
                            message: res.data.msg,
                            duration: 1500
                        });
                        setTimeout(()=>{
                            document.location.reload();
                        },1000)
                    }).catch(err => {
                        alert(err)
                    })
                };
            },
            //获取用户信息
            getUserInfo(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.Member/index.html',{
                    sign: sign,
                    token: this.getToken()
                }).then( response => {
                    if(response.data.code == 1){
                        this.wxIsBinding = response.data.data.member.wxbinding;
                    }
                }).catch( error => {
                    console.log(error)
                })
            },

            //解除绑定微信
            unbindWX(){
                this.messageBoxShow = false;
                let sign = this.getEncrypt('token='+this.getToken());
                this.$http.post('/app.member/wxUntied.html',{
                    sign: sign,
                    token: this.getToken()
                }).then( response => {
                    Toast({
                        message: response.data.msg,
                        duration: 1500
                    });
                    setTimeout(()=>{
                        document.location.reload();
                    },1000)
                }).catch( error => {
                    console.log(error)
                })
            },
        },
        filters:{
            title(valuer){
                if(valuer == 1){
                    return "已绑定微信号"
                }else{
                    return "您还未绑定微信号"
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
.con{
    padding: 0 40px;
    font-size: 30px;
    text-align: center;
    .title{
        margin: 80px 0;
    }
    .btn{
        background: none;
        border: 1px solid $main-color;/*no*/
        border-radius: 5px;
        height:80px;
        width:100%;
        color: $main-color;
    }
}
.message-box{
    background: rgba(0,0,0,0.5);
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 2000;
    .confirm-box{
        position: absolute;
        min-height: 200px;
        margin-top: -120px;
        top:50%;
        width: 100%;
        box-sizing: border-box;
        padding:0 60px;
        &>div{
              background: #fff;
              min-height: 200px;
              border-radius: 10px;
              overflow: hidden;
            .header{
                min-height:120px;
                padding: 40px;
                box-sizing: border-box;
                text-algin: center;
                h4{
                    font-weight: bold;
                    font-size: 30px;
                }
                p{
                    font-size: 30px;
                    color: $c4-color;
                    margin-top: 20px;
                }
            }
            .btn-group{
                height:80px;
                box-sizing: border-box;
                border-top: 1px solid $c6-color;/*no*/
                display: flex;
                align-items: stretch;
                .btn{
                    flex: 1;
                    background: #fff;
                    border: none;
                    padding: 0;
                    font-size: 26px;
                }
                .btn-main{
                    color: $main-color;
                    border-left: 1px solid $c6-color;
                }
            }
        }
    }
}


</style>



