<template>
    <div class="has-topbar">
    <div class="photo-gallery" >
        <div class="photo_nav">
            <div class="nav_bar ">
                <router-link :to="tab.src" :class="{tab_active:index == tab_num }" v-for="(tab,index) in tabs" :key="index" @click="tab_active(index,tab.cateId)">
                    <i :class="tab.icon"></i><span>{{tab.title}}</span>
                </router-link>
            </div>
            <div class="photo_tab">
                <div :class="{'photo-active':index == photoTabNum }" v-for="(photoTab,index) in photoTabs" :key="index" @click="photoTabAcitve(index,photoTab.src)">{{photoTab.title}}</div>
            </div>
        </div>
        <keep-alive>
            <router-view/>
        </keep-alive>
        <Nav message="photo-gallery"></Nav>
    </div>
    </div>
</template>

<script>
    import Nav from '@/base/nav/nav';
    export default {
        components:{
            Nav
        },
        data () {
            return {
                tabs: [
                    {
                        title:'细胞圈',
                        icon:'icon-cell-circle',
                        src:'/Information/1',
                        cateId:5
                    },
                    {
                        title:'图片库',
                        icon:'icon-picture',
                        src:'',
                        cateId:5
                    }
                ],
                tab_num:1,
                photoTabs:[
                    {
                        title:'产品图',
                        src:'/PhotoGallery/PhotoProduct/0',
                        id:5
                    },
                    {
                        title:'文化图',
                        src:'/PhotoGallery/PhotoCulture/1',
                        id:4
                    },
                    {
                        title:'创意图',
                        src:'/PhotoGallery/PhotoIdeas/2',
                        id:3
                    },
                ],
                photoTabNum: 0,
            }
        },
        created(){
        },
        activated(){
            this.tab_num = 1;
            this.photoTabNum = this.$route.params.id;
        },
        methods:{
            photoTabAcitve(index,src){
                this.photoTabNum = index;
                this.$router.push(src)
            }
        }
    }
</script>
<style lang="scss" scoped>
.nav_bar{
    a{
        flex:1;
        text-align: center;
        font-size:36px;
        padding:20px;
        position:relative;
        color: $c4-color;
        &:first-child{
            border-right:1px solid $c6-color;/*no*/
        }
        &.tab_active{
            color: #000;
        }
        span{
            height:36px;
            display: inline-block;
            padding-left:50px;
        }
        i{
            font-size:36px;
            position: absolute;
            left:110px;
            top:22px;
            &.icon-cell-circle{
                 left:118px;
                 top:20px;
             }
        }
    }
}
.photo_tab{
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    height:100px;
    background: #fff;
    border-bottom:1px solid $c6-color;/*no*/
    font-size:36px;
    line-height: 100px;
    width:100%;
    z-index: 88;
    color: $c4-color;
    div{
        padding: 0 20px;
        &.photo-active{
             color: $main-color;
             border-bottom:3px solid $main-color;/*no*/
         }
    }
}
.photo-gallery{
    padding-top:200px;
    padding-bottom:125px;
}
.photo_nav{
    position: fixed;
    top:0;
    left:0;
    width:100%;
    padding-top: 100px;
    z-index:888;
    .nav_bar{
        position: absolute;
        top:0;
    }
}

</style>



