<template>
<div class="my-message">
    <div class="has-topbar">
    <div class="message-lists bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack('/My')"></span>
            <p>我的消息</p>
        </div>

        <div class="con">
            <div class="menu">
                <div class="list" @click="PathJump(1,'/MyInformation')">我的细胞圈 <i class="icon-right"></i></div>
                <div class="list" @click="PathJump(2,'/MessageLists')">评论 <i class="icon-right"></i></div>
            </div>
        </div>
    </div>
    </div>
</div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        data(){
            return{

            }
        },
        created(){

        },
        methods:{
            PathJump(index,url){
                if(index == 2){
                    let sign = this.getEncrypt('token='+this.getToken());
                    this.$http.post("/app.bbs/cancelunread.html",{
                        sign: sign,
                        token: this.getToken(),
                    }).then(res => {
                        console.log(res.data)
                        this.$router.push(url);
                    })
                    this.$router.push(url);
                    return
                }else{
                    this.$router.push(url);
                }
            },
        }
    }
</script>
<style lang="scss" scoped>
.menu{
    border-top: 1px solid $c6-color;/*no*/
    .list{
        height:100px;
        display: flex;
        padding:0 40px;
        line-height: 100px;
        justify-content: space-between;
        border-bottom: 1px solid $c6-color;/*no*/
        font-size: 26px;
        align-items: center;
        p{
            flex: 1;
            padding-left: 20px;
        }
        i{
            font-size:40px;
        }
    }
}
</style>


