<template>
    <div class="has-topbar">
        <div class="my-index">
            <div class="user">
                <div class="headimg">
                    <img v-if="member.headimg != ''" v-lazy="member.headimg" :key="member.headimg" alt="">
                </div>
                <div class="info">
                    <h4>{{member.nickname}}</h4>
                    <h5>{{member.typename}}</h5>
                    <p>ID：{{member.id}}</p>
                </div>
                <div class="btn-group">
                    <div @click.stop="toSet" to="" class="btn">设置</div>
                </div>
            </div>
            <div class="order">
                <div class="orderWrap">
                    <router-link class="toOrder" v-for="(list,index) in orderStatus" :class="{'active':orderNum[index+1] > 0}" :key="index" :to="list.url">{{list.name}}<span v-if="index != 3 && orderNum[index+1] !=''">({{orderNum[index+1]}})</span></router-link>
                </div>
            </div>
            <div class="menu">
                <div>
                    <router-link to="/Cart" class="list"><i class="icon-cart"></i><p>我的购物车</p><i class="icon-right"></i></router-link>
                    <router-link v-if="userType != 1 && isAuth == 1 && payGoods == 1" to="/Wallet" class="list"><i class="icon-wallet"></i><p>我的钱包</p><i class="icon-right"></i></router-link>
                    <router-link v-if="userType == 3" to="/Margin" class="list"><i class="icon-margin"></i><p>保证金</p><i class="icon-right"></i></router-link>
                    <router-link v-if="userType != 1  && isAuth == 1 && payGoods == 1" to="/Team/Development/0" class="list"><i class="icon-team"></i><p>我的团队</p><i class="icon-right"></i></router-link>
                    <router-link v-if="userType != 1 && isAuth == 1 && payGoods == 1" to="/Invite" class="list"><i class="icon-invite"></i><p>邀请好友</p><i class="icon-right"></i></router-link>
                    <router-link to="/MyMessage" class="list"><i class="icon-info"></i><p>我的消息<span v-if="bbsnum != '' || bbsnum != 0" class="label"></span></p><i class="icon-right"></i></router-link>
                    <router-link to="/Notice/2" class="list"><i class="icon-question"></i><p>常见问题</p><i class="icon-right"></i></router-link>
                </div>
            </div>
            <Nav></Nav>
        </div>
    </div>
</template>

<script>
import Nav from '@/base/nav/nav'
export default {
    components:{
        Nav
    },
    data(){
        return {
            member:{},
            userType:'',
            orderStatus:[
                {
                    name:'待付款',
                    url:'/order/1/PayOrder',
                },
                {
                    name:'待发货',
                    url:'/order/2/DeliverOrder',
                },
                {
                    name:'待收货',
                    url:'/order/3/TakeOrder',
                },
                {
                    name:'全部订单',
                    url:'/order/0/AllOrder',
                },
            ],
            page: 1,
            orderNum:[],
            token:'',
            isLogin:'',
            bbsnum:'',
            echatMsg:'',
            isAuth:'',
            payGoods:'',
        }
    },
    created(){
        this.token = this.getToken();
        this.userTypeFn();
//        this.serviceeEchatMsg();
        this.getUserInfo();
//        api.closeFrame({
//            name: 'cs'
//        });
    },
    methods:{
        //客服
//        serviceeEchatMsg(){
//            let sign = this.getEncrypt('token='+this.token);
//            this.$http.post("/app.Echat/echatmsg.html",{
//                sign: sign,
//                token: this.token,
//            }).then(res => {
//                if( res.data.code == 1){
//                    this.echatMsg = res.data.data.echatmsg
//                }
//            })
        //判断用户类型
        userTypeFn(){
            let sign = this.getEncrypt("token="+this.token);
            this.$http.post("/app.member/userinfo.html",{
                sign: sign,
                token: this.token,
            }).then(res => {
                if(res.data.code == 1){
                    this.userType = res.data.data.type;
                    this.isAuth = res.data.data.is_auth;
                    this.payGoods = res.data.data.pay_goods;
                    return
                }else{
                    this.userType = 1;
                    this.isAuth = 0;
                }
            })
        },
        getUserInfo(){
            let sign = this.getEncrypt('token='+this.token)
            this.$http.post('/app.Member/index.html',{
                sign: sign,
                token: this.token
            }).then( response => {
                if(response.data.code == 1){
                    this.member = response.data.data.member;
                    this.bbsnum = response.data.data.bbsnum;
                    this.orderNum = response.data.data.ordertotal;
                    this.userType = response.data.data.member.type;
                }else{
                    this.$router.push('./login')
                }
            }).catch( error => {
                console.log(error)
            })
        },
        toSet(){
            this.$router.push('/Set')
        },

    }
}
</script>

<style lang="scss" scoped>
.my-index{
    padding-bottom: 125px;
}
.user{
    padding:35px 20px;
    display: flex;
    justify-content: flex-start;
    /*align-items: center;*/
    .headimg{
        width:130px;
        height:130px;
        border-radius: 5px;
        overflow:hidden;
    }
    .info{
        flex:1;
        font-size: 28px;
        padding: 0 20px;
        h4{
            line-height: 40px;
            font-size: 32px;
        }
        h5{
            margin-top:30px;
            color: $c4-color;
        }
        p{
            color: $c4-color;
            margin-top:5px;
        }
    }
    .btn{
        width:100px;
        font-size: 30px;
        display: inline-block;
        text-align: center;
        line-height: 50px;
        border:1px solid $main-color;/*no*/
        color: $main-color;
        border-radius: 5px;
        margin-top:36px;
    }
}
.order{
    background: $c8-color;
    padding:20px 0;
    .orderWrap{
        height:100px;
        background: #fff;
        border-top:1px solid $c6-color;/*no*/
        border-bottom:1px solid $c6-color;/*no*/
        display: flex;
        justify-content: center;
        align-items: stretch;
        font-size: 24px;
        .toOrder{
            flex: 1;
            vertical-align: middle;
            color: $c4-color;
            line-height: 100px;
            text-align:center;
            &.active{
                color: $main-color;
             }
            &:last-child{
                border-left: 1px solid $c6-color;/*no*/
                padding: 0 20px;
                color: #000;
            }
        }
    }
}
.menu{
    border-top: 1px solid $c6-color;/*no*/
    .line{
        height:20px;
        background: $c8-color;
    }
    .service{
        border-top: 1px solid $c6-color;/*no*/
    }
    .list{
        height:100px;
        display: flex;
        padding:0 40px;
        line-height: 100px;
        border-bottom: 1px solid $c6-color;/*no*/
        font-size: 26px;
        align-items: center;
        justify-content: flex-start;
        position: relative;
        p{
            flex: 1;
            padding-left: 20px;
            position: relative;

        }
        i{
            font-size:40px;
        }
        .label{
            color: #fff;
            background: $color-red;
            /*position: absolute;*/
            right: 0;
            top: 32px;
            width: 14px;
            height: 14px;
            text-align: center;
            border-radius: 50%;
            display: inline-block;
            margin-left: 12px;
            margin-bottom: 12px;
        }
        /*.icon-right{*/
            /*font-size:40px;*/
            /*position: absolute;*/
            /*right:40px;*/
            /*top:32px;*/
            /*&.icon-cart{*/
                 /*top:28px;*/
             /*}*/
        /*}*/
    }
}
.headimg img{
    width: 100%
}
.customer{
    background: #fff;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 100;
}

</style>



