            <div class="nav_bar">
                <span class="icon-left fast-click" @click="getBack()"></span>
                <p>订单详情</p>
            </div>
            <div class="order-details-con">
                <div class="order-details-logistics">
                    <div class="order-details-logistics-top">
                        <p>{{orderDetails.status | statusP}}</p>
                        <span>{{orderDetails.status | statusSpan}}</span>
                    </div>
                    <div class="order-details-logistics-bottom">
                        <p>{{express.express_username}} <span>{{express.express_phone}}</span></p>
                        <p class="order-details-logistics-p">地址： {{express.express_province + express.express_city + express.express_area + express.express_address}}</p>
                    </div>
                </div>
                <i class="line"></i>
                <div class="order-details-num">
                    <div>
                        <router-link :to="{name:'Goods',  params: { id: goods.goods_id } }" class="order-details-num-li" v-for="(goods,index) in orderGoods" :key="index">
                            <div>
                                <div class="order-details-num-img">
                                    <img v-lazy="goods.goods_image" :key="goods.goods_image" alt="">
                                </div>
                                <div class="order-details-num-con">
                                    <h3>{{goods.goods_title}}</h3>
                                    <h5>{{goods.goods_spec}}</h5>
                                    <p>
                                        <span class="order-details-num-left">¥ {{goods.selling_price}}</span>
                                        <span class="order-details-num-right">数量：{{goods.number}}</span>
                                    </p>
                                </div>
                            </div>
                        </router-link>
                    </div>
                    <!--<p class="service"  @click="service()">-->
                        <!--联系客服-->
                    <!--</p>-->
                </div>
                <i class="line"></i>
                <div class="order-details-bottom">
                    <div class="order-details-bottom-time">
                        <h4>订单编号：{{orderDetails.order_no}}</h4>
                        <p>下单时间：{{orderDetails.create_at}}</p>
                    </div>
                    <div class="order-details-bottom-list">
                        <p>支付方式：在线支付</p>
                    </div>
                    <div class="order-details-bottom-list">
                        <p>商品总额：¥{{orderDetails.total_price}}</p>
                    </div>
                    <div v-if="orderDetails.status == 1" class="order-pay">
                        <div class="pay-list">
                            <h4>支付方式</h4>
                            <!-- <div @click="selectFn(1)" :class="['pay-item',{check_on: isSelect == 1}]"><p><i class="icon-integral"></i><span>积分支付</span></p><div :class="['choose',{check_on: isSelect == 1}]"></div></div> -->
                            <div v-if="orderGoods[0].is_need != 1" @click="selectFn(2)" :class="['pay-item',{check_on: isSelect == 2}]"><p><i class="icon-wechat"></i><span>微信支付</span></p><div :class="['choose',{check_on: isSelect == 2}]"></div></div>
                        </div>
                    </div>
                    <div class="order-details-bottom-btn">
                        <button v-if="orderDetails.status ==3" @click="StatusLeft" class="order-details-bottom-left">查看物流</button>
                        <button v-if="orderDetails.status != 0" @click="StatusRight(orderDetails.status)" class="order-details-bottom-right">{{orderDetails.status | orderStatusRight}}</button>
                    </div>
                </div>
            </div>
        </div>
        <!--支付功能-->
        <mt-popup  v-model="popupVisible" position="bottom" :closeOnClickModal="false">
            <div class="po-box has-padding-bottom">
                <div class="po-header"><i class="icon-cross" @click="popupVisible = false"></i><p>{{ popupTitleValue | popupTitle }}</p></div>
                <div v-if="!keyboardShow">
                    <h4>支付积分：{{orderDetails.total_price}}</h4>
                    <ul>
                        <li><span>支付方式</span><span>积分</span></li>
                        <li><span>可用积分</span><span>{{integral}}</span></li>
                    </ul>
                    <button @click="payment" class="btn bottom-menu-fixed">立即支付</button>
                </div>
                <div class="key-list" v-else>
                    <div class="key-input">
                        <div class="input-group">
                            <div><span v-if="passwordList.length>=1"></span></div>
                            <div><span v-if="passwordList.length>=2"></span></div>
                            <div><span v-if="passwordList.length>=3"></span></div>
                            <div><span v-if="passwordList.length>=4"></span></div>
                            <div><span v-if="passwordList.length>=5"></span></div>
                            <div><span v-if="passwordList.length>=6"></span></div>
                        </div>
                    </div>
                    <div class="input-pan bottom-menu-fixed">
                        <div class="pan-group" v-for="(list,index) in panNumber" :key="index">
                            <div class="pan-num fast-click" v-for="(item,ind) in list" @click="inputPsd(item)" :key="ind">{{item}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </mt-popup>
        <div class="message-box" v-if="messageBoxShow">
            <div class="confirm-box">
                <div>
                    <div class="header">
                        <h4>您还没有设置支付密码！</h4>
                        <p>是否要进入设置支付密码？</p>
                    </div>
                    <div class="btn-group">
                        <button class="btn" @click.stop="messageBoxShow = false">否</button>
                        <button @click.stop="setPassword" class="btn btn-main">是</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return{
            orderDetails:{},
            express:{},
            orderGoods:{},
            token:'',
            integral:'',
            orderId:'',
            //支付功能
            popupVisible:false,
            keyboardShow: false,
            popupTitleValue: 1,
            panNumber:[
                ['1','2','3'],
                ['4','5','6'],
                ['7','8','9'],
                ['','0','删除'],
            ],
            passwordList:[],
            messageBoxShow: false,
            setPayCode:'',
            isSelect:'2',
        }
    },
    created(){
        this.token = this.getToken();
        this.orderId = this.$route.params.id;
        this.getUserInfo();
        this.getOrderDetails();
//        api.closeFrame({
//            name: 'cs'
//        });
    },
    methods:{
        //获取订单详情
        getOrderDetails(){
            let sign = this.getEncrypt('order_id='+this.$route.params.id+'&token='+this.getToken());
            this.$http.post('/app.Order/get.html',{
                sign: sign,
                order_id: this.$route.params.id,
                token: this.getToken()
            }).then( res => {
                this.orderDetails = res.data.data.data[0]
                this.express = res.data.data.data[0].express
                this.orderGoods = res.data.data.data[0].goods
                this.integral = res.data.data.data[0].member.integral
            })
        },
        //设置密码
        setPassword(){
            this.$router.push({name:'ResetPassword',params:{value:'paySubNew',orderId: this.orderId}})
        },
        //客服
        service(){
            this.$router.push('/Service/1')
        },
        StatusLeft(){
            this.$router.push({name:'OrderTracking',params:{orderId:this.orderId}})
        },
        //支付方式选择方式
        selectFn(type){
            this.isSelect = type;
        },
        StatusRight(value){
            if(value == 1){
                if(this.isSelect == 1){
                    this.popupVisible=true;
                    this.keyboardShow = false;
                    this.popupTitleValue = 1;
                    this.passwordList = [];
                }else if(this.isSelect == 2){
                    //检查有没有安装微信
                    var _this = this;
                    var wx = api.require('wx');
                    wx.isInstalled(function(ret, err) {
                        if (!ret.installed) {
                            Toast({
                                message: '当前设备未安装微信客户端',
                                duration: 1500
                            });
                        } else {
                            //微信支付（app）接口
                            let sign = _this.getEncrypt("order_no="+_this.orderDetails.order_no+"&token="+_this.token)
                            _this.$http.post("/app.Order/wxpay.html",{
                                order_no: _this.orderDetails.order_no,
                                token: _this.token,
                                sign: sign
                            }).then( res => {
                                var wxPay = api.require('wxPay');
                                wxPay.payOrder({
                                    apiKey: res.data.data.appid,
                                    orderId: res.data.data.prepayid,
                                    mchId: res.data.data.partnerid,
                                    nonceStr: res.data.data.noncestr,
                                    timeStamp: res.data.data.timestamp,
                                    package: res.data.data.package,
                                    sign: res.data.data.sign,
                                }, function(ret, err) {
                                    if (ret.status) {
                                        //检查是否支付（微信支付）
                                        let checkPaySign = _this.getEncrypt("order_no="+_this.orderDetails.order_no+"&token="+_this.token)
                                        _this.$http.post("/app.Order/checkPay.html",{
                                            order_no: _this.orderDetails.order_no,
                                            token: _this.token,
                                            sign: checkPaySign
                                        }).then(res => {
                                            if( res.data.code == 1){
                                                Toast({
                                                    message: '支付成功',
                                                    duration: 2000
                                                });
                                                _this.$router.push("/order/2/DeliverOrder")
                                            }else{
                                                Toast({
                                                    message: res.data.msg,
                                                    duration: 2000,
                                                });
                                            }
                                        })
                                    } else {
                                        if (err.code == 1) {
                                            Toast({
                                                message: 'apiKey值非法',
                                                duration: 2000
                                            });
                                        } else if (err.code == -1) {
                                            Toast({
                                                message: '未知错误',
                                                duration: 2000
                                            });
                                        } else if (err.code == -2) {
                                            Toast({
                                                message: '用户取消',
                                                duration: 2000
                                            });
                                        }
                                        _this.$router.push("/order/1/PayOrder")
                                    }
                                });
                            }).catch( error =>{
                                alert(error)
                            })
                            return
                        }
                    });
                }
                return
//                return '立即支付'
            }else if(value == 2){
                this.remind()
                return
//                return '提醒发货'
            }else if(value == 3){
                this.notarize()
                return
//                return '确认收货'
            }else if(value == 4){
                this.$router.push({name:'OrderTracking',params:{orderId: this.$route.params.id}})
                return
//                return '查看物流'
            }
        },
        //确认收货
        notarize(){
            let sign = this.getEncrypt("order_no="+this.orderDetails.order_no+"&token="+this.token)
            this.$http.post("/app.Order/confirm.html",{
                sign: sign,
                order_no: this.orderDetails.order_no,
                token: this.token
            }).then( response => {
                if(response.data.code == 1){
                    if(response.data.msg == '确认收货成功！'){
//                        this.lists.splice(index,1);
                        this.$router.go(0)
                    }
                    Toast({
                        message: response.data.msg,
                        duration: 1000
                    });
                }
            })
        },
        //提醒发货
        remind(){
            let sign = this.getEncrypt("order_no="+this.orderDetails.order_no+"&token="+this.token)
            this.$http.post("/app.Order/remind.html",{
                sign: sign,
                token: this.token,
                order_no: this.orderDetails.order_no
            }).then( res => {
                Toast({
                    message: res.data.msg,
                    duration: 1000
                });
            })
        },
        //支付
        payment(){
            if(this.orderDetails.total_price > this.integral){
                Toast({
                    message: "可用积分不足，请先充值积分",
                    duration: 2000
                });
                return
            }else if(this.setPayCode == 0){
                this.popupVisible = false;
                this.messageBoxShow = true;
                return
            }else {
                this.popupTitleValue = 2;
                this.keyboardShow = true;
            }
        },
        //获取是否设置支付密码
        getUserInfo(){
            let sign = this.getEncrypt("token="+this.getToken());
            this.$http.post("/app.Member/index.html",{
                sign: sign,
                token: this.getToken(),
            }).then(res => {
                if(res.data.code == 1){
                    this.setPayCode = res.data.data.member.setpayceode
//                    console.log(this.setPayCode)
                }
            }).catch(err => {
                console.log(err)
            })
        },
        getBack(){
//            if(this.orderDetails.status == '1'){
//                this.goBack('/order/1/PayOrder')
//                return;
//            }else {
                this.goBack(-1)
//            }
        },
        //输入密码
        inputPsd(value){
            if(value == '删除'){
                this.passwordList.pop()
                return
            }else if(value == ''){
                return
            }else{
                if(this.passwordList.length < 6) {
                    this.passwordList.push(value);
                    if (this.passwordList.length == 6) {
                        let sign = this.getEncrypt("order_no="+this.orderDetails.order_no+"&password="+this.passwordList.join("")+"&token="+this.token)
                        this.$http.post("/app.Order/integralPay.html",{
                            sign: sign,
                            token: this.token,
                            order_no: this.orderDetails.order_no,
                            password: this.passwordList.join("")
                        }).then(res => {
                            if(res.data.code == 1){
//                                console.log("支付成功")
                                this.$router.push("/order/2/DeliverOrder")
                            }else{
                                this.passwordList = [];
                            }
                            Toast({
                                message: res.data.msg,
                                duration: 1000
                            });
                        })
                    }
                }
            }
        }
    },
    filters:{
        statusP(value){
            if(value == 1){
                return '待付款'
            }else if(value == 2){
                return '待发货'
            }else if(value == 3){
                return '待收货'
            }else if(value == 4){
                return '已完成'
            }else if(value == 0){
                return '已取消'
            }
        },
        statusSpan(value){
            if(value == 1){
                return '亲，您的订单还未付款哦！'
            }else if(value == 2){
                return '亲，您的订单我们会尽快送到您的手上，请耐心等候！'
            }else if(value == 3){
                return '亲，您的订单已发货，收到美物后记得确认收货哦！'
            }else if(value == 4){
                return '亲，您的订单交易已成功！'
            }else{
                return '亲，您的订单已取消！'
            }
        },
        orderStatusRight(value){
            if(value == 1){
                return '立即支付'
            }else if(value == 2){
                return '提醒发货'
            }else if(value == 3){
                return '确认收货'
            }else if(value == 4){
                return '查看物流'
            }
        },
        popupTitle(value){
            if(value == 1){
                return "确认支付"
            }else if(value == 2){
                return "输入密码"
            }
        }
    }
}
</script>

