<template>
    <div class="has-topbar">
    <div class="pay-choose bar_top">
        <!--修改支付密码选择-->
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>修改支付密码</p>
        </div>
        <div class="list">
            <div class="header">
                <p>您是否记得账号 <span>{{PhoneShow}}</span></p>
                <p>当前使用的支付密码</p>
            </div>
            <div class="btn-group">
                <router-link to="/ResetPassword/pay" class="btn">不记得</router-link>
                <router-link to="/PayNewPassword/1" class="btn btn-main">记得</router-link>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        data () {
            return {
                PhoneShow:'',
            }
        },
        created(){
            this.getUserInfo();
        },
        methods:{
            //获取用户手机号
            getUserInfo(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.Member/index.html',{
                    sign: sign,
                    token: this.getToken()
                }).then( response => {
                    if(response.data.code == 1){
                        this.PhoneShow = response.data.data.member.phonedeal;
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
        }
    }
</script>
<style lang="scss" scoped>
.list{
    .header{
        padding: 60px 40px 50px;
        text-align: center;
        border-bottom: 1px solid $c6-color;/*no*/
        p{
            color: $c4-color;
            margin-bottom: 10px;
            font-size: 26px;
            span{
                color: #000;
                font-size: 30px;
            }
        }
    }
    .btn-group{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 20px;
        margin-top: 40px;
        .btn{
            flex: 1;
            height: 60px;
            background: #fff;
            border: 1px solid $c6-color;/*no*/
            border-radius: 5px;
            margin: 0 20px;
            text-align: center;
            line-height: 60px;
            font-size: 26px;
        }
        .btn-main{
            background: $main-color;
            color: #fff;
        }
    }
}
</style>



