<template>
    <div class="has-topbar">
    <div class="my-margin bar_top">
        <div class="nav_bar">
            <span class="icon-left" @click="goBack('/My')"></span>
            <p>保证金</p>
            <span class="icon-question nav_right" @click="getInfo(15)"></span>
        </div>
        <div class="margin-con">
            <div class="top">
                <p><img src="@/common/images/icon-margin.png" alt="保证金"></p>
                <p>保证金：<span>￥{{marginData.price}}</span></p>
            </div>
            <div v-if="rgIsPay == 0" @click="PopSubmit=true" class="btn">提交保证金</div>
            <div v-if="rgIsPay != 0" @click="getInfo(16)" class="btn">保证金退款</div>
            <div v-if="rgIsPay == 3" @click="getInfo(17)" class="btn btn-star">星级经销商提交保证金</div>
            <div v-if="rgIsPay == 2" class="btn">保证金退款审核中</div>
        </div>

        <!--保证金说明-->
        <div v-show="PopProblem">
            <div class="pop-bg"  @click="PopProblem=false"></div>
            <div class="pop-wrap">
                <div class="pop">
                    <h3>{{infoData.title}}</h3>
                    <div class="box">
                        <p v-html="infoData.content"></p>
                    </div>
                    <div class="pop-btn-wrap">
                        <button @click="PopProblem=false">确定</button>
                    </div>
                </div>
            </div>
        </div>
        <!--保证金提交-->
        <div v-show="PopSubmit">
            <div class="pop-bg"  @click="PopSubmit=false"></div>
            <div class="pop-wrap">
                <div class="pop">
                    <h3>保证金提交选择</h3>
                    <div class="box ">
                        <div class="pay-list">
                            <div :class="['choose',{check_on: isSelect}]"></div>
                            <img src="@/common/images/wechat.png" alt="">
                        </div>
                    </div>
                    <div class="pop-btn-wrap PopRefund-btn-group">
                        <button @click="PayTheDeposit()">确定</button>
                        <div></div>
                        <button class="cancel" @click="PopSubmit=false">取消</button>
                    </div>
                </div>
            </div>
        </div>
        <!--星级经销商保证金提交-->
        <div v-show="PopStar">
            <div class="pop-bg"  @click="PopSubmit=false"></div>
            <div class="pop-wrap">
                <div class="pop">
                    <h3>{{infoData.title}}</h3>
                    <div class="box">
                        <p v-html="infoData.content"></p>
                    </div>
                    <div class="agreement">
                        <div :class="['choose',{check_on: isSelectStar}]"  @click="single"></div>
                        <div>请阅读并同意遵守星级经销商协议</div>
                        <!--<span @click="goBack('/Agreement/1')">用户协议</span> 和-->
                        <!--<span @click="goBack('/Agreement/2')">隐私条例</span>-->
                    </div>
                    <div class="pop-btn-wrap PopRefund-btn-group">
                        <button @click="PopStarFn()">确定</button>
                        <div></div>
                        <button class="cancel" @click="PopStar=false">取消</button>
                    </div>
                </div>
            </div>
        </div>
        <!--保证金退款-->
        <div v-show="PopRefund">
            <div class="pop-bg"  @click="PopRefund=false"></div>
            <div class="pop-wrap">
                <div class="pop">
                    <h3>{{infoData.title}}</h3>
                    <div class="box">
                        <p v-html="infoData.content"></p>
                    </div>
                    <div class="pop-btn-wrap PopRefund-btn-group">
                        <button @click="PayRefund()">确定</button>
                        <div></div>
                        <button class="cancel" @click="PopRefund=false">取消</button>
                    </div>
                </div>
            </div>
        </div>
        <!--取消保证金退款申请-->
        <div v-show="PopCancel">
            <div class="pop-bg"  @click="PopCancel=false"></div>
            <div class="pop-wrap">
                <div class="pop">
                    <h3>确定取消保证金退款？</h3>
                    <div class="pop-btn-wrap PopRefund-btn-group">
                        <button @click="PopCancel = false">确定</button>
                        <div></div>
                        <button class="cancel" @click="PopCancel=false">取消</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        name: 'MyTeam',
        data () {
            return {
                PopProblem: false,
                PopRefund: false,
                PopSubmit: false,
                PopCancel: false,
                PopStar: false,
                marginData:'',
                infoData:'',
                rgIsPay:'',
                isSelect: true,
                isSelectStar: false,
            }
        },
        created(){
            this.getData();
        },
        methods:{
            single(){
                this.isSelectStar = !this.isSelectStar;
            },
            //获取信息
            getData(){
                let token = this.getToken();
                let sign = this.getEncrypt('token='+token);
                this.$http.post('/app.Recognizance/index.html',{
                    sign: sign,
                    token: token
                }).then( res =>{
                    if(res.data.code == 1){
                        this.marginData = res.data.data;
                        this.rgIsPay = res.data.data.rg_is_pay;
                    }else{
                        Toast({
                            message: res.data.msg,
                            duration: 1500,
                        });
                    }
                })
            },
            getInfo(id){
                let token = this.getToken();
                let sign = this.getEncrypt('id='+id+'&token='+token);
                this.$http.post('/app.Placard/get.html',{
                    sign: sign,
                    token: token,
                    id: id,
                }).then( res =>{
                    if(res.data.code == 1){
                        this.infoData = res.data.data;
                        if(id == 15){
                            this.PopProblem = true
                        }else if( id == 16 ){
                            this.PopRefund = true
                        }else if(id == 17){
                            this.PopStar = true
                        }
                    }else{
                        Toast({
                            message: res.data.msg,
                            duration: 1500,
                        });
                    }
                })
            },
            //星级经销商
            PopStarFn(){
                if(this.isSelectStar == true){
                    this.PopStar = false;
                    this.PopSubmit = true;
                }else{
                    Toast({
                        message: "请阅读并同意遵守星级经销商协议",
                        duration: 2000
                    });
                }
            },
            //退款
            PayRefund(){
                let token = this.getToken();
                let sign = this.getEncrypt('token='+token);
                this.$http.post('/app.Recognizance/rgRefund.html',{
                    sign: sign,
                    token: token
                }).then( res =>{
                    Toast({
                        message: res.data.msg,
                        duration: 1500,
                    });
                })
            },
            //支付
            PayTheDeposit(){
                var _this = this;
                var wx = api.require('wx');
                wx.isInstalled(function(ret, err){
                    if (!ret.installed) {
                        Toast({
                            message: '当前设备未安装微信客户端',
                            duration: 1500
                        });
                    }else{
                        //微信支付（app）接口
                        let token = _this.getToken();
                        let sign = _this.getEncrypt('token='+token);
                        _this.$http.post('/app.Recognizance/rgOrder.html',{
                            sign: sign,
                            token: token
                        }).then( res =>{
                            var wxPay = api.require('wxPay');
                            wxPay.payOrder({
                                apiKey: res.data.data.appid,
                                orderId: res.data.data.prepayid,
                                mchId: res.data.data.partnerid,
                                nonceStr: res.data.data.noncestr,
                                timeStamp: res.data.data.timestamp,
                                package: res.data.data.package,
                                sign: res.data.data.sign,
                            }, function(ret, err) {
                                if (ret.status) {
                                    //检查是否支付（微信支付）
                                    let checkPaySign = _this.getEncrypt("order_no="+_this.orderDetails.order_no+"&token="+_this.token)
                                    _this.$http.post("/app.Order/checkPay.html",{
                                        order_no: _this.orderDetails.order_no,
                                        token: _this.token,
                                        sign: checkPaySign
                                    }).then(res => {
                                        if( res.data.code == 1){
                                            Toast({
                                                message: '支付成功',
                                                duration: 2000
                                            });
                                        }else{
                                            Toast({
                                                message: res.data.msg,
                                                duration: 2000,
                                            });
                                        }
                                        _this.PopSubmit = false;
                                    })
                                } else {
                                    _this.PopSubmit = false;
                                    if (err.code == 1) {
                                        Toast({
                                            message: 'apiKey值非法',
                                            duration: 2000
                                        });
                                    } else if (err.code == -1) {
                                        Toast({
                                            message: '未知错误',
                                            duration: 2000
                                        });
                                    } else if (err.code == -2) {
                                        Toast({
                                            message: '用户取消',
                                            duration: 2000
                                        });
                                    }
                                }
                            });
                            //微信支付end
                        })
                    }
                });
                //检查客户端end
            }
        }
    }
</script>
<style lang="scss" scoped>
    .agreement{
        display: flex;
        align-items: center;
        margin-top: 40px;
        font-size: 30px;
        div{
            margin-left: 10px;
            color: $c4-color;
        }
        span{
            margin: 0 10px;
            color: $main-color;
        }
    }
    .nav_bar{
        .nav_right{
            margin-right: 30px;
            font-size: 40px;
        }
        p{
            text-align: center;
        }
    }
    .margin-con{
        border-top: 15px solid #f5f5f5;
        padding: 80px 40px 0;
        .top{
            img{
                width: 80px;
                height: auto;
            }
            p{
                text-align: center;
                font-size: 28px;
                margin-top: 20px;
            }
        }
        .btn{
            margin-top: 100px;
            width: 100%;
            height: 90px;
            line-height: 90px;
            font-size: 30px;
            text-align: center;
            color: #fff;
            background: $main-color;
            &.btn-star{
                background: #fff;
                box-sizing: border-box;
                border:1px solid $main-color;/*no*/
                margin-top: 40px;
                color: $main-color;
            }
        }
    }
    //pop
    .pop-bg{
        position:fixed;
        top:0;
        bottom:0;
        width:100%;
        background-color:$color-background-d;
        z-index: 9999;
    }
    .pop-wrap{
        width: 550px;
        max-height: 700px;
        background-color: #fff;
        position: absolute;
        left: 50%;
        top: 50%;

        margin-left: -315px;
        margin-top: -315px;
        padding: 40px;
        z-index: 9999;
        .pop{
            width: 100%;
            h3{
                font-size: 36px;
                margin-bottom: 30px;
                text-align: center
            }
            .box{
                overflow: auto;
                max-height:500px;
                margin-bottom: 20px;
                line-height: 36px;
                P{
                    font-size: 26px;
                }
            }
            .pay-list{
                display: flex;
                justify-content: flex-start;
                align-items: center;
                padding-left: 20px;
                img{
                    height:100px;
                    margin-left: 10px;
                }
            }
            button{
                width: 100%;
                height: 56px;
                background-color: $main-color;
                color: #fff;
                border: 1px solid $main-color;/*no*/
                /*margin-right: 70px;*/
                /*margin-left: 16px;*/
                /*border-radius: 5px;*/
            }
            .PopRefund-btn-group{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding-top: 40px;
                div{
                    width: 40px;
                }
                .cancel{
                    background: #fff;
                    color: $main-color;
                }
            }
            .pop-btn{
                width: 168px;
                height: 54px;
                background-color: #fff;
                color: $main-color;
                margin: 0;
                border: 2px solid $main-color;
            }
        }
    }

</style>



