<template>
<div class="my-message">
    <div class="has-topbar">
    <div class="message-lists bar_top">
        <div class="nav_bar">
            <span class="icon-left" @click="goBack('/MyTourism')"></span>
            <p>{{title}}</p>
        </div>

        <div class="con">
            <div class="menu">
                <div class="list">
                    <p>当前团队等级：{{teamnum}}</p>
                    <p>当前状态：{{nowstatus}}</p>
                </div>
                <div class="tourism_con">
                    <div v-html="content"></div>
                </div>
            </div>
        </div>
    </div>
    </div>
</div>
</template>

<script>
    // import { Toast } from 'mint-ui';
    export default {
        data () {
            return {
                teamnum:'',
                nowstatus:'',
                title:'',
                content:''
            }
        },
        created(){
            this.getTeamData()
        },
        methods:{
            getTeamData(){
                let sign = this.getEncrypt("id="+this.$route.params.id+'&token='+this.getToken())
                this.$http.post('/app.Wallet/mytourism.html',{
                    sign: sign,
                    token: this.getToken(),
                    id:this.$route.params.id
                }).then( response => {
                    if(response.data.code == 1){
                        // console.log(response.data)
                        this.teamnum = response.data.data.leveltitle
                        this.nowstatus = response.data.data.nowstatus
                        this.title = response.data.data.project.title
                        this.content = response.data.data.project.content
                    }else{
                        
                    }
                }).catch( error => {
                    console.log(error)
                })
            }
        }
    }
</script>
<style lang="scss" scoped>
.menu{
    // border-top: 1px solid $c6-color;/*no*/
    .list{
        height:100px;
        display: flex;
        padding:0 20px;
        line-height: 100px;
        // border-bottom: 1px solid $c6-color;/*no*/
        font-size: 26px;
        justify-content: space-between;
        /*align-items: ;*/
        p{
            /*flex: 1;*/
            /*padding-left: 20px;*/
        }
        i{
            font-size:40px;
        }
    }
}
.con{
    padding:40px 40px;
    font-size:26px;
    line-height: 30px;
    word-wrap:break-word;
    .tourism_con{
        line-height: 60px;
        /*padding-top:20px;*/
        color: $c4-color;
    }
}
</style>


