<template>
    <div class="photo-culture" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <!--文化图-->
        <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText"
                     :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
            <div class="photo-list">
                <div class="item" v-for="(list,index) in lists" :key="index"
                     @touchstart="showDeleteButton(list.content.replace(/<br\s*\/?>/gi,'\r\n'))"
                     @touchmove="clearLoop()" @touchend="clearLoop()">
                    <p :class="{isOpen: list.is_open != 0}" ref="listP" v-html="list.content"></p>
                    <div class="btn" v-if="list.is_show"><span @click="isOpen(index)">{{unfold[list.is_open]}}</span>
                    </div>
                    <div class="img-group" v-if="list.images != ''">
                        <img :class="[{'thumbnail':list.images.length >1},{'sola':list.images.length == 1}]"
                             @click.stop="openImg(list.images,ind)" v-for="(item,ind) in list.images"
                             :key="item+'-EquaCompression'" v-lazy="item+'-EquaCompression'"/>
                    </div>
                    <div class="time">{{list.create_at}}</div>
                </div>
            </div>
            <div class="loadEnd" v-if="allLoaded">-- 已加载全部 --</div>
        </mt-loadmore>
    </div>

</template>

<script>
    import {Toast} from 'mint-ui';
    export default {
        data() {
            return {
                unfold: ["全文", "收起"],
                sign: "",
                page: 1,
                cateId: 4,
                lists: [],
                token: "",

                allLoaded: false,
                wrapperHeight: 0,
                autoFill: false,
                bottomPullText: "加载更多",
                topPullText: "刷新",
//      isRecharge: "",
//      isLogin: "",

                loop: ""
            };
        },
        created() {
            //            this.token = this.getToken()
            this.request();
            //            this.isRechargeFn();
        },
        activated() {
            this.token = this.getToken();
        },
        methods: {
            // 复制黏贴功能
            showDeleteButton(txt) {
                clearTimeout(this.loop);
                this.loop = setTimeout(
                    function () {
                        var clipBoard = api.require("clipBoard");

                        clipBoard.set(
                            {
                                value: txt
                            },
                            function (ret, err) {
                                if (ret) {
                                    Toast({
                                        message: "复制成功",
                                        duration: 1000
                                    });
                                } else {
                                    alert(JSON.stringify(err));
                                }
                            }
                        );
                    }.bind(this),
                    700
                );
            },
            clearLoop() {
                clearTimeout(this.loop);
            },

            //判断有没有充值
//    isRechargeFn() {
//      let sign = this.getEncrypt("token=" + this.token);
//      this.$http
//        .post("/app.Member/judgefield.html", {
//          token: this.token,
//          sign: sign
//        })
//        .then(res => {
//          if (res.data.code == 1) {
//            this.isRecharge = res.data.data.is_recharge;
//            this.isLogin = true;
//          } else {
//            this.isLogin = false;
//          }
//        });
//    },
            request() {
                let token = this.getToken();
                this.sign = this.getEncrypt(
                    "cate_id=" + this.cateId + "&page=" + this.page + "&token=" + token
                );
                this.$http
                    .post("/app.Picture/getinfo.html", {
                        sign: this.sign,
                        cate_id: this.cateId,
                        page: this.page,
                        token: token
                    })
                    .then(response => {
                        if (this.page == 1) {
                            this.lists = response.data.data.data;
                            this.lastPage = response.data.data.last_page;
                            if (this.page >= this.lastPage) {
                                this.allLoaded = true;
                            }
                            this.isOverFrom();
                        } else {
                            this.lists = this.lists.concat(response.data.data.data);
                            this.isOverFrom();
                        }
                    });
            },
            isOpen(index) {
                if (this.lists[index].is_open == 0) {
                    this.lists[index].is_open = 1;
                } else {
                    this.lists[index].is_open = 0;
                }
            },
            isOverFrom() {
                if (this.lists.length > 0) {
                    setTimeout(() => {
                        this.$refs.listP.forEach((e, index) => {
                            if (e.scrollHeight > 1.28 * window.rem) {
                                this.lists[index].is_show = true;
                            }
                        });
                    }, 100);
                }
            },
            loadBottom() {
                this.page += 1;
                if (this.page <= this.lastPage) {
                    this.request();
                } else {
                    this.allLoaded = true;
                }
                this.$refs.loadmore.onBottomLoaded();
            },
            loadTop() {
                this.page = 1;
                this.allLoaded = false;
                this.request();
                this.$refs.loadmore.onTopLoaded();
            }
        },
        mounted() {
            setTimeout(() => {
                let hasBottombar = document
                    .querySelector("body")
                    .classList.contains("has-bottombar");
                if (hasBottombar) {
                    this.wrapperHeight =
                        document.documentElement.clientHeight -
                        this.$refs.wrapper.getBoundingClientRect().top -
                        1.7 * window.rem -
                        34;
                    return;
                } else {
                    this.wrapperHeight =
                        document.documentElement.clientHeight -
                        this.$refs.wrapper.getBoundingClientRect().top -
                        1.7 * window.rem;
                    return;
                }
            }, 50);
        }
    };
</script>
<style lang="scss" scoped>
    .photo-culture {
        background: $c8-color;
        overflow: scroll;
        -webkit-overflow-scrolling: touch;
        position: relative;
    }

    .item {
        padding: 40px;
        position: relative;
        p {
            font-size: 24px;
            line-height: 32px;
            text-align: justify;
            color: $c4-color;
            max-height: 1.26rem;
            @include ellipsis-3h;
            &.isOpen {
                display: block;
                max-height: none;
            }
        }
        .btn {
            margin-top: 50px;
            font-size: 30px;
        }
        .img-group {
            margin-top: 20px;
            /*max-width:500px;*/
            width: 100%;
            .sola {
                width: 350px;
                height: 250px;
                object-fit: cover;
            }
            .thumbnail {
                display: inline-block;
                height: 200px;
                width: 32%;
                margin: 0 2% 2% 0;
                object-fit: cover;
            }
            .thumbnail:nth-of-type(3n) {
                margin-right: 0;
            }
        }
        .time {
            font-size: 22px;
            color: $c4-color;
            margin-top: 5px;
        }
    }

    .item {
        margin-top: 0;
        padding: 40px;
        background: #fff;
        margin-top: 20px;
        border-bottom: 1px solid $c6-color; /*no*/
        border-top: 1px solid $c6-color; /*no*/
        &:first-child {
            margin-top: 0;
            border-top: 0;
        }
    }

    .loadEnd {
        color: #999;
        text-align: center;
        font-size: 24px;
        padding: 20px 0 20px;
    }
</style>



