<template>
    <div class="has-topbar">
        <div class="set-userdata bar_top">
            <div class="nav_bar">
                <span class="icon-left fast-click" @click="goBack(-1)"></span>
                <p>{{setType?'修改昵称':'修改性别'}}</p>
                <div class="set-userdata-btn" @click="modifyUserInfo">保存</div>
            </div>
            <div class="new-userData">
                <input v-if="setType" type="text" placeholder="请输入昵称" v-model="userName">
                <div class="box"></div>
                <!--<select v-else v-model="userSex">-->
                    <!--<option value="男">男</option>-->
                    <!--<option value="女">女</option>-->
                <!--</select>-->
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            setType: '',
            userName: '',
            userSex: '男'
        }
    },
    created(){
        if(this.$route.params.id==1){
            this.setType = false
        }else{
            this.setType = true
        }
    },
    methods:{
        modifyUserInfo(){
            if(this.setType){
                if(this.userName){
                    let sign = this.getEncrypt('nickname='+this.userName+'&token='+this.getToken())
                    this.$http.post('/app.Member/update.html',{
                        sign: sign,
                        token: this.getToken(),
                        nickname: this.userName
                    }).then( response => {
                        if(response.data.code == 1){
                            this.$router.push('/Personage')
                        }
                    }).catch( error => {
                        console.log(error)
                    })
                }else{
                    alert('名字不能为空')
                }
            }else{
                let sign = this.getEncrypt('sex='+this.userSex+'&token='+this.getToken())
                this.$http.post('/app.Member/update.html',{
                    sign: sign,
                    token: this.getToken(),
                    sex: this.userSex
                }).then( response => {
                    if(response.data.code == 1){
                        this.$router.push('/Personage')
                    }
                }).catch( error => {
                    console.log(error)
                })
            }
        },
    }
}
</script>

<style lang="scss" scoped>
.set-userdata{
    .set-userdata-btn{
        font-size: 36px;
        margin-right: 40px;
        color: $main-color;
    }
    .new-userData{
        /*text-align: center;*/
        padding: 40px;
        input{
            width: 100%;
            height: 60px;
            border: none;
            padding:0;
            margin: 0;
            border-bottom: 1px solid $main-color;/*no*/
            font-size: 26px;
        }
    }
}
</style>



