<template>
    <div class="has-topbar">
    <div class="sign-in">
        <div class="nav"><i class="icon-left fast-click" @click="getBack"></i> 上一步</div>
        <div class="input-group">
            <label for="user">昵称:</label>
            <input id="user" type="text" v-model.trim="nickname">
        </div>
        <div class="input-group">
            <label for="password">输入密码:</label>
            <input id="password" class="input" type="password" v-model="password">
        </div>
        <div class="input-group">
            <label for="fix-psd">确认密码:</label>
            <input id="fix-psd" class="input" type="password" v-model="confirmPassword">
        </div>
        <div class="agreement">
            <div :class="['choose',{check_on: isSelect}]"  @click="single"></div>
            <div>登录注册表示同意</div>
            <span @click="goBack('/Agreement/1')">用户协议</span> 和
            <span @click="goBack('/Agreement/2')">隐私条例</span>
        </div>
        <div class="btn-group">
            <button class="btn btn-main" @click="signIn()">立刻注册</button>
        </div>
    </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return{
            nickname:'',
            password:'',
            confirmPassword:'',
            phone:'',
            isSelect: false,
        }
    },
    created(){
        this.phone = this.$route.params.phone;
        api.closeFrame({
            name: 'cs'
        });
    },
    methods:{
        single(){
            this.isSelect = !this.isSelect;
        },
        getBack(){
            this.goBack(-1);
            this.$cache.set("PhoneData",'');
        },
        signIn(){
            let reg = /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
            if(this.nickname == ''){
                Toast({
                    message: "请输入昵称",
                    duration: 2000
                });
                return;
            }else if(this.password == ''){
                Toast({
                    message: "请输入密码",
                    duration: 2000
                });
                return;
            }else if(this.confirmPassword == ''){
                Toast({
                    message: "请输入确认密码",
                    duration: 2000
                });
                return;
            }
            else if(this.password != this.confirmPassword){
                Toast({
                    message: "两次密码不一致，请重新输入",
                    duration: 2000
                });
                return;
            }else if( this.isSelect == false){
                Toast({
                    message: "请阅读并同意遵守用户协议",
                    duration: 2000
                });
                return;
            }else{
                let sign = this.getEncrypt(
                    "confirmpassword="+this.confirmPassword+
                    "&nickname="+this.nickname+
                    "&password="+this.password+
                    "&phone="+this.phone)
                this.$http.post("/app.member/register.html",{
                    sign: sign,
                    phone: this.phone,
                    nickname: this.nickname,
                    password: this.password,
                    confirmpassword: this.confirmPassword
                }).then( response => {
                    if(response.data.code == 1){
                        //登录请求
                        let loginSign = this.getEncrypt('password='+this.password+'&phone='+this.phone)
                        this.$http.post('/app.Member/login.html',{
                            sign: loginSign,
                            phone: this.phone,
                            password: this.password
                        }).then( response => {
                            if(response.data.code == 1){
                                this.setCookie(response.data.data.token,response.data.data.tokentime)
                                this.$cache.set("PhoneData",'');
                                this.$router.push('/Index')
                            }else{
                                Toast({
                                  message: response.data.msg,
                                  duration: 1500
                                });
                            }
                        }).catch( error => {
                            console.log(error)
                        })
                        Toast({
                            message: response.data.msg,
                            duration: 2000
                        });
                        this.$cache.set("appParam","");
                    }else{
                        Toast({
                            message: response.data.msg,
                            duration: 2000
                        });
                    }
                }).catch(error => {
                    console.log(error)
                })
            }
        },
        setCookie(token,tokentime){
            let newdate = new Date();
            newdate.setTime(tokentime * 1000);
            newdate = newdate.toUTCString()
            document.cookie = `mysteryCellToken=${token};expires=${newdate}`
        },
    }
}
</script>

<style lang="scss" scoped>
.agreement{
    display: flex;
    align-items: center;
    margin-top: 40px;
    font-size: 30px;
    div{
        margin-left: 10px;
        color: $c4-color;
    }
    span{
        margin: 0 10px;
        color: $main-color;
    }
}
.sign-in{
    padding:40px;
    .nav{
        font-size: 52px;
        color: $main-color;
        padding-bottom: 30px;
        i{
            font-size: 46px;
        }
    }
    .input-group{
        position: relative;
        margin-top: 110px;
        font-size: 30px;
        color: #ccc;
        height: 60px;
        .input{
            padding-left: 140px
        }
        label{
            position: absolute;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            line-height: 60px;
            top: 0px;
            left: 5px;
            i{
                font-size: 36px;
            }
        }
        input{
            height: 100%;
            box-sizing: border-box;
            width: 100%;
            font-size: 30px;
            border:0;
            padding-left: 80px;
            border-bottom: 1px solid $c6-color;/*no*/
        }
    }
    .btn-group{
        margin-top: 90px;
        .btn{
            width: 100%;
            height: 80px;
            font-size: 30px;
            background: #fff;
            border: 1px solid $main-color;/*no*/
            color: $main-color;
            border-radius: 5px;
        }
        .btn-main{
            background: $main-color;
            color: #fff;
            margin-bottom: 20px;
        }
    }
}
.message-box{
    background: #fff;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 99;
    .confirm-box{
        overflow: auto;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        &>div{
            background: #fff;
            .header{
                padding: 20px 20px 0;
                height:100%;
                box-sizing: border-box;
            }
            .box-btn-group{
                padding:20px 40px;
                .btn{
                    width: 100%;
                    line-height: 70px;
                    font-size: 30px;
                    text-align: center;
                    background: $main-color;
                    border-radius: 5px;
                    color: #fff;
                }
            }
        }
    }
}

</style>



