<template>
    <div class="has-topbar">
        <div class="bar_top wallet-pay">
            <div class="nav_bar">
                <div @click="getBack" class="icon-left fast-click"></div>
                <p>{{ id | Title}}</p>
            </div>
            <div class="con">
                <div v-html="list"></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            EchatUrl:'',
            list:'',
            id:'',
            headerH:'',
        }
    },
    created(){
        this.id = this.$route.params.id;
        if( this.id == 1){
            this.getAgreement();
        }else if( this.id == 2){
            setTimeout(()=>{
                this.getPrivacy();
            },50)
        }
    },
    mounted(){
        if (  screen.width == 1125 && screen.height === 2436){
            this.headerH = (1.334*window.rem) + 44;
            return;
        }else{
            this.headerH = (1.334*window.rem) + 20;
        }
    },
    filters:{
        Title(value){
            if(value == 1){
                return "用户协议";
            }else if(value == 2){
                return "隐私条例";
            }
        }
    },
    methods:{
        getPrivacy(){
            api.openFrame({
                name: 'cs',
                url: 'http://www.mysterycell.cn/user_agreement.html',
                rect: {
                    x: 0,
                    y: this.headerH,
                    w: 'auto',
                    h: 'auto'
                },
                bounces: false,
                reload: true,
                bgColor: '#fff'
            });
        },
        getBack(){
            this.goBack(-1);
            api.closeFrame({
                name: 'cs'
            });
        },
        getAgreement(){
            let sign = this.getEncrypt("");
            this.$http.post("/app.Data/agreement.html",{
                sign: sign
            }).then(res => {
                if(res.data.code == 1){
                    this.list = res.data.data
                }
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.con{
    padding: 40px;
}
</style>



