<template>
    <div class="has-topbar">
    <div class="order-tracking bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>物流详情</p>
        </div>
        <div class="con">
            <div class="goods">
                <div class="img-group">
                    <img v-lazy="goods.goods_image" :key="goods.goods_image" class="img"/>
                </div>
                <div class="info">
                    <h4>{{goods.goods_title}}</h4>
                    <h5>{{goods.goods_spec}}</h5>
                    <p>￥{{goods.selling_price}}</p>
                </div>
            </div>
            <div class="list" v-if="lists != ''">
                <div class="header">
                    <h4>物流公司：{{lists.express_title}}</h4>
                    <p>订单号码：{{lists.express_no}}</p>
                </div>
                <div class="item" v-for="(list,index) in lists.express" :key="index">
                    <div class="left"><span :class="{active:index == 0}"></span></div>
                    <div class="right">
                        <h4>{{list.accept_address}}</h4>
                        <p>{{list.accept_time}}</p>
                    </div>
                </div>
            </div>
            <div class="none" v-else>
                - 没有查询到物流信息 -
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                orderId: '',
                lists:[],
                goods:[],
                token:'',
            }
        },
        created(){
            this.orderId = this.$route.params.orderId;
            this.token = this.getToken();
            this.request();
        },
        methods:{
            request(){
                let sign = this.getEncrypt("order_id="+this.orderId+"&token="+this.token)
                this.$http.post("/app.Order/ordertracking.html",{
                    sign: sign,
                    token: this.token,
                    order_id: this.orderId
                }).then( res => {
                    if( res.data.code == 1){
                        let message = res.data.data
                        this.goods = message.goodslist;
                        this.lists = message.expresslist
                    }
                })
            }
        }
    }
</script>
<style lang="scss" scoped>
.con{
    background: $c8-color;
}
.goods{
    height:120px;
    padding: 40px;
    display: flex;
    background: #fff;
    border-bottom: 1px solid $c6-color;/*no*/
    .img-group{
        width: 120px;
        height: 120px;
        margin-right: 20px;
    }
    .info{
        flex: 1;
        position:relative;
        h4{
            font-size: 26px;
        }
        h5{
            font-size: 22px;
            color: $c4-color;
            margin-top: 10px;
        }
        p{
            position: absolute;
            bottom: 0;
            left: 0;
            font-size: 26px;
        }
    }
}
.list{
    margin-top: 20px;
    background: #fff;
    border-top: 1px solid $c6-color;/*no*/
    .header{
        font-size: 26px;
        padding: 40px 40px;
        border-bottom: 1px solid $c6-color;/*no*/
        h4{
            margin-bottom: 20px;
        }
    }
    .item{
        min-height: 120px;
        display: flex;
        justify-content: flex-start;
        .left{
            width: 65px;
            border-right: 1px solid $c6-color;/*no*/
            position: relative;
            span{
                width: 20px;
                height: 20px;
                background: $c6-color;
                position: absolute;
                right: -11px;
                border-radius: 50%;
                top: 50%;
                margin-top:-10px;
                &.active{
                    background: $main-color;
                }
            }
        }
        .right{
            padding: 20px 40px;
            flex:1;
            h4{
                font-size: 24px;
                line-height: 30px;
                margin-bottom: 10px;
            }
            p{
                font-size: 22px;
                color: $c4-color;
            }
        }
    }
}
.none{
    text-align: center;
    font-size: 30px;
    padding: 60px 0 0;
    background: #fff;
}
</style>



