<template>
    <div class="has-topbar">
        <div class="bill-details bar_top">
            <div class="nav_bar">
                <span class="icon-left fast-click" @click="goBack(-1)"></span>
                <p>{{withdrawId | title}}</p>
            </div>
            <div class="con">
                <h4 class="title">￥{{lists.money}}</h4>
                <div class="list">
                    <div class="item">
                        <p>当前状态</p>
                        <span>{{lists.status | status(withdrawId)}}</span>
                    </div>
                    <div v-if="lists.status>1" class="item">
                        <p>审核备注</p>
                        <span>{{lists.reply}}</span>
                    </div>
                    <div class="item">
                        <p>账单时间</p>
                        <span>{{lists.create_at}}</span>
                    </div>
                    <div v-if="lists.status>1" class="item">
                        <p>审核时间</p>
                        <span>{{lists.reply_at}}</span>
                    </div>
                    <div class="item">
                        <p>{{withdrawId | rechargeNo}}</p>
                        <span>{{lists.recharge_no}}</span>
                    </div>
                </div>
                <!--<div class="btn-group">-->
                    <!--<button class="btn" @click="service">联系客服</button>-->
                <!--</div>-->
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                withdrawId:'',
                withdrawNo:'',
                token:'',
                lists:[],
            }
        },
        created(){
            this.withdrawId = this.$route.params.id;
            this.withdrawNo = this.$route.params.no;
            this.token = this.getToken();
            this.getData();
//            api.closeFrame({
//                name: 'cs'
//            });
        },
        methods:{
            //获取数据
            getData(){
                let sign = this.getEncrypt("id="+this.withdrawNo+"&token="+this.token)
                if(this.withdrawId == 2){
                    this.$http.post("/app.integral/recordDetail.html",{
                        sign: sign,
                        token: this.token,
                        id: this.withdrawNo
                    }).then(res => {
                        this.lists = res.data.data;
                    })
                }else{
                    this.$http.post("/app.Wallet/withdrawDetail.html",{
                        sign: sign,
                        token: this.token,
                        id: this.withdrawNo
                    }).then(res => {
                        this.lists = res.data.data;
                    })
                }
            },
            //客服
//            service(){
//                this.$router.push('/Service/1')
//            },
        },
        filters:{
            title(value){
                if(value == 1){
                    return "账单详情"
                }else {
                    return "充值详情"
                }
            },
            rechargeNo(value){
                if(value == 1){
                    return "提现单号"
                }else {
                    return "充值单号"
                }
            },
            status(value,id){
                if(id == '1'){
                    if(value == 0){
                        return "待审核"
                    }else if(value == 1){
                        return "待打款"
                    }else if(value == 2){
                        return "提现成功"
                    }else if(value == 3){
                        return "审核不通过"
                    }
                }else{
                    if(value == 1){
                        return "审核中"
                    }else if(value == 2){
                        return "已通过"
                    }else if(value == 3){
                        return "审核不通过"
                    }
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
.con{
    .title{
        padding: 60px 0;
        text-align: center;
        font-size: 46px;
        border-bottom: 1px solid $c6-color;/*no*/
    }
    .list{
        border-bottom: 1px solid $c6-color;/*no*/
        .item{
            padding: 0 40px;
            min-height: 80px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            p{
                min-width: 120px;
            }
        }
    }
    .btn-group{
        padding: 60px 40px 0;
        .btn{
            width: 100%;
            height: 80px;
            background: $main-color;
            border: none;
            color: #fff;
            font-size: 30px;
            border-radius: 5px;
        }
    }
}
</style>



