<template>
    <div class="has-topbar">
    <div class="my-integral bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>我的积分</p>
        </div>
        <div class="wrapper"  ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
                <div class="header">
                    <div class="image"><img class="img" v-lazy="headimg" :key="headimg" alt=""></div>
                    <h4>
                        <h5>总积分:{{integral}}</h5>
                    </h4>
                    <div class="btn-group">
                        <router-link to="/Pay/1" class="btn">积分充值</router-link>
                    </div>
                </div>
                <div class="list">
                    <div class="liest-header">充值详情</div>
                    <div class="item" @click="goBillDetailds(list.id)" v-for="(list,index) in withdrawData" :key="index">
                        <div>
                            <h4>{{list.desc}}</h4>
                            <p>{{list.create_at}}</p>
                        </div>
                        <div class="money">
                            <p>￥{{list.money}}</p>
                            <span>{{list.way}}</span>
                        </div>
                    </div>
                </div>
                <div v-if="allLoaded && withdrawData != ''" class="load">- 已加载全部 -</div>
            </mt-loadmore>
            <div v-if="withdrawData == ''" class="load">- 暂无记录 -</div>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: 'MyIntegral',
        data () {
            return {
                headimg:'',
                integral:'',
                withdrawData:[],
                page: 1,
                lastPage:'',

                allLoaded: false,
                wrapperHeight: 0,
                autoFill:false,
                bottomPullText:'加载更多',
                topPullText:'刷新'
            }
        },
        created(){
            this.getUserInfo();
            this.getRecharge();
        },
        methods:{
            getUserInfo(){
                let sign = this.getEncrypt('token='+this.getToken())
                this.$http.post('/app.integral/hPage.html',{
                    sign: sign,
                    token: this.getToken()
                }).then( response => {
                    if(response.data.code == 1){
                        // console.log(response.data.data)
                        this.headimg = response.data.data.headimg
                        this.integral = response.data.data.integral
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            //上拉加载
            loadBottom() {
                this.page += 1;
                this.getRecharge()
                this.$refs.loadmore.onBottomLoaded();
            },
            //下拉刷新
            loadTop() {
                this.page = 1;
                this.getRecharge()
                this.$refs.loadmore.onTopLoaded();
            },
            //跳转数据详情
            goBillDetailds(no){
                this.$router.push({name:"BillDetails",params:{id:'2',no:no}})
            },
            //获取充值明细数据
            getRecharge(){
                let sign = this.getEncrypt('page='+this.page+'&token='+this.getToken())
                this.$http.post("/app.integral/rechargeRecord.html",{
                    sign: sign,
                    token: this.getToken(),
                    page: this.page,
                }).then(res => {
                    if(res.data.code == 1){
                        if(this.page == 1){
                            this.withdrawData = res.data.data.data;
                            this.lastPage = res.data.data.last_page;
                        }else{
                            this.withdrawData = this.withdrawData.concat(res.data.data.data);
                        }
                        if(this.page >= this.lastPage){
                            this.allLoaded = true;
                        }else{
                            this.allLoaded = false;
                        }
                    }
                }).catch(err => {
                    console.log(err)
                })
            },
        },
        mounted(){
            setTimeout(()=>{
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar){
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - 34;
                    return
                }else{
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
                    return
                }
            },50)
        },
    }
</script>
<style lang="scss" scoped>
.wrapper{
    overflow:scroll;
    -webkit-overflow-scrolling: touch;
    position: relative;
}
.load{
    line-height: 60px;
    text-align: center;
    font-size:26px;
    background: $c8-color;
}
.header{
    padding: 40px;
    border-bottom: 1px solid $c6-color;
    .image{
        width: 130px;
        height:130px;
        border-radius: 5px;
        overflow: hidden;
        margin: 0 auto 40px;
    }
    h4{
        font-size: 35px;
        flex: 1;
        padding: 0;
        text-align: center;
    }
}

.list{
    background: $c8-color;
    padding-top: 20px;
    .item{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 120px;
        background: #fff;
        padding: 0 40px;
        border-bottom: 1px solid $c6-color;/*no*/
        font-size: 26px;
        &:first-child{
             border-top: 1px solid $c6-color;/*no*/
        }
        i{
            font-size:40px;
        }
        p{
            color: $c4-color;
            margin-top:5px;
        }
        h4{
            margin-bottom: 10px;
        }
        .money{
            p{
                font-size: 30px;
                text-align: right;
                color: $color-text;
            }
            span{
                float: right;
                font-size: 20px;
                margin-top: 10px;
                color: $c4-color;
            }
        }
    }
    .liest-header{
        height: 120px;
        line-height: 120px;
        background: #fff;
        font-size: 30px;
        border-bottom: 1px solid $c6-color;/*no*/
        padding: 0 40px;
    }
}

.btn-group{
    background: #fff;
    padding: 80px 0 0;
    .btn{
        line-height:80px;
        text-align: center;
        display: block;
        color: #fff;
        background: $main-color;
        border-radius: 5px;
        font-size: 30px;
    }
}

</style>



