<template>
    <div class="has-topbar">
    <div class="withdraw-details bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>{{withdrawId | title}}</p>
        </div>
        <div class="wrapper"  ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
                <div class="list" v-for="(list,index) in withdrawData" :key="index">
                    <div class="header" v-if="list.year != withdrawData[0].year || index == 0">{{list.year}}年</div>
                    <!--<div>{{withdrawData[0].year}} {{list.year}}</div>-->
                    <div class="item" @click="goBillDetailds(item.id)" v-for="(item,ind) in list.list" :key="ind">
                        <div>
                            <h4>{{item.desc}}</h4>
                            <p>{{item.create_at}}</p>
                        </div>
                        <div class="money">
                            <p>￥{{item.money}}</p>
                            <span>{{item.way}}</span>
                        </div>
                    </div>
                </div>
                <div v-if="allLoaded && withdrawData != ''" class="load">- 已加载全部 -</div>
            </mt-loadmore>
            <div v-if="withdrawData == ''" class="load">- 暂无记录 -</div>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: 'WithdrawDetails',
        data () {
            return {
                withdrawData:{},
                withdrawId:'',
                page:1,
                lastPage: '',

                allLoaded: false,
                wrapperHeight: 0,
                autoFill:false,
                bottomPullText:'加载更多',
                topPullText:'刷新'
            }
        },
        created(){
            this.withdrawId = this.$route.params.id
            this.getWithdrawDetails();
        },
        methods:{
            //初始数据获取
            getWithdrawDetails(){
                if(this.withdrawId == 1){
                    this.getWithdrawCash()
                    return
                }else if(this.withdrawId == 2){
                    this.getRecharge()
                }
            },
            //上拉加载
            loadBottom() {
                this.page += 1;
                this.getWithdrawCash()
                this.$refs.loadmore.onBottomLoaded();
            },
            //下拉刷新
            loadTop() {
                this.page = 1;
                this.getWithdrawCash()
                this.$refs.loadmore.onTopLoaded();
            },
            //跳转数据详情
            goBillDetailds(no){
                this.$router.push({name:"BillDetails",params:{id:this.$route.params.id,no:no}})
            },
            //获取提现明细数据
            getWithdrawCash(){
                let sign = this.getEncrypt('page='+this.page+'&token='+this.getToken())
                this.$http.post('/app.Wallet/withdrawCashRecord.html',{
                    sign: sign,
                    token: this.getToken(),
                    page: this.page,
                }).then( res => {
                    if(res.data.code == 1){
                        if(this.page == 1){
                            this.withdrawData = res.data.data.data;
                            this.lastPage = res.data.data.last_page;
                        }else{
                            this.withdrawData = this.withdrawData.concat(res.data.data.data);
                        }
                        if(this.page >= this.lastPage){
                            this.allLoaded = true;
                        }else{
                            this.allLoaded = false;
                        }
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
        },
        mounted(){
            setTimeout(()=>{
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar){
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - 34;
                    return
                }else{
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
                    return
                }
            },50)
        },
        filters:{
            title(value){
                if(value == 1){
                    return "提现明细"
                }else if(value == 2){
                    return "充值明细"
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
.wrapper{
    overflow:scroll;
    -webkit-overflow-scrolling: touch;
    position: relative;
}
.header{
    height: 100px;
    line-height: 100px;
    padding:0 40px;
    background: $c8-color;
    border-bottom: 1px solid $c6-color;/*no*/
    font-size: 26px;
}
.item{
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 40px;
    border-bottom: 1px solid $c6-color;/*no*/
    font-size: 26px;
    p{
        color: $c4-color;
        margin-top:5px;
    }
    h4{
        margin-bottom: 10px;
    }
    .money{
        // font-size: 30px;
        p{
            font-size: 30px;
            text-align: right;
            color: $color-text;
        }
        span{
            float: right;
            font-size: 20px;
            margin-top: 10px;
            color: $c4-color;
        }
    }
}
.load{
    line-height: 60px;
    text-align: center;
    font-size:26px;
    background: $c8-color;
}
</style>



