<template>
    <div class="has-topbar">
    <div class="bonus-details bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>奖金明细</p>
            <!-- <div class="question" @click="question">
                <i class="icon-question"></i>
            </div> -->
        </div>
        <div class="wrapper"  ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
                <div class="totalbonus">
                    <p>¥{{totalbonus}}</p>
                    <span>累计总金额</span>
                </div>
                <ul>
                    <li class="list" v-for="(bonuslist,index) in bonuslists" :key="index">
                        <div class="left">
                            <div class="image">
                                <img class="img" v-lazy="bonuslist.headimg" :key="bonuslist.headimg" alt=""/>
                            </div>
                            <h4>{{bonuslist.nickname}}</h4>
                            <p>{{bonuslist.create_at}}</p>
                        </div>
                        <div class="right">
                            <h4>￥{{bonuslist.money}}</h4>
                            <p>返利金额</p>
                        </div>
                    </li>
                </ul>
                <div v-if="allLoaded" class="load">- 已加载全部 -</div>
            </mt-loadmore>
        </div>

        <!-- <transition name="fade">
            <div v-show="Pop">
                <div class="pop-bg"  @click="Pop=false"></div>
                <div class="pop-wrap">
                    <div class="pop">
                        <h3>{{rule.title}}</h3>
                        <div class="box">
                            <p v-html="rule.content"></p>
                        </div>
                        <div class="pop-btn-wrap">
                            <button @click="Pop=false">确定</button>
                        </div>
                    </div>
                </div>
            </div>
        </transition> -->
    </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
    export default {
        name: 'BonusDetails',
        data () {
            return {
                bonuslists:[],
                bonusdetailType:'',
                totalbonus:'',
                Pop:false,
                rule: {},

                page:1,
                lastPage:'',
                allLoaded: false,
                wrapperHeight: 0,
                autoFill:false,
                bottomPullText:'加载更多',
                topPullText:'刷新',
            }
        },
        created(){
//            this.bonusdetailTypeOf()
            this.getData()
            this.reward()
        },
        methods:{
            reward(){
                let sign = this.getEncrypt('id='+(this.$route.params.id+8)+'&token='+this.getToken())
                this.$http.post('/app.Placard/get.html',{
                    sign: sign,
                    token: this.getToken(),
                    id: (this.$route.params.id+8)
                }).then( response => {
                        this.rule = response.data.data
                        // console.log(this.rule)
                }).catch( error => {
                    console.log(error)
                })
            },
            question(){
                this.Pop = true;
            },
            //上拉加载
            loadBottom() {
                this.page += 1;
                this.getData()
                this.$refs.loadmore.onBottomLoaded();
            },
            //下拉刷新
            loadTop() {
                this.page = 1;
                this.getData()
                this.$refs.loadmore.onTopLoaded();
            },

            getData(){
                let sign = this.getEncrypt('page='+this.page+'&token='+this.getToken())
                console.log()
                this.$http.post('/app.Wallet/bonusdetail.html',{
                    sign: sign,
                    token: this.getToken(),
                    // type: this.$route.params.id,
                    page: this.page,
                }).then( response => {
                    if(response.data.code == 1){
                        this.lastPage = response.data.data.bonuslist.last_page;
                        if(this.page >= this.lastPage){
                            this.allLoaded = true;
                        }else{
                            this.allLoaded = false;
                        }
                        if(this.page == 1){
                            this.bonuslists = response.data.data.bonuslist.data;
                            this.totalbonus = response.data.data.totalbonus;
                            return
                        }else if(this.page >1){
                            this.bonuslists = this.bonuslists.concat(response.data.data.bonuslist.data);
                            return
                        }
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
        },
        mounted(){
            setTimeout(()=>{
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar){
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - 34;
                    return
                }else{
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
                    return
                }
            },50)
        },
        filters:{
            myfilters(value){
                if(value == 1){
                    return '领取奖励'
                }else{
                    return '暂无领取资格'
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
.wrapper{
    overflow:scroll;
    -webkit-overflow-scrolling: touch;
    position: relative;
}
.load{
    line-height: 60px;
    text-align: center;
    font-size:26px;
    background: $c8-color;
}
.list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 150px;
    background: #fff;
    border-bottom: 1px solid $c6-color;/*no*/
    padding: 0 40px;
    .left{
        position:relative;
        padding-left: 100px;
        .image{
            position: absolute;
            left:0;
            top:0;
            z-index: 2;
            width:80px;
            height:80px;
            overflow: hidden;
            border-radius: 5px;
            background: #000;
        }
        h4{
            font-size: 26px;
            line-height: 28px;
        }
        p{
            color: $c4-color;
            font-size: 22px;
            margin-top:30px;
        }
    }
    .right{
        h4{
            font-size: 36px;
        }
        p{
            font-size: 26px;
            color: $c4-color;
            text-align: right;
            margin-top: 16px;
        }
    }
}
.totalbonus{
    width: 100%;
    // height: 350px;
    text-align: center;
    border-bottom: 1px solid $c6-color;/*no*/
    padding-top: 58px;
    box-sizing: border-box;
    padding-bottom: 30px;
    h3{
        font-size: 50px;
        margin-bottom: 58px;
    }
    p{
        font-size: 58px;
        /*margin-top: 80px;*/
        margin-bottom: 20px;
    }
    span{
        font-size: 36px;
        color: $c4-color;
    }
}

.management{
    .management_con{
        width: 400px;
        height: 200px;
        margin: 40px auto;
        position: relative;
        .management_con_left{
            position: absolute;
            left: -60px;
            bottom: -60px;
            font-size: 36px;
        }
        .management_con_right{
            position: absolute;
            right: -60px;
            bottom: -60px;
            font-size: 36px;
        }
        .management_con_img{
            width: 360px;
            margin: 20px auto;
            img{
                width: 100%;
            };
            canvas{
                display: none;
            }
        }
        .management_con_text{
            text-align: center;
            p{
                font-size: 40px;
                position: absolute;
                text-align: center;
                min-width:180px;
                left: 50%;
                top: 50%;
                margin-left: -90px;
                margin-top: 30px;
                .management_con_span{
                    font-size: 50px;
                    color: $main-color;
                }
            }
        }
    }
    
}
.teamAwardWrap{
    border: 0;
    .teamAward{
        li{
            width: 100%;
            // height: 600px;
            text-align: center;
            // border-bottom: 1px solid $c6-color;
            img{
                width: 500px
            }
            h3{
                font-size: 22px;
                margin-bottom: 20px;
            }
            p{
                font-size: 30px;
                color: $c4-color;
            }
            .btn-group{
                width: 660px;
                height: 80px;
                margin: 0 auto;
                margin-top: 30px;
                margin-bottom: 30px;
                background-color: $main-color;
                border-radius: 5px;
                .btn{
                    height: 80px;
                    line-height: 80px;
                    color: #fff;
                    font-size: 30px;
                }
            }
            .is-btn-group{
                background-color:$c4-color
            }
        }
    }
}
.pop-wrap{
    width: 550px;
    max-height: 700px;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;

    margin-left: -315px;
    margin-top: -315px;
    padding: 40px;
    z-index: 9999;
    .pop{
        width: 100%;
        h3{
            font-size: 36px;
            margin-bottom: 30px;
            text-align: center
        }
        .box{
            overflow: auto;
            max-height:500px;
            margin-bottom: 20px;
            line-height: 36px;
            P{
                font-size: 26px;
            }
        }
        button{
            width: 100%;
            height: 56px;
            background-color: $main-color;
            color: #fff;
            border: 1px solid $main-color;/*no*/
            /*margin-right: 70px;*/
            /*margin-left: 16px;*/
            border-radius: 5px;
        }
        .pop-btn{
            width: 168px;
            height: 54px;
            background-color: #fff;
            color: $main-color;
            margin: 0;
            border: 2px solid $main-color;
        }
    }
}
.pop-bg{
    position:fixed;
    top:0;
    bottom:0;
    width:100%;
    background-color:$color-background-d;
    z-index: 9999;
}
button{
    border:0px;
    background-color:transparent
}
.description{
    margin-top: 100px;
    text-align: center;
    button{
        width: 170px;
        height: 56px;
        background-color: $main-color;
        color: #fff;
        // margin-right: 70px;
        // margin-left: 16px;
        border-radius: 5px;
    }
}
.question{
    margin-right: 40px;
}
</style>



