<template>
    <div class="has-topbar">
    <div class="message-lists bar_top">
        <div class="nav_bar">
            <span class="icon-left fast-click" @click="goBack(-1)"></span>
            <p>消息列表</p>
        </div>
        <div class="wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
        <div class="con">
            <div class="list" :class="{'first':index == 0}" v-for="(list,index) in lists" :key="index">
                <div class="list-top">
                    <div class="top-box">
                        <div class="img-group">
                            <img class="img" v-lazy="list.headimg" :key="list.headimg" alt=""/>
                        </div>
                        <div class="user">
                            <h4 v-html="list.username"></h4>
                            <p v-html="list.create_at"></p>
                        </div>
                        <div>
                            <button class="btn" @click="commentFn(list.type,list.comm_id,list.mid,list.precord_id,list.id)">回复</button>
                        </div>
                    </div>
                    <div class="info"><span v-if="list.from_username !=''">回复<span class="name" v-html="list.from_username"></span>：</span>{{list.content}}</div>
                </div>
                <div class="item">
                    <div  v-if="list.type != '1'" class="info">
                        <span v-if="list.front_from_nickname != ''"><span class="name">{{list.front_from_nickname}}</span> 回复</span>
                        <span class="name">{{list.front_nickname}}</span>：{{list.front_content}}
                    </div>
                    <div class="item-box" @click="getBack(list.precord_id)">
                        <div class="img-group" v-if="list.pimages != ''" :style="{backgroundImage: 'url(' + list.pimages + ')'}"></div>
                        <div class="img-group" v-else :style="{backgroundImage: 'url(' + list.pheadimg + ')'}"></div>
                        <div class="user">
                            <h4>{{list.pusername}}</h4>
                            <p>{{list.pcontent}}</p>
                        </div>
                    </div>
                </div>
            </div>
            <div class="loadEnd" v-if="allLoaded">- 已加载全部 -</div>
        </div>
        </mt-loadmore>
        </div>
        <!--写评论-->
        <div v-if="commentBoxShow" class="comment-box has-fixed-top">
            <div class="nav_bar">
                <div @click="commentBoxShow = false">取消</div>
                <div class="title">发布评论</div>
                <div><span class="btn" @click="submitComment(typeNum)">发送</span></div>
            </div>
            <div class="text-area">
                <textarea rows="8" placeholder="写评论" v-model="CommentInfo"></textarea>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    export default {
        name: 'MessageLists',
        data () {
            return {
                page:1,
                lists:[],
                commentBoxShow: false,
                CommentInfo:'',
                sign:'',
                commentId:'',
                fromMid:'',
                commReplyId:'',
                recordId:'',
                token:'',
                typeNum:'',
                //加载
                allLoaded: false,
                wrapperHeight: 0,
                autoFill:false,
                bottomPullText:'加载更多',
                topPullText:'刷新'
            }
        },
        created(){
            this.token = this.getToken();
            this.getMessage()
        },
        methods:{
            getMessage(){
                let sign = this.getEncrypt('page='+this.page+'&token='+this.getToken())
                this.$http.post('/app.Bbs/membernews.html',{
                    sign: sign,
                    token: this.getToken(),
                    page: this.page,
                }).then( response => {
                    if(response.data.code == 1){
                        this.lists = response.data.data;
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            getBack(cellId){
                this.$router.push('/InfComment/'+cellId+'/2')
            },
            //评论显示
            commentFn(type,commentId,fromMid,recordId,commReplyId=''){
                this.typeNum = type;
                this.commentId = commentId;
                this.fromMid = fromMid;
                this.recordId = recordId;
                this.commReplyId = commReplyId;
                this.CommentInfo = '';
                this.commentBoxShow = true;
            },
            //发送评论
            submitComment(type){
                if(type == 1){
                    this.sign = this.getEncrypt(
                        "comment_id="+this.commentId+
                        "&content="+this.CommentInfo+
                        "&from_mid="+this.fromMid+
                        "&record_id="+this.recordId+
                        "&token="+this.token+
                        "&type=2"
                    )
                    this.$http.post("/app.bbs/comment.html",{
                        sign: this.sign,
                        token: this.token,
                        type: 2,
                        record_id: this.recordId,
                        content: this.CommentInfo,
                        comment_id: this.commentId,
                        from_mid: this.fromMid
                    }).then(response => {
                        this.commentBoxShow = false;
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        });
                    })
                }else if(type == 2){
                    this.sign = this.getEncrypt(
                        "comm_reply_id="+this.commReplyId+
                        "&comment_id="+this.commentId+
                        "&content="+this.CommentInfo+
                        "&from_mid="+this.fromMid+
                        "&record_id="+this.recordId+
                        "&token="+this.token+
                        "&type=2"
                    )
                    this.$http.post("/app.bbs/comment.html",{
                        sign: this.sign,
                        token: this.token,
                        type: 2,
                        record_id: this.recordId,
                        content: this.CommentInfo,
                        comment_id: this.commentId,
                        from_mid: this.fromMid,
                        comm_reply_id: this.commReplyId
                    }).then(response => {
                        this.commentBoxShow = false;
                        Toast({
                            message: response.data.msg,
                            duration: 1500
                        });
                    })
                }
            },
            loadBottom(){
                this.page += 1;
                let sign = this.getEncrypt('page='+this.page+'&token='+this.getToken())
                this.$http.post('/app.Bbs/membernews.html',{
                    sign: sign,
                    token: this.getToken(),
                    page: this.page,
                }).then( response => {
                    if(response.data.code == 1){
                        if(response.data.data.length === 0){
                            this.allLoaded = true;
                        }
                        this.lists = this.lists.concat(response.data.data);
                    }
                }).catch( error => {
                    console.log(error)
                })
                this.$refs.loadmore.onBottomLoaded();
            },
            loadTop(){
                this.page = 1;
                this.allLoaded = false;
                this.getMessage();
                this.$refs.loadmore.onTopLoaded();
            },
        },
        mounted(){
            setTimeout(()=>{
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar){
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - 34;
                    return
                }else{
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top ;
                    return
                }
            },50)
        },
    }
</script>
<style lang="scss" scoped>
.wrapper{
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    position: relative;
}
.con{
    background: $c6-color;
}
.list{
    background: #fff;
    margin-top: 20px;
    &.first{
        margin-top:0;
        border-top: 1px solid $c6-color;
     }
    .list-top {
        padding: 20px 40px;
        .top-box{
            margin-bottom: 30px;
            display: flex;
            justify-content: flex-start;
            align-items: stretch;
            .img-group{
                width: 100px;
                height: 100px;
                border-radius: 5px;
                overflow: hidden;
            }
            .user{
                flex: 1;
                padding:0 20px;
                h4{
                    font-size: 26px;
                    line-height: 28px;
                    margin-top: 10px;
                }
                p{
                    margin-top:24px;
                    color: $c4-color;
                    font-size:22px;
                }
            }
            .btn{
                border: 1px solid $main-color;/*no*/
                border-radius: 5px;
                background: none;
                margin-top: 5px;
                color: $main-color;
                font-size:26px;
            }
        }
    }
    .item{
        padding: 20px 40px 40px;
        background: #f5f5f5;
        .item-box{
            display: flex;
            justify-content: flex-start;
            align-items: stretch;
            margin-top: 20px;
            background: #fff;
            .img-group{
                width: 100px;
                height: 100px;
                overflow: hidden;
                background-size:cover;
                background-repeat: no-repeat;
                background-position: center center;
            }
            .user{
                flex: 1;
                padding:20px;
                h4{
                    font-size: 26px;
                    line-height: 28px;
                }
                p{
                    color: $c4-color;
                    font-size:22px;
                    margin-top:10px;
                }
            }
        }
    }
    .info{
        color: $c4-color;
        font-size: 24px;
        .name{
            color: $main-color;
        }
    }
}

.comment-box{
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 99;
    width: 100%;
    height:100%;
    background: #fff;
    box-sizing: border-box;
    .nav_bar{
        padding: 0 20px;
        box-sizing: border-box;
        .title{
            flex:1;
            text-align: center;
        }
        .btn{
            color: $main-color;
        }
    }
    .text-area{
        padding-top: 100px;
        textarea{
            width: 100%;
            box-sizing: border-box;
            font-size: 26px;
            padding: 20px 20px 0;
            resize: none;
            border: none;
        }
    }
}

.loadEnd{
    color: #999;
    text-align: center;
    font-size:24px;
    padding:20px 0 20px;
}
</style>



