<template>
    <div class="wrapper"  ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <mt-loadmore :top-method="loadTop" :top-pull-text="topPullText" :bottom-pull-text="bottomPullText" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill">
                <div class="user">
                    <div class="canvas">
                        <span v-if="members.nextnum != 0">{{members.leveltitle}}</span>
                        <canvas ref="process" width="200" height="150"></canvas>
                        <!--<img src="" ref="management_img" class="management_img" alt="">-->
                        <span v-if="members.nextnum != 0" class="next">{{members.nexttitle}}</span>
                        <div v-if="members.nextnum != 0" class="info">
                            <p>还差<span>{{members.lacknum}}</span>个A级会员</p>
                            <p>即可晋升</p>
                        </div>
                        <div v-if="members.nextnum == 0" class="info">
                            <p class="highest">{{members.leveltitle}}</p>
                            <p class="highest-p">已是最高级</p>
                        </div>
                    </div>
                    <h4>
                        <p class="people">{{HierarchyTeamNums.TotalTeamNum}}人</p>
                        <p>团队总人数</p>
                    </h4>
                </div>
                <ul class="tabs">
                    <li>
                        <h4>{{HierarchyTeamNums.one}}人</h4>
                        <p>直属</p>
                    </li>
                    <li>
                        <h4>{{HierarchyTeamNums.two}}人</h4>
                        <p>隔代</p>
                    </li>
                </ul>
                <div v-if="OneLevelLists.length > 0" class="list">
                    <router-link :to="{path:`/secondteam/${OneLevelList.id}/3`  }" class="item" v-for="(OneLevelList,index) in OneLevelLists" :key="index">
                        <div class="left">
                            <div class="image">
                                <img class="img" v-lazy="OneLevelList.headimg" :key="OneLevelList.headimg" alt=""/>
                            </div>
                            <h4>{{OneLevelList.nickname}} <span v-if="OneLevelList.isPromotion == 1" class="promotion">(晋升)</span></h4>
                            <p>{{OneLevelList.leveltitle}}</p>
                            <p>{{OneLevelList.spreat_at}}</p>
                        </div>
                        <div class="right">
                            <div class="right-left">
                                <h4>{{OneLevelList.levelnum}}人</h4>
                            </div>
                            <div class="right-right">
                                <i class="icon-right"></i>
                            </div>
                        </div>
                    </router-link>
                    <div v-if="allLoaded" class="load">- 已加载全部 -</div>
                </div>
                <div v-else class="load">- 暂无记录 -</div>
        </mt-loadmore>
    </div>
</template>

<script>
    export default {
        name: 'MyTeam',
        data () {
            return {
                members:{},
                HierarchyTeamNums:{},
                OneLevelLists:[],
                runDraw:false,
                levelNum:'',

                page:1,
                lastPage:'',
                allLoaded: false,
                wrapperHeight: 0,
                autoFill:false,
                bottomPullText:'加载更多',
                topPullText:'刷新',
            }
        },
        created(){
            this.getTeamData();
            this.runDraw = true;
        },
        methods:{
            getTeamData(){
                let sign = this.getEncrypt('page='+this.page+'&token='+this.getToken())
                this.$http.post('/app.Teams/team_manage.html',{
                    sign: sign,
                    token: this.getToken(),
                    page: this.page,
                }).then( response => {
                    if(response.data.code == 1){
                        this.members = response.data.data.member;
                        this.HierarchyTeamNums = response.data.data.HierarchyTeamNum;
                        if(this.page == 1){
                            this.OneLevelLists = response.data.data.OneLevelList;
                        }else{
                            this.OneLevelLists = this.OneLevelLists.concat(response.data.data.OneLevelList);
                        }
                        this.lastPage = response.data.data.last_page;
                        if(this.page >= this.lastPage){
                            this.allLoaded = true;
                        }else{
                            this.allLoaded = false;
                        }
                        //圆弧比例
                        if(this.members.nextnum == 0){
                            this.levelNum = 1
                        }else{
                            this.levelNum =1 - this.members.lacknum/this.members.nextnum;
                        }
                        if(this.runDraw == true){
                            this.runDraw = false;
                            this.drawBackground();
                        }
                        //当前等级人数计算
//                        this.userLevel(this.members.lacknum,this.members.nextnum);
                    }else{
                        
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            subActiveFn(index){
                this.subNum = index;
            },
            //上拉加载
            loadBottom() {
                this.page += 1;
                this.getTeamData()
                this.$refs.loadmore.onBottomLoaded();
            },
            //下拉刷新
            loadTop() {
                this.page = 1;
                this.getTeamData()
                this.$refs.loadmore.onTopLoaded();
            },
            drawBackground(){
                // 外面的圆环边框
                let ctx = this.$refs.process.getContext('2d')
                let width = ctx.canvas.width
                let height = ctx.canvas.height
                let r = width / 2

                ctx.translate(r,r)
                ctx.beginPath()
                ctx.lineWidth = 2      //圆环的厚度
                ctx.lineCap = 'round'
                ctx.fillStyle = '#000'
                ctx.arc(0, 0, 0, 0, 0)
                ctx.stroke()

                this.draw()
                this.round()
//                this.canvasToImage()
            },
            draw(){
                let ctx = this.$refs.process.getContext('2d')
                let width = ctx.canvas.width
                let height = ctx.canvas.height
                let r = width / 2
                // 外圆环
                ctx.beginPath()
                ctx.lineWidth = 14      //圆环的厚度
                ctx.lineCap = 'round'
                ctx.strokeStyle = '#f0f0f0'
                ctx.arc(0, 0, r-7,  0.85*Math.PI, 2.15*Math.PI)
                ctx.stroke()
            },
            round(){
                // 里面的渐变圆环进度
                let ctx = this.$refs.process.getContext('2d')
                let width = ctx.canvas.width
                let height = ctx.canvas.height
                let r = width / 2

                ctx.beginPath()
                ctx.lineWidth = 12
                ctx.lineCap = 'round'
                let gradient = ctx.createLinearGradient(r,0,0,16)
                gradient.addColorStop("0","#7db23c");
                gradient.addColorStop("1","#25c1e7");
                ctx.strokeStyle = gradient
                ctx.arc(0, 0, r-7,  0.85*Math.PI, 0.85*Math.PI+(2.15-0.85)*Math.PI*this.levelNum)
                ctx.stroke()
            },
            canvasToImage(){
                let img = new Image();
                this.$refs.management_img.src = this.$refs.process.toDataURL('image/jpg');
            },
//            userLevel(lacknum,nextnum){
////                this.level = '普通会员'
////                this.nextLevel = 'C级会员'
//                this.levelNum = lacknum / nextnum
////                this.nextLevelNum = lacknum
//            },
        },
        mounted(){
            setTimeout(()=>{
                let hasBottombar = document.querySelector("body").classList.contains("has-bottombar");
                if (hasBottombar){
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top - 34;
                    return
                }else{
                    this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
                    return
                }
            },50)
        },
    }
</script>
<style lang="scss" scoped>
.wrapper{
    overflow:scroll;
    -webkit-overflow-scrolling: touch;
    position: relative;
}
.load{
    line-height: 60px;
    text-align: center;
    font-size:26px;
    background: $c8-color;
}
.user{
    /*height: 200px;*/
    /*display: flex;*/
    /*justify-content: flex-start;*/
    /*align-items: center;*/
    padding: 40px;
    border-bottom: 1px solid $c6-color;/*no*/
    .canvas{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        &>span{
            font-size: 24px;
            color: #000;
            min-width: 120px;
            text-align: right;
            &.next{
                color: #999;
                text-align: left;
            }
        }
        .info{
            position: absolute;
            left: 50%;
            top: 50%;
            margin-top: 10px;
            width: 300px;
            font-size: 34px;
            line-height: 50px;
            margin-left: -150px;
            p{
                text-align: center;
                color: #999;
                span{
                    color: $main-color;
                }
            }
            .highest{
                color: #333;
            }
            .highest-p{
                color: #ccc;
                font-size: 28px;
            }
        }
    }
    h4{
        margin-top: 40px;
        p{
            font-size: 24px;
            color: #999;
            text-align: center;
        }
        .people{
            color: #000;
            font-size: 36px;
            margin-bottom: 10px;
        }
    }
}
.tabs{
    height:150px;
    border-bottom: 1px solid $c6-color;/*no*/
    display: flex;
    justify-content: center;
    align-items: stretch;
    text-align: center;
    li{
        flex:1;
        border-right:1px solid $c6-color;/*no*/
        &:last-child{
            border: none;
        }
        h4{
            font-size: 40px;
            margin-bottom: 10px;
            margin-top: 38px;
        }
        p{
            font-size: 26px;
            color: $c4-color;
        }
    }
}
.list{
    padding-top: 20px;
    background: $c8-color;
    .item{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 150px;
        background: #fff;
        border-bottom: 1px solid $c6-color;/*no*/
        padding: 0 40px;
        &:first-child{
            border-top: 1px solid $c6-color;/*no*/
        }
        .left{
            position:relative;
            padding-left: 120px;
            .image{
                position: absolute;
                left:0;
                top:0;
                z-index: 2;
                width:100px;
                height:100px;
                overflow: hidden;
                border-radius: 5px;
                background: #000;
            }
            .promotion{
                color: $main-color;
            }
            h4{
                font-size: 26px;
                line-height: 28px;
                min-height: 28px;
                margin-top: 4px;
            }
            p{
                color: $c4-color;
                font-size: 22px;
                margin-top:10px;
            }
        }
        .right{
            width: 180px;
            text-align: center;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            .right-left{
                font-size: 36px;
            }
            .right-right{
                font-size: 50px;
                margin-top: 4px;
            }
        }
        .middle{
            font-size: 26px;
        }
    }
}
</style>



